import {Component} from '@angular/core';

import {Configuration} from '../../../config/voj-configuration';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import {VojHeaderSegmentsType} from "../../../models/voj-header-segments-type";
import {ObjectUtils, StringUtils} from "../../../utils/utils";
import {EnterpriseService} from "../../../providers/enterprise-service/enterprise-service";
import {EnterpriseDto} from "../../../dtos/enterprise.dto";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {CompanyPage} from "../company/company";
import {NavParams, ViewController} from "ionic-angular";

@Component({
	selector: 'enterprise-search-page',
	templateUrl: 'enterprise-search.html',
})
export class EnterpriseSearchPage{
	config: VojConfiguration;
	isInscription: boolean;
	enterpriseData: EnterpriseDto[];
	headerData: VojPageHeaderType;
	searchInput: string = '';
	segmentData: VojHeaderSegmentsType;

	siren1: string;
	siren2: string;
	siren3: string;

	constructor(public configurationService: Configuration,
				private enterpriseService: EnterpriseService,
				private navigationService: NavigationService,
				private navParams: NavParams,
				private viewCtrl: ViewController) {
		this.config = this.configurationService.configuration;
		this.isInscription = this.navParams.data.isInscription;

		this.headerData = {
			title: 'Rechercher mon entreprise',
			style: 'any',
			actions: [{
				id: 'gcfhvjbknml',
				label: '',
				icon: 'help',
				action: function showHelp() {
					console.log('showHelp works')
				}
			}],
			isCanceled: true
		};

		this.segmentData = {
			activeSegmentTab: 0,
			segmentsList: [
				{
					title: 'Par nom',
				},
				{
					title: 'Par siren'
				}]
		}
	}

	getCompaniesByName() {
		this.enterpriseService.getCompanies("company", this.searchInput).then((data: any) => {
			if (data.status == "failure") {
				//TODO: display the error msg in the form
				console.log(ObjectUtils.isEmpty(data.error) ? "Une erreur est survenue. Veuillez ressayer." : data.error);
			} else {
				this.enterpriseData = data;
			}
		});
	}

	getCompanyBySIREN(siren) {
		let isDirectSearch = ObjectUtils.isEmpty(siren);
		siren = (isDirectSearch ? this.siren1 + "" + this.siren2 + "" + this.siren3 : siren);
		this.enterpriseService.getCompanies("siren", siren).then((data: any) => {
			if (data.status == "failure") {
				//TODO: display the error msg in the form
				console.log(ObjectUtils.isEmpty(data.error) ? "Une erreur est survenue. Veuillez ressayer." : data.error);
			} else {
				this.enterpriseData = [data];
				if(!isDirectSearch){
					this.segmentData.activeSegmentTab = 1;
					this.splitSIREN(siren);
				}
			}
		});
	}

	splitSIREN(siren: string){
		this.siren1 = siren.substring(0, 3);
		this.siren2 = siren.substring(3, 6);
		this.siren3 = siren.substring(6, 9);
	}

	clearEnterpriseData() {
		this.enterpriseData = [];
		this.searchInput = '';
		this.siren1 = '';
		this.siren2 = '';
		this.siren3 = '';
	}

	gotoCompanyPage(item: EnterpriseDto){
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: CompanyPage, isRoot: false, isWebModal: true, params:{corporamaEnterprise: item}};
		this.navigationService.navigate("main", vojPage);
	}

	isEmpty(str){
		return ObjectUtils.isEmpty(str);
	}

	preventNull(str){
		return StringUtils.preventNull(str);
	}
}



