import {Component} from '@angular/core';

import {JobyerType} from "../../../models/jobyer-type";
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {Storage} from "@ionic/storage";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {ProfileService} from "../../../providers/profile-service/profile-service";
import {ObjectUtils} from "../../../utils/utils";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {UserInformationPage} from "../user-information/user-information";
import {SearchPage} from "../../search-module/search/search";
import {AboutPage} from "../../test-components/about/about";
import {Configuration} from "../../../config/voj-configuration";
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {Events} from "ionic-angular";

@Component({
	selector: 'account-page',
	templateUrl: 'account.html',
})
export class AccountPage{

	dataPageHeader: VojPageHeaderType;
	profileData: JobyerType;
	currentUser: CurrentUserDto;
	config: VojConfiguration;

	constructor(private storage: Storage,
				public event: Events,
				private navigationService: NavigationService,
				private configuration: Configuration) {

		this.config = this.configuration.configuration;
		// Set page header
		this.dataPageHeader = {
			title: 'Mon compte',
			actions: [
				{
					id: 'parameters',
					label: '',
					icon: 'settings',
					action: () => {
						console.log('Click parameters button.')
					}
				},
				{
					id: 'help',
					label: '',
					icon: 'help',
					action: () => {
						console.log('Click help button.')
					}
				}
			],
			style: 'any',
			isCanceled: true
		};
		this.storage.get("currentUser").then((data: string) => {
			if (!data) {
				return;
			}
			this.currentUser = JSON.parse(data);

			// Set profile data
			this.profileData = {
				cover: 'http://www.turfgrass.ie/wp-content/uploads/2014/07/T-Projects-Lubker-1.jpg',
				thumbnail: 'http://diysolarpanelsv.com/images/clip-art-user-14.png',
				title: {
					label: (ObjectUtils.isEmpty(this.currentUser.identity.firstName) ? "Nous vous invitons  saisir vos informations d'identit." : this.currentUser.identity.civility + " " + this.currentUser.identity.firstName + " " + this.currentUser.identity.middleName + " " + this.currentUser.identity.lastName)
				},
				subtitles: [
					{
						icon: 'call',
						label: this.currentUser.account.phone
					},
					{
						icon: 'mail',
						label: this.currentUser.account.mail
					}
				],
				actions: [
					{
						id: '',
						icon: '',
						label: 'se dconnecter',
						action: () => {
							this.logout();
						}
					},
					{
						id: '',
						icon: 'information-circle',
						label: 'mes informations',
						action: () => {
							this.gotoUserInformationPage();
						}
					}
				],
				segments: [
					{
						title: {
							icon: '',
							label: '0'
						},
						subtitle: {
							icon: '',
							label: 'Contrat(s) en attente'
						},
						action: function () {
							console.log('Present Waiting Contracts page.')
						},
						style: 'style1'
					},
					{
						title: {
							icon: '',
							label: '0'
						},
						subtitle: {
							icon: '',
							label: 'Pointage(s)  effectuer'
						},
						action: function () {
							console.log('Present Hour Tracking page.')
						},
						style: 'style1'
					},
					{
						title: {
							icon: '',
							label: '0'
						},
						subtitle: {
							icon: '',
							label: 'Mission(s)  clturer'
						},
						action: function () {
							console.log('Present Hours Validation Signature page.')
						},
						style: 'style1'
					}
				]
			};
		});
	}

	gotoUserInformationPage() {
		let vojPage: VojPage = {page: UserInformationPage}
		this.navigationService.navigate("details", vojPage);
	}

	ngOnInit() {
		//this.profileService.getAccountData(this.currentUser.account.id).then((data: any) => {

		//});

		this.updateUserInfo();
	}

	ionViewDidLoad() {

	}

	logout() {
		this.storage.clear().then(() => {
			let vojPageList: VojPage = {page: SearchPage, isRoot: true};
			this.navigationService.navigate("list", vojPageList);
			if(this.config.isLargeScreen) {
				this.navigationService.hide("details");
				let vojPageMain: VojPage = {page: AboutPage, isRoot: true};
				this.navigationService.navigate("main", vojPageMain);
			}
		});
	}

	/**
	 * update user info when the user modify his identity data
	 */
	updateUserInfo() {
		this.event.subscribe('user:update', (userUpdate: CurrentUserDto) => {
			this.profileData.title.label = (ObjectUtils.isEmpty(userUpdate.identity.firstName) ? "Nous vous invitons  saisir vos informations d'identit." : userUpdate.identity.civility + " " + userUpdate.identity.firstName + " " + userUpdate.identity.middleName + " " + userUpdate.identity.lastName);
		});
	}
}



