import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'hasEvents',
  pure: true
})
export class HasEventsPipe implements PipeTransform {
  public transform(week, dayIndex: number/*, weekIndex: number*/): boolean {
    if (Array.isArray(week.rows[0])) {  // if this is matrix
      return week.rows.some(row => row[dayIndex].events.length > 0);
    }
    return week.rows[dayIndex].events.length > 0
  }
}



