import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import {ButtonType} from "../../../models/button-type.model";
import { FieldType } from '../../../models/field-type';
import { JobyerAvailabilityChoicePage } from "../../cv-offer-module/jobyer-availability-choice/jobyer-availability-choice";
import { UserInformationPage } from "../../account-module/user-information/user-information";
import { OptionsPage } from '../../account-module/options/options';

@Component({
	selector: 'page-contact',
	templateUrl: 'contact.html',

})
export class ContactPage {
	icons: any;
	contactButtonsData: {style: any, actions: Array<ButtonType>};
	testVar: string;

    formData: {
        title: string;
        fields: Array<FieldType>;
        submit: ButtonType
    };

	constructor(
        public navCtrl: NavController
	) {
		// Form data for testing
        this.formData = {
            title: 'Form Title',
            fields: [
                {
                    placeholder: 'Item 1',
                    type: 'text',
                    value: 'Label 1',
                    label: 'Label 1',
                    dataset: [
                        { id: 4, key: 'russian', value: 'Russian' },
                        { id: 5, key: 'ukrainian', value: 'Ukrainian' },
                        { id: 6, key: 'english', value: 'English' }
                    ],
                    required: true,
                    readonly: false,
                    visible: true,
                    min: 3,
                    max: 10,
                    regex: '^[A-z]+$',
                    id: '',
                    name: 'Input1'
                },
                {
                    type: 'button',
                    label: 'Button',
                    visible: true,
                    id: '',
                    action: () => {
                        console.log('click Button')
                    }
                },
                {
                    placeholder: 'Item 2',
                    type: 'text',
                    value: 'Label 2',
                    label: 'Label 2',
                    required: true,
                    readonly: false,
                    visible: true,
                    min: 3,
                    max: 10,
                    regex: '^[A-z]+$',
                    id: '',
                    name: 'Input2'
                },
                {
                    placeholder: 'Password 1',
                    type: 'password',
                    value: 'Password 1',
                    label: 'Password 1',
                    required: true,
                    readonly: false,
                    visible: true,
                    min: 3,
                    max: 10,
                    regex: '^[A-z]+$',
                    id: '',
                    name: 'Password 1'
                },
                {
                    type: 'select',
                    value: 'select 1',
                    label: 'Label 3',
                    dataset: [
                        { id: 1, key: 'select 1', value: 'select 1' },
                        { id: 2, key: 'select 2', value: 'select 2' },
                        { id: 3, key: 'select 3', value: 'select 3' }
                    ],
                    required: true,
                    visible: true,
                    id: '',
                    name: 'Select1'
                },
                {
                    type: 'radio',
                    value: 'radio 1',
                    label: 'Label 4',
                    dataset: [
                        { id: 1, key: 'radio 1', value: 'radio 1' },
                        { id: 2, key: 'radio 2', value: 'radio 2' }
                    ],
                    required: true,
                    visible: true,
                    id: '',
                    name: 'radioTest'
                },

                // Custom Select
                {
                    type: 'customSelect',
                    value: null,
                    label: 'Label 6',
                    dataset: [
                        { id: 4, key: 'select 4', value: 'select 4' },
                        { id: 5, key: 'select 5', value: 'select 5' },
                        { id: 6, key: 'select 6', value: 'select 6' }
                    ],
                    required: true,
                    visible: true,
                    id: '',
                    name: 'customSelect'
                },

                {
                    type: 'customTwoSelects',
                    value: 'select 2',
                    value2: 'select 7',
                    label: 'Label 6',
                    dataset: [
                        { id: 4, key: 'russian', value: 'Russian' },
                        { id: 5, key: 'ukrainian', value: 'Ukrainian' },
                        { id: 6, key: 'english', value: 'English' }
                    ],
                    dataset2: [
                        { id: 7, key: 'beginner', value: 'Beginner' },
                        { id: 8, key: 'intermediate', value: 'Intermediate' },
                        { id: 9, key: 'advanced', value: 'Advanced' }
                    ],
                    required: true,
                    visible: true,
                    id: '',
                    name: 'customTwoSelects'
                }
            ],
            submit: {
                id:'',
                icon: 'build',
                label: 'Form Button',
                action: (event) => {
                    console.log(event)
                }
            }
        };
	}
	ngOnInit(){
		let actions: Array<ButtonType> = [{id:'create', icon: "create", label: "Crer", action: this.action1}, {id:'delete',icon: "trash", label: "Supprimer", action: this.action2}];
		this.contactButtonsData = {style: {'color': '#488aff'}, actions};
		this.testVar ="Nom de la barre";
	}

	action1(){
		console.log("action1 button clicked");
	}

	action2(){
		console.log("action2 button clicked");
	}

    presentOptionsPage() {
        this.navCtrl.push(OptionsPage);
    }
    presentJobyerAvailabilityChoicePage() {
        console.log('Present Jobyer Availability Choice Page.');
        this.navCtrl.push(JobyerAvailabilityChoicePage);
    }
    presentUserInfoPage() {
        console.log('Present user info page.');

        // Testing user data
        let testUser = {
            name: 'John',
            firstName: 'Terry',
            no: 11,
            streetName: 'Central Street',
            postcode: 12345,
            city: 'Paris',
            birthDate: '16/01/88',
            placeOfBirth: 'Paris, France',
            identityDocumentType: 'CNI',
            docNumber: 67890,
            bankDetails: 'Some bank details',
            documents: 'Some documents'
        };

        this.navCtrl.push(UserInformationPage, { userData: testUser });
    }

}



