import { Component } from '@angular/core';
import { IonicPage } from 'ionic-angular';
import { Storage } from "@ionic/storage";
import { ModalService } from '../../../services/modal.service';
import { AlertController } from 'ionic-angular';
import { VojConfiguration } from '../../../models/voj-configuration.model';
import { Configuration } from '../../../config/voj-configuration';
import { NavigationService } from '../../../services/navigation.service';
import { VojPage } from '../../../models/voj-page';
import { SearchPage } from '../../search-module/search/search';

@IonicPage()
@Component({
	selector: 'options-page',
	templateUrl: 'options.html'
})
export class OptionsPage {
    config: VojConfiguration;
    dataPageHeader: any;
    currentUser: any;

	constructor(private storage: Storage,
                public configurationService: Configuration,
                private alertCtrl: AlertController,
                public navigationService: NavigationService) {
		// Set page header title
		this.dataPageHeader = {
            title: 'Options',
            actions:[
                {
                    id : 'help',
                    icon : 'help',
                    action : () => { console.log('Click help button.') },
                    label : ''
                }
            ]
        };
    }

    presentPage(pageName) {
	    if (pageName === 'Disconnect') {
            this.storage.get('currentUser').then((sdata:any) => {
                this.currentUser = sdata;
            }).then(() => {
                if (this.currentUser !== null) {
                        let alert = this.alertCtrl.create({
                            message: 'tes vous sr de vouloir vous dconnecter?',
                            cssClass: 'options-message',
                            buttons: [
                                {
                                    text: 'Oui',
                                    handler: () => {
                                        this.storage.remove('currentUser').then(() => {
                                            let vojNav: VojPage = { page: SearchPage, isRoot: true };
                                            this.navigationService.navigate('list', vojNav);
                                        })
                                    }
                                },
                                {
                                    text: 'Non',
                                    role: 'cancel',
                                    handler: () => {
                                        console.log('Non clicked');
                                    }
                                }
                            ]
                        });
                        alert.present();
                    // this.modalService.present({page: OptionsMessagePage});
                } else {
                    console.log('You are not authorized');
                }
            })

        }
		console.log('Present page:', pageName);
	}

	ionViewDidLoad() {
        this.storage.get('currentUser').then((sdata:any) => {
            this.currentUser = sdata;
        });
    }
}



