import { Component, TemplateRef } from '@angular/core';
import { NavParams, ViewController } from 'ionic-angular';

@Component({
	templateUrl: './calendar-mode.popover.html',
	styles: [
		`
			ion-list {
				margin-bottom: 0;
			}
			.item-block:last-child {
				border-bottom: none;
			}
		`
	]
})
export class CalendarModePopoverPage {
	public menuTemplate: TemplateRef<any>;
	public menuTemplateContext: any;

	constructor(
		public viewCtrl: ViewController,
		public navParams: NavParams
	) {
		this.menuTemplateContext = {
			changeViewHandler: this.changeMode.bind(this)
		};
	}

	public ngOnInit() {
		this.menuTemplate = null;
		this.menuTemplate = this.navParams.get('menuTemplate');
	}

	changeMode(mode) {
		this.viewCtrl.dismiss({ mode: mode });
	}
}



