import {Component} from '@angular/core';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojFormModel} from '../../../models/voj-form.model';
import {AccountService} from "../../../providers/account-service/account-service";
import {Storage} from "@ionic/storage";
import {ModalService} from "../../../services/modal.service";
import {SignUpPage} from "../sign-up/sign-up";
import { AlertController, Events, ToastController, ViewController } from "ionic-angular";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {md5} from "../../../scripts/md5";
import {MasterdataService} from "../../../providers/masterdata-service/masterdata-service";
import {CountryDto} from "../../../dtos/country.dto";
import {ResetPasswordPage} from "../reset-password/reset-password";
import {Configuration} from "../../../config/voj-configuration";
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {VojPage} from "../../../models/voj-page";
import {NavigationService} from "../../../services/navigation.service";
import {SearchPage} from "../../search-module/search/search";
import {EventBusProvider} from "../../../providers/voj-events/voj-events";

@Component({
	selector: 'login-page',
	templateUrl: 'login.html',
})
export class LoginPage{
	config: VojConfiguration;
	dataPageHeader: VojPageHeaderType;
	formData: VojFormModel;

	constructor(public accountService: AccountService,
				private eventBus : EventBusProvider,
							private storage: Storage,
							public modalService: ModalService,
							public viewCtrl: ViewController,
							public alertCtrl: AlertController,
							private masterdataService: MasterdataService,
							private configuration: Configuration,
							private navigationService: NavigationService,
							public toastCtrl: ToastController,
						public events: Events) {
		this.config = this.configuration.configuration;

		// Set form data
		this.formData = {
			title: '',
			fields: [
				// Phone code
				{
					type: 'select',
					value: null,
					label: 'FORM.COUNTRY_CODE',
					dataset: [],
					required: true,
					visible: true,
					id: 'phone-code-field',
					name: 'code'
				},
				// Phone
				{
					placeholder: '',
					type: 'tel',
					value: '',
					label: 'FORM.PHONE',
					required: true,
					readonly: false,
					visible: true,
					min: 9,
					max: 10,
					regex: '^[0-9]*$',
                    validationMsg: 'VALIDATION.PHONE_VALIDATION_MSG',
					id: 'phone-field',
					name: 'phone'
				},
				// Password
				{
					placeholder: '',
					type: 'password',
					value: '',
					label: 'FORM.PASSWORD',
					required: true,
					readonly: false,
					visible: true,
					min: 6,
					max: 100,
					regex: '',
                    validationMsg: 'VALIDATION.PASSWORD_VALIDATION_MSG',
					id: 'password-field',
					name: 'password'
				},
				// Forgotten password button
				{
					type: 'button',
					label: 'LOGIN.FORGOTTEN_PASSWORD',
					visible: true,
					id: 'forgotten-password-btn',
					action: () => {
						//display password forgotten modal
						this.gotoPasswordForgotten();
					}
				}
			],
			submit: {
				id: '',
				icon: '',
				label: 'LOGIN.VALIDATE',
				action: () => {
					console.log('Click validate button.');
					this.authenticate();
				}
			}
		};

		// Set page header
		this.dataPageHeader = {
			title: 'LOGIN.TITLE',
			actions: [
				{
					id: 'help',
					label: '',
					icon: 'help',
					action: () => {
						console.log('Click help button.')
					}
				}
			],
			style: 'any',
			isCanceled: true
		};
	}

	ngOnInit() {
		// load phone code list
		this.masterdataService.loadCountriesList().then((data: CountryDto[]) => {
			//set dataset of phone codes of the form
			this.formData.fields.filter(f => f.name === "code")[0].dataset = this.masterdataService.convertCountriesListToDataset(data);
			//the default phone code is France code (33)
			this.formData.fields.filter(f => f.name === "code")[0].value = "33";
		});
	}

	authenticate() {
		//prepare credentials data
		let code = this.formData.fields.filter(f => f.name === "code")[0].value;
		let phone = this.formData.fields.filter(f => f.name === "phone")[0].value;
		let password = md5(this.formData.fields.filter(f => f.name === "password")[0].value);
		let phoneWithCode = "+" + code + phone.substr(phone.length - 9);

		//call web service method
		this.accountService.authenticate(phoneWithCode, password).then((data: any) => {
			if (data.status == "passwordError") {
				let msg = 'Le mot de passe est incorrect. Veuillez ressayer.';
				this.displayErrorNotification(msg);
				return;
			}

			if (data.found == false) {
				let msg = 'Aucun compte associ  ce numro de tlphone.';
				this.displayErrorNotification(msg);
				return;
			}

			if (data.id != 0) {
				let account: CurrentUserDto = data;

                // show toasy with first name
                let toast = this.toastCtrl.create({
                    message: `Bienvenue ${account.identity.firstName}, vous venez de vous connecter.`,
                    duration: 3000,
                    position: 'top'
                });
                toast.present();

				this.storage.set("currentUser", JSON.stringify(account)).then(() => {
					this.events.publish('currentUser:logged-in');
					if (this.config.isLargeScreen) {
						this.viewCtrl.dismiss();
					} else {
						let vojPage: VojPage = {page: SearchPage, isRoot: true};
						this.navigationService.navigate("list", vojPage);
					}
					this.eventBus.publishAppEventUpdate();

				});
			}
		});
	}

	gotoPasswordForgotten(){
		this.navigationService.dismiss(this.viewCtrl);
		// this.modalService.present({page: ResetPasswordPage}, true);
        let vojNav: VojPage = {page: ResetPasswordPage, isRoot: false};
        // this.navigationService.navigate('list', vojNav);
        this.config.isLargeScreen ? this.modalService.present({page: ResetPasswordPage}, true) : this.navigationService.navigate('list', vojNav);
	}

	goToSignup() {
		if (this.config.isLargeScreen) {
			this.viewCtrl.dismiss();
		}

		let loginFields = {
			tel: this.formData.fields[1].value,
            password: this.formData.fields[2].value
		};

		let vojPage: VojPage = {page: SignUpPage, isRoot: true, params: { loginData: loginFields, closeConfirmation:true }, isWebModal: true};
		this.navigationService.navigate("list", vojPage);
	}

	displayErrorNotification(msg: string) {
		let alert = this.alertCtrl.create({
			title: 'Attention!',
			subTitle: msg,
			buttons: ['OK']
		});
		alert.present();
	}
}


