import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { AboutPage } from "../pages/test-components/about/about";
import {Configuration} from "../config/voj-configuration";
import {SearchPage} from "../pages/search-module/search/search";
import { TranslateService } from '@ngx-translate/core';
import {Page} from "ionic-angular/navigation/nav-util";
import {HomePage} from "../pages/home/home";
import {NavigationService} from "../services/navigation.service";
import {envConfig} from "../config/environment";
import {VojConfiguration} from "../models/voj-configuration.model";
import {VojPage} from "../models/voj-page";
import {EventBusProvider} from "../providers/voj-events/voj-events";
import {LandingPage} from "../pages/landing-page/landing-page";
import {ModalService} from "../services/modal.service";
import {LoginPage} from "../pages/account-module/login/login";
import {Storage} from "@ionic/storage";
import {CurrentUserDto} from "../dtos/current-user.dto";
import {AccountPage} from "../pages/account-module/account/account";
import {UserInformationPage} from "../pages/account-module/user-information/user-information";
import {IdentityPage} from "../pages/account-module/identity/identity";
import { CalendarPage } from '../pages/calendar/calendar';
import {SearchServiceProvider} from "../providers/search-service/search-service";
import {VojCardModel} from "../models/voj-card.model";
import {Strategy} from "./startegy/strategy";
import {JobyerStrategy} from "./startegy/jobyer";
import {EmployerStrategy} from "./startegy/employer";
import {JitCompiler} from "@angular/compiler";
import {EnterpriseSearchPage} from "../pages/account-module/enterprise-search/enterprise-search";
import {MyMissionsListPage} from "../pages/missions/my-missions-list/my-missions-list";
import { MyCvPage } from '../pages/cv-offer-module/my-cv/my-cv';
import { JobsListPage } from '../pages/cv-offer-module/jobs-list/jobs-list';
import { InformationResumePage } from '../pages/cv-offer-module/information-resume/information-resume';

export interface PageInterface {
	title: string;
	name: string;
	component: any;
	icon: string;
	logsOut?: boolean;
	index?: number;
	tabName?: string;
	tabComponent?: any;
}

@Component({
	templateUrl: 'app.html'
})
export class MyApp {
  config: VojConfiguration;

  //contentList will be displayed in the left of the large screens
  @ViewChild('contentList') nav1: Nav;
  //contentDetails will be displayed in the center of the large screens
  @ViewChild('contentDetails') nav2: Nav;
  //contentMain will be displayed in the small screens and in the right for the large screens
  @ViewChild('contentMain') nav3: Nav;

  //To initialize root pages, go to SetMainPages function
  //the root page that will be shown in the contentMain nav
  rootPageMain:Page;
  //the root page that will be shown in the contentList nav
  rootPageList:Page;
  //the root page that will be shown in the contentDetails nav
  rootPageDetails:Page;

  //the selected segment (available only in small screens)
  selectedTab: string;

  //flags to show/hide navs (only available in large screens)
  showList: boolean;
  showDetails: boolean;
  showMain: boolean;
  static showCacheButton: boolean = true;

  pageMapping: PageMapping;

  dataPageHeader:{
		title: string, 
		actions : [
			{ icon : string, label : string, action : Function },
			{ icon : string, label : string, action : Function },
			{ icon : string, label : string, action : Function }
		], 
		style : any
	}
	dataHeader:{
  	menu : [
			{
				self?: any,
				icon : string, 
				label : string,
				badge? : number,
				action : Function,
				notificationFunction? : Function
			}
		],
		logo: string,
		searchbar: {
			placeholder: string 
		}, 
		segments : [
			{ 
				title:	{ 
					icon : string, 
					label : string 
				}, 
				subtitle: { 
					icon : string, 
					label : string 
				}, 
				action: Function, 
				style : any 
			}
		] 
	};


	cards: any;
	listTitle: string;
	appPages: PageInterface[] = [
		{ title: 'Schedule', name: 'Tabs', component: TabsPage, tabComponent: "", index: 0, icon: 'calendar' },
		{ title: 'Speakers', name: 'Tabs', component: TabsPage, tabComponent: "", index: 1, icon: 'contacts' },
		{ title: 'Map', name: 'Tabs', component: TabsPage, tabComponent: "", index: 2, icon: 'map' },
		{ title: 'About', name: 'Tabs', component: TabsPage, tabComponent: "", index: 3, icon: 'information-circle' }
	];
	loggedInPages: PageInterface[] = [
		{ title: 'Account', name: 'AccountPage', component: "", icon: 'person' },
		{ title: 'Support', name: 'SupportPage', component: "", icon: 'help' },
		{ title: 'Logout', name: 'Tabs', component: TabsPage, icon: 'log-out', logsOut: true }
	];
	loggedOutPages: PageInterface[] = [
		{ title: 'Login', name: 'LoginPage', component: "", icon: 'log-in' },
		{ title: 'Support', name: 'SupportPage', component: "", icon: 'help' },
		{ title: 'Signup', name: 'SignupPage', component: "", icon: 'person-add' }
	];
	self: any;
	task : any;
	interval : number = 1000*60*15;
	badge : number = 0;
	annoncesLabel : string = 'Annonces';
    compteTab = 'Compte';

	constructor(platform: Platform,
                statusBar: StatusBar,
                splashScreen: SplashScreen,
				private searchService : SearchServiceProvider,
                private configurationService: Configuration,
                public event: Events,
                public navigationService: NavigationService,
                private translate: TranslateService,
								private vojEvent: EventBusProvider,
								public modalService: ModalService,
								private storage: Storage, private jitCompiler:JitCompiler) {

    	this.self = this;
    this.config = this.configurationService.configuration;

    this.dataPageHeader = {
			title: 'string', 
			actions : [
				{ icon : 'search', label : 'button 1', action : Function },
				{ icon : 'search', label : 'button 2', action : Function },
				{ icon : 'search', label : 'button 3', action : Function }
			], 
			style : 'any'
		};

		platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can dopu any higher level native things you might need.
            statusBar.styleDefault();
            splashScreen.hide();

			//determine the main root page according to the screen size
			this.config.isLargeScreen = (platform.width() >= envConfig.thresholdWidth);
			//set the main page (third nav) according to the screen width
			//(this function should be called after the platform is ready)
			this.setMainPages();
		});

			this.listTitle = "Ma liste des cartes";

        this.cards = [{
            title: "Jobyer 1",
            content: "contenu 1",
            actionName: "action 1"
        }, {
            title: "Jobyer 2",
            content: "contenu 2",
            actionName: "action 2"
        }, {
            title: "Jobyer 3",
            content: "contenu 3",
            actionName: "action 3"
        }, {
            title: "Jobyer 4",
            content: "contenu 4",
            actionName: "action 4"
        }, {
            title: "Jobyer 5",
            content: "contenu 5",
            actionName: "action 5"
        }];

        this.initializeTranslation();
		this.updateUserInfo();
    }

	ngOnInit() {
    this.configurationService.initialize();

    //initializing the navigation params
    this.initializeNavigation();

    //initializing header
		this.initializeWebHeader();


		setTimeout(() => {
			this.checkNotifications();
		}, 2000);

		this.task = setInterval(() => {
			this.checkNotifications();
		}, this.interval);

		this.vojEvent.subscribeMainApp(this);
  }

  checkNotifications() {
	  console.log('STARTUP NOTIFICATIONS CHECK');
	  this.storage.get("currentUser").then((sdata:any)=>{
		  let data = JSON.parse(sdata);

		  if (data !== null) {
              this.compteTab = data.identity.firstName + ' ' + data.identity.lastName.slice(0, 1) + '.';
		  }

		 if(data && data.account) {
			 //console.clear();
			 console.log(JSON.stringify(data.account));
			 this.searchService.checkEvents(data.account.id, false).then((data : Array<VojCardModel>)=>{
				 console.log(JSON.stringify(data));
				 this.badge = 0;
				 this.dataHeader.menu[0].badge = 0;
				 for(let i=0 ; i < this.searchService.events.length ; i++) {
					 if(!this.searchService.events[i].read)
					 	this.badge++;
				 }
				 this.dataHeader.menu[0].badge = this.badge;

				 if(data)
				 	this.vojEvent.publishSearch(data);
			 });
		 }
	  });

  }

	notificationFunction(caller? : MyApp){
		if(!caller)
			caller = this;

		let p1 : VojPage = {page : SearchPage, isRoot : true, params: {isNewNotification:true, app:caller}};
		caller.navigationService.navigate('list', p1);
		caller.navigationService.hide('details');
		let p3 : VojPage = {page : AboutPage, isRoot : true};
		caller.navigationService.navigate('main', p3);

		setTimeout(()=> {
			caller.vojEvent.publishPushEvents();

		},1000);
	}

	navigateToMission(caller? : MyApp) {
		if(!caller)
			caller = this;

		caller.storage
			.get('currentUser').then(user=>{
			if(user == null) {
				let vojPageList: VojPage = {
					page: LoginPage,
					isRoot: true,
					isWebModal: true
				};
				caller.navigationService.navigate("list", vojPageList);
			} else {
				let p1 : VojPage = {page : MyMissionsListPage, isRoot : true};
				caller.navigationService.navigate('list', p1);
			}
		});



	}

	navigateToSearch(caller? : MyApp) {
		if(!caller)
			caller = this;
		let p1 : VojPage = {page : SearchPage, isRoot : true};
		caller.navigationService.navigate('list', p1);
		caller.navigationService.hide('details');
		let p3 : VojPage = {page : AboutPage, isRoot : true};
		caller.navigationService.navigate('main', p3);


	}

	mock(caller? : MyApp) {
		if(!caller)
			caller = this;
	}
  initializeWebHeader(){

		this.dataHeader = {
			menu : [
				{
					icon : 'search',
					label : 'Recherche',
					badge : 0,
					action : this.notificationFunction,
					notificationFunction : this.notificationFunction
				},
				{
					icon : 'search',
					label : 'Annonces',
					badge : -1,
					action : () => {
						this.nav3.push(AboutPage);
					}
				},
				{
					icon : 'search',
					label : 'Missions',
					badge : -1,
					action : Function
				},
				{
					self: this,
					icon : 'search',
					label : 'Compte',
					badge : -1,
					action : this.goToAccount
				}
			],
			logo: 'assets/img/vitonjob.png',
			searchbar: {
				placeholder: 'string'
			},
			segments : [
				{
					title:	{
						icon : 'string',
						label : '0'
					},
					subtitle: {
						icon : 'string',
						label : 'Contrats en attente'
					},
					action: function(){
						console.log('works 1')
					},
					style : 'style2'
				},
				{
					title:	{
						icon : 'string',
						label : '0'
					},
					subtitle: {
						icon : 'string',
						label : 'Pointages  valider'
					},
					action: function(){
						console.log('works 2')
					},
					style : 'style2'
				},
				{
					title:	{
						icon : 'string',
						label : '0'
					},
					subtitle: {
						icon : 'string',
						label : 'Missions  clturer'
					},
					action: function(){
						console.log('works 3')
					},
					style : 'style2'
				}
			]
		};
		this.annoncesLabel =this.dataHeader.menu[1].label;

	}

	// update compte tab when user modify his identity data
	updateUserInfo() {
        this.event.subscribe('user:update', (userUpdate: CurrentUserDto) => {
			this.compteTab = userUpdate.identity.firstName + ' ' + userUpdate.identity.lastName.slice(0, 1) + '.';
        });
	}

	updateWebMenu(){
		this.storage
			.get('currentUser')
			.then(user => {
				console.log('[currentUser]', !!user);

				const navigateToLogin = () => {
					const vojPageList: VojPage = {
						page: LoginPage,
						isRoot: true,
						isWebModal: true,
						params: {
							closeConfirmation: false
						}
					};
					this.navigationService.navigate("list", vojPageList);
				}
				const navigateToCV = () => {
					if (user == null) {
						navigateToLogin();
					} else {
						const vojJobsListPage: VojPage = {
							page: JobsListPage,
							isRoot: true,
							isWebModal: false
						};
						const vojInformationResumePage: VojPage = {
							page: InformationResumePage,
							isRoot: true,
							isWebModal: false
						};
						const vojCalendarPage : VojPage = {
							page: CalendarPage,
							isRoot: true,
							isWebModal:false
						};
						this.navigationService.navigate('list', vojInformationResumePage);
                        //this.navigationService.navigate('details', vojJobsListPage);
                        this.navigationService.hide('details');
                        this.navigationService.navigate('main', vojCalendarPage);
					}
				};
		
				if(this.config && this.config.customer) {
					let s : Strategy = this.config.customer == 'J'?new JobyerStrategy():new EmployerStrategy();
					this.dataHeader = s.loadHeaderData(
						this,
						this.notificationFunction,
						this.navigateToSearch,
						navigateToCV,
						this.navigateToMission,
						this.goToAccount
					);
					this.annoncesLabel =this.dataHeader.menu[1].label;
				}
			});
	}

    openPage() {

    }

    openTutorial() {

    }

    isActive(page: PageInterface) {
        let childNav = this.nav1.getActiveChildNav();

        // Tabs are a special case because they have their own navigation
        if (childNav) {
            if (childNav.getSelected() && childNav.getSelected().root === page.tabComponent) {
                return 'primary';
            }
            return;
        }

        if (this.nav1.getActive() && this.nav1.getActive().name === page.name) {
            return 'primary';
        }
        return;
    }

    initializeTranslation(): void {
        // Translation configuration
        this.translate.addLangs(['en', 'fr']);

        // Default language
        this.translate.setDefaultLang('fr');

        // Use language
        this.translate.use('fr');
    }

	/*
	Set the main page according to the screen width
	 */
	private setMainPages(){
		if(!this.config.isLargeScreen){
			console.log("just one pane");
			//the other navs are not displayed in small screens, only the main nav is active
			this.rootPageMain = SearchPage;
		}else{
			console.log("many panes");
			//the main page is the third nav (the one in the right) for large screens
			this.rootPageList = SearchPage;
			this.rootPageDetails = LandingPage;
			this.rootPageMain = AboutPage;
			//this.rootPageMain = HomePage;
		}
	}

	/*
	Function updating the rootMain page when switching from one tab to another
	(called only for small screens)
	 */
	tabChanged(e){
		this.storage.get("currentUser").then((sdata:any)=> {
			let currentUser = JSON.parse(sdata);
			switch(this.selectedTab){
				case("searchTab"):
					this.rootPageMain = SearchPage;
					break;
				case("advertTab"):
					this.rootPageMain = (!currentUser ? LoginPage : InformationResumePage);
					break;
				case("missionTab"):
					this.rootPageMain = AboutPage;
					break;
				case("accountTab"):
					this.rootPageMain = (!currentUser ? LoginPage : AccountPage);
					break;
				default:
					this.rootPageMain = SearchPage;
					break;
			}
		});
	}

	/*
	Initializing navigation params and subscribing to events
	 */
	initializeNavigation(){
		//initializition
		this.nav1.name = "list";
		this.nav2.name = "details";
		this.nav3.name = "main";
		this.navigationService.setNav1(this.nav1);
		this.navigationService.setNav2(this.nav2);
		this.navigationService.setNav3(this.nav3);

		//initializing the selected tab for small screens
		this.selectedTab = "searchTab";

		//the first and third navs should be displayed in large screens
		//the second nav is hidden by default
		this.showList = true;
		this.showDetails = false;
		this.showMain = true;

		//subscribe to routing events (push, setRoot, pop, hideNav)
		this.catchNavigationEvents();
		this.catchHideNavEvents();
		this.catchPopEvents();

		//construct page mapping with tabs for small screens
		if(!this.config.isLargeScreen) {
			this.pageMapping = this.navigationService.constructPageMapping();
		}
	}

	/*
	Catch navigation events for routing
	 */
	private catchNavigationEvents(){
		this.event.subscribe("navigate:" + this.nav1.name, (data:VojPage) => {
			this.showList = true;
			this.selectedTab = this.pageMapping[data.page.name];
			if(data.isRoot){
				this.nav1.setRoot(data.page, data.params);
			}else{
				this.nav1.push(data.page, data.params);
			}
		});

		this.event.subscribe("navigate:" + this.nav2.name, (data:VojPage) => {
			this.showDetails = true;
			this.selectedTab = this.pageMapping[data.page.name];
			if(data.isRoot){
				this.nav2.setRoot(data.page, data.params);
			}else{
				this.nav2.push(data.page, data.params);
			}
		});

		this.event.subscribe("navigate:" + this.nav3.name, (data:VojPage) => {
			this.showMain = true;
			this.selectedTab = this.pageMapping[data.page.name];
			if(data.isRoot){
				this.nav3.setRoot(data.page, data.params);
			}else{
				this.nav3.push(data.page, data.params);
			}
		});
	}

	/*
  Catch hide navs events while routing
   */
	private catchHideNavEvents(){
		this.event.subscribe("hide:" + this.nav1.name, () => {
			this.showList = false;
		});

		this.event.subscribe("hide:" + this.nav2.name, () => {
			this.showDetails = false;
		});

		this.event.subscribe("hide:" + this.nav3.name, () => {
			this.showMain = false;
		});
	}

	/*
	Catch pop events for routing
	 */
	private catchPopEvents(){
		this.event.subscribe("pop:" + this.nav1.name, () => {
			this.nav1.pop();
		});

		this.event.subscribe("pop:" + this.nav2.name, () => {
			this.nav2.pop();
		});
  }

	goToAccount(caller?:MyApp){
		if(!caller)
			caller = this;
		caller.self.storage.get("currentUser").then((currentUser: CurrentUserDto) => {
			//if the user has not yet signed in
			if(!currentUser){
				let vojPageList: VojPage = {page: LoginPage, isRoot: true, isWebModal: true, params: {closeConfirmation:true}};
				caller.self.navigationService.navigate("list", vojPageList);
			}else{
				let vojPageList: VojPage = {page: AccountPage, isRoot: true};
				caller.self.navigationService.navigate("list", vojPageList);
				if(caller.self.config.isLargeScreen){
					let vojPageDetails: VojPage = {page: UserInformationPage, isRoot: true};
					caller.self.navigationService.navigate("details", vojPageDetails);
					let vojPageMain: VojPage = {page: IdentityPage, isRoot: true};
					caller.self.navigationService.navigate("main", vojPageMain);
				}
			}
		});
	}

    clearCacheStorage(){
		//debugger;
		//navigator.storage.clear();
		//let storageManager =  Navigator.storage;
		//MyApp.showCacheButton = false;

		window.localStorage.clear();
		window.sessionStorage.clear();
		this.storage.clear();
		//this.storage.remove('currentUser');
		this.jitCompiler.clearCache();
		// Unregister service worker :
        if ('serviceWorker' in navigator) {
            navigator.serviceWorker.getRegistrations().then(function(registrations) {
                for(let registration of registrations) {
                    registration.unregister()
                }
                location.reload();
            })
        }
	}
}



