import {Component} from '@angular/core';
import {AlertController, NavParams, ViewController} from 'ionic-angular'
import {Storage} from '@ionic/storage';

import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {Configuration} from "../../../config/voj-configuration";
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {SignupService} from "../../../providers/signup-service/signup-service";
import {VojFormModel} from "../../../models/voj-form.model";
import {IdentityPage} from "../identity/identity";
import {LoginPage} from "../login/login";
import {md5} from "../../../scripts/md5";
import {MasterdataService} from "../../../providers/masterdata-service/masterdata-service";
import {CountryDto} from "../../../dtos/country.dto";

import {VojPage} from "../../../models/voj-page";
import {NavigationService} from "../../../services/navigation.service";
import {EnterpriseSearchPage} from "../enterprise-search/enterprise-search";

@Component({
	selector: 'sign-up-page',
	templateUrl: 'sign-up.html',
})

export class SignUpPage{
	config: VojConfiguration;
	formData: VojFormModel;
	headerData: VojPageHeaderType;

	constructor(public params: NavParams,
							public configurationService: Configuration,
							public signupService: SignupService,
							public viewCtrl: ViewController,
							public alertCtrl: AlertController,
							private storage: Storage,
							private masterdataService: MasterdataService,
							private navigationService: NavigationService,
                			public navParams: NavParams) {
		this.config = this.configurationService.configuration;

		this.formData = {
			fields: [
				// Phone code
				{
					type: 'search-select',
					value: null,
					label: 'FORM.COUNTRY_CODE',
					dataset: [],
					required: true,
					visible: true,
					id: 'select-search',
					name: 'selectPhoneCode'
				},
				// Phone
				{
					//placeholder: 'PLACEHOLDER.ENTER_PHONE',
					type: 'tel',
					value: '',
					label: 'FORM.PHONE',
					required: true,
					readonly: false,
					visible: true,
					id: '',
					min: 9,
					max: 10,
					regex: '^[0-9]*$',
					name: 'userPhone',
					validationMsg: 'VALIDATION.PHONE_VALIDATION_MSG'
				},
				// Email
				{
					//placeholder: 'PLACEHOLDER.ENTER_MAIL',
					type: 'text',
					value: '',
					label: 'FORM.EMAIL',
					required: true,
					readonly: false,
					visible: true,
					name: 'userEmail',
					id: '',
					min: 5,
					validationMsg: 'VALIDATION.EMAIL_VALIDATION_MSG',
					regex: '^(([^<>()\\[\\]\\\\.,;:\\s@"]+(\\.[^<>()\\[\\]\\\\.,;:\\s@"]+)*)|(".+"))@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}])|(([a-zA-Z\\-0-9]+\\.)+[a-zA-Z]{2,}))$'
				},
				// Password
				{
					//placeholder: 'PLACEHOLDER.ENTER_PASSWORD',
					type: 'password',
					value: '',
					label: 'FORM.PASSWORD',
					required: true,
					readonly: false,
					visible: true,
					min: 6,
					id: '',
					name: 'userPassword',
					validationMsg: 'VALIDATION.PASSWORD_VALIDATION_MSG',
				},
				// Confirm password
				{
					//placeholder: 'PLACEHOLDER.RE_ENTER_PASSWORD',
					type: 'password',
					value: '',
					label: 'FORM.PASSWORD_CONFIRMATION',
					required: true,
					readonly: false,
					visible: true,
					min: 6,
					id: '',
					name: 'passwordConfirmation',
					validationMsg: 'VALIDATION.PASSWORD_VALIDATION_MSG',
				},
			],
			submit: {
				id: '',
				icon: 'build',
				label: 'SIGNUP.VALIDATE',
				action: (event) => {
					let samePasswd = this.checkPasswordConfirmation();
					samePasswd ? this.signUp() : false;
				}
			}
		};
		//header data
		this.headerData = {
			title: 'SIGNUP.TITLE',
			actions: [],
			style: '',
			isCanceled: false
		};
		this.setTelAndPassword();
	}

	checkPasswordConfirmation() {
		let password = md5(this.formData.fields.filter(f => f.name === "userPassword")[0].value);
		let passwordConfirmation = md5(this.formData.fields.filter(f => f.name === "passwordConfirmation")[0].value);

		if (password !== passwordConfirmation) {
			return false;
		}
		return true;
	}

	/*checkPhone() {
			for (let i = 0; i < this.formData.fields.length; i++) {
					if (this.formData.fields[i].name === 'userPhone') {
							if (this.formData.fields[i].value[0] === '0') {
									console.log('checkPhone was called. First symbol is 0. Corrected.');
									this.formData.fields[i].value = this.formData.fields[i].value.slice(1);
							}
					}
			}
	}*/

	ngOnInit() {
		// Load phone code list
		this.masterdataService.loadCountriesList().then((data: CountryDto[]) => {
			// Set dataset of phone codes of the form
			this.formData.fields.filter(f => f.name === "selectPhoneCode")[0].dataset = this.masterdataService.convertCountriesListToDataset(data);
			// The default phone code is France code (33)
			this.formData.fields.filter(f => f.name === "selectPhoneCode")[0].value = "33";
		});
	}

	signUp() {
		//prepare credentials data
		let code = this.formData.fields.filter(f => f.name === "selectPhoneCode")[0].value;
		let phone = this.formData.fields.filter(f => f.name === "userPhone")[0].value;
		let email = this.formData.fields.filter(f => f.name === "userEmail")[0].value;
		let password = md5(this.formData.fields.filter(f => f.name === "userPassword")[0].value);
		let phoneWithCode = "+" + code + phone.substr(phone.length - 9);

		//call web service method
		this.signupService.signup(phoneWithCode, email, password).then((data: any) => {
			//if credentials already registred in our DB : show error msg
			if (data.found) {
				this.displayNotificationForUsedCredentials();
			} else {
				//if credentials does not exist in our DB : store new user in localDB
				//dismiss the sign in modal and display identity modal
				let account: CurrentUserDto = data;
				this.storage.set("currentUser", JSON.stringify(account)).then(() => {
					if (this.config.customer == 'J') {
						this.goToIdentityPage();
					} else {
						this.gotoCorporamaPage()
					}
				});
			}
		});
	}

	goToIdentityPage() {
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: IdentityPage, isRoot: true, isWebModal: true, params: {isInscription: true}};
		this.navigationService.navigate("main", vojPage);
	}

	gotoCorporamaPage() {
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: EnterpriseSearchPage, isRoot: true, isWebModal: true, params: {isInscription: true}};
		this.navigationService.navigate("main", vojPage);
	}

	//TODO: this error msg should be displayed in the form
	displayNotificationForUsedCredentials() {
		let alert = this.alertCtrl.create({
			title: 'Attention!',
			subTitle: 'Ces informations d\'identification sont dj enregistres. Entrez un autre numro de tlphone et un email.',
			buttons: ['OK']
		});
		alert.present();
	}

	goToLogin() {
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: LoginPage, isRoot: true, isWebModal: true};
		this.navigationService.navigate("list", vojPage);
	}

    setTelAndPassword() {
	    if (this.navParams.get('loginData') !== null) {
            let loginData = this.navParams.get('loginData');
            this.formData.fields[1].value = loginData.tel;
            this.formData.fields[3].value = loginData.password;
        }
	}
}


