export enum MultiSelectTarget { Week, CustomRange };

export interface MultiSelectActive {
  isAccepted: boolean;
  /** Optional for accepting selected range */
  target?: MultiSelectTarget | string
}

export interface MultiSelectRange {
  start: Date;
  end: Date;
}

export interface MultiSelect {
  payload: Array<Date>;
}

export interface MultiSelectEntry {
  id: string;
  date: Date;
}

export type MultiSelectEntries = Array<MultiSelectEntry>;


