import { Component, ElementRef, Input } from '@angular/core';
import { IonicPage } from 'ionic-angular';
import { Platform, AlertController } from 'ionic-angular';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { CalendarType, CalendarTypeMode } from '../../models/calendar.model';

@IonicPage()
@Component({
	selector: 'voj-calendar',
	templateUrl: 'voj-calendar.html'
})
export class VojCalendar {
	@Input('data')
	public data: CalendarType;

	@Input('type')
	public calendarType: CalendarTypeMode;

	elRef: ElementRef;
	platform: Platform;

	desktopPlatform: boolean = false;

	constructor(
		public alert: AlertController,
		elRef: ElementRef,
		platform: Platform
	) {
		this.elRef = elRef;
		this.platform = platform;

		if (this.platform.is('core')) {
			this.desktopPlatform = true;
			console.info('Desktop');
		}
	}

	public handleWeekSelect(data) {
		this.alert
			.create({
				title: 'Week Selection',
				message: `<pre>${ JSON.stringify(data, null, 2) }</pre>`,
				buttons: [ 'OK' ]
			})
			.present();
	}

	public handleonMultiSelect($event) {
		this.alert
			.create({
				title: 'Multi-selection',
				message: `<pre>${ JSON.stringify($event, null, 2) }</pre>`,
				buttons: [ 'OK' ]
			})
			.present();
	}

	public handleonRangeSelect($event) {
		this.alert
			.create({
				title: 'Range selection',
				message: `<pre>${ JSON.stringify($event, null, 2) }</pre>`,
				buttons: [ 'OK' ]
			})
			.present();
	}

}



