import { Component } from '@angular/core';
import { IonicPage } from 'ionic-angular';

import { Configuration } from "../../../config/voj-configuration";
import {Storage} from "@ionic/storage";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {EmployerStrategy} from "./strategy/employer";
import {JobyerStrategy} from "./strategy/jobyer";
import {Context} from "./strategy/context";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {IdentityPage} from "../identity/identity";
import {MainAddressPage} from "../main-address/main-address";
import {PersonalDetailsPage} from "../personal-details/personal-details";
import {PersonalDetailsIdentityPage} from "../personal-details-identity/personal-details-identity";
import {BankSettingsPage} from "../bank-settings/bank-settings";
import {Page} from "ionic-angular/navigation/nav-util";
import {DocumentsPage} from "../../documents/documents";

@IonicPage()
@Component({
	selector: 'user-information-page',
	templateUrl: 'user-information.html'
})
export class UserInformationPage {

    dataPageHeader: VojPageHeaderType;
    userData: any = {};
    userType: string;
    currentUser: CurrentUserDto;

	constructor(public config: Configuration,
							private storage: Storage,
							private context: Context,
							private navigationService: NavigationService) {
		// Get user type
		this.userType = (this.config.configuration.customer == "J" ? "jobyer" : "employeur");
		console.log('User type:', this.userType);

        /***
		 * Strategy pattern test
         * @type {EmployerStrategy | JobyerStrategy}
         */
        Context.strategy = (this.userType!=='jobyer') ? new EmployerStrategy() : new JobyerStrategy();
        this.dataPageHeader = this.context.loadHeaderData();
        /** END **/

		this.storage.get("currentUser").then((data: string) => {
			if (!data) {
				return;
			}
			this.currentUser = JSON.parse(data);

			this.userData = {
				// JobyerStrategy
				//name: this.currentUser.identity ? this.currentUser.identity.lastName : '',
				//firstName: this.currentUser.identity ? this.currentUser.identity.firstName : '',
				name: "Ma civilit",
				identityPage: IdentityPage,
				no: "Mon adresse", // the same for jobyer and employer
				addressPage: MainAddressPage,
				birthDate: "Mes donnes sociales",
				birthDataPage: PersonalDetailsPage,
				placeOfBirth: "",
				identityDocumentType: 'Mon identit',
				identityCardPage: PersonalDetailsIdentityPage,
				docNumber: "",
				bankDetails: 'Mes coordonnes bancaires',
				bankSettingPage: BankSettingsPage,
				documents: 'Mes documents',
				documentsPage: DocumentsPage,

				streetName: '', // the same for jobyer and employer
				postcode: "", // the same for jobyer and employer
				city: '', // the same for jobyer and employer

				// EmployerStrategy
				companyName: 'Dome Enterprise'
			};
		});
	}

    presentPage(page: Page) {
		let vojPage: VojPage = {page: page, isRoot: true};
		this.navigationService.navigate("main", vojPage)
	}
	test(){

	}
}



