import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { VojSearchSelect } from './voj-search-select';
import { TranslateModule } from '@ngx-translate/core';
import { VojFormModule } from "../../components/voj-form/voj-form.module";


@NgModule({
	declarations: [
        VojSearchSelect,
	],
	imports: [
		IonicPageModule.forChild(VojSearchSelect),
        TranslateModule.forChild(),
        VojFormModule
	],
	exports: [
        VojSearchSelect
	]
})
export class VojSearchSelectModule {
}



