import { Injectable, Inject, LOCALE_ID } from '@angular/core';
import { CalendarComponent } from 'ap-angular2-fullcalendar';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';
import { Options, EventObject, ViewObject } from 'fullcalendar';
import { Moment } from 'moment';
import * as $ from 'jquery';

import {
	MultiSelectActive,
	MultiSelectTarget
} from '../models/shared';

export type DayClickHandler = (date: Date | Moment, jsEvent: MouseEvent, view: ViewObject) => void;

export interface FullCalendarOptions extends Options {
	locale?: string;
	fixedWeekCount?: boolean;
	selectAllow?: (info: { start: Moment, end: Moment, resourceId: any }) => boolean;
	dayClick?: DayClickHandler
}

export class MultiSelect {
	public isActive = false;

	constructor() {
	}
}


@Injectable()
export class FullCalendarService {
	private optionsStore: FullCalendarOptions;
	private options$: BehaviorSubject<FullCalendarOptions>;
	private instance: CalendarComponent;

	public activateMultiSelect$: Subject<MultiSelectTarget>;

	/** Whether to emit or reset selected range in multi-select mode */
	public acceptSelectedRange$: Subject<MultiSelectActive>;

	public calendarRendered$: Subject<void>;
	public clickDelegate$: Subject<EventTarget>;

	public isMultiSelectActive: boolean;
	public tempSelection: Array<any>;

	private weekSelectionHandler() {
		const parentContext: this = this;
		return function() {
			const vm = this as HTMLButtonElement;
			const $oppositeModeButton = $(vm.parentElement).find('.fc-multiSelect-button');

			vm.parentElement.classList.toggle('voj-weekselect-active');
			vm.parentElement.dataset.target = MultiSelectTarget.Week.toString();

			parentContext.resetEnabledState(vm.parentElement);
			parentContext.isMultiSelectActive = vm.parentElement.classList.contains('voj-weekselect-active');

			if (parentContext.isMultiSelectActive) {
				$oppositeModeButton.prop('disabled', true);
				parentContext.activateMultiSelect$.next(MultiSelectTarget.Week);
			}
		}
	}

	private multiSelectionHandler() {
		const parentContext: this = this;
		return function() {
			const vm = this as HTMLButtonElement;
			const $oppositeModeButton = $(vm.parentElement).find('.fc-weekSelect-button');

			console.log('[enabling:multiselect]');

			vm.parentElement.classList.toggle('voj-multiselect-active');
			vm.parentElement.dataset.target = MultiSelectTarget.CustomRange.toString();

			parentContext.resetEnabledState(vm.parentElement);
			parentContext.isMultiSelectActive = vm.parentElement.classList.contains('voj-multiselect-active');

			if (parentContext.isMultiSelectActive) {
				$oppositeModeButton.prop('disabled', true);
				parentContext.activateMultiSelect$.next(MultiSelectTarget.CustomRange);
			}
		}
	}

	private resetState(elem: HTMLElement) {
		elem.classList.remove('voj-weekselect-active');
		elem.classList.remove('voj-multiselect-active');
		elem.dataset.target = '';

		[].slice
			.call(elem.children)
			.forEach((item: HTMLElement) => {
				if (item.classList.contains('fc-acceptSelection-button')
					|| item.classList.contains('fc-resetSelection-button')) {
					$(item).prop('disabled', true);
				} else {
					$(item).prop('disabled', false);
				}
			});
	}

	private resetEnabledState(elem: HTMLElement) {
		[].slice
			.call(elem.children)
			.forEach((item: HTMLElement) => $(item).prop('disabled', false));
	}

	private acceptSelectionHandler() {
		const parentContext = this;
		return function() {
			const vm = this;
			const target = MultiSelectTarget[Number(vm.parentElement.dataset.target)];

			parentContext.tempSelection = [];
			parentContext.resetState(vm.parentElement);
			parentContext.acceptSelectedRange$.next({
				isAccepted: true,
				target
			});
		}
	}

	private resetSelectionHandler() {
		const parentContext = this;
		return function() {
			const vm = this;
			const target = MultiSelectTarget[Number(vm.parentElement.dataset.target)];
			
			parentContext.tempSelection = [];
			parentContext.resetState(vm.parentElement);
			parentContext.acceptSelectedRange$.next({
				isAccepted: false,
				target
			});
		}
	}

	public getOptions() {
		return this.options$.asObservable();
	}

	public setOptions(updatedOptions: FullCalendarOptions) {
		this.optionsStore = { ...this.optionsStore, ...updatedOptions };
		this.options$.next(this.optionsStore);
	}

	public setInstance(instance: CalendarComponent) {
		this.instance = instance;
	}

	public disableMultiSelect() {
		this.isMultiSelectActive = false;
	}

	constructor(@Inject(LOCALE_ID) private locale) {
		this.activateMultiSelect$ = new Subject<MultiSelectTarget>();
		this.acceptSelectedRange$ = new Subject<MultiSelectActive>();
		this.calendarRendered$ = new Subject<void>();
		this.clickDelegate$ = new Subject<EventTarget>();

		this.optionsStore = {
			header: {
				right: 'agendaDay,agendaWeek,month',
				center: 'prev,title,next',
				left: 'weekSelect,multiSelect,acceptSelection,resetSelection'
			},
			customButtons: {
				weekSelect: {
					text: 'Week',
					click: this.weekSelectionHandler()
				},
				multiSelect: {
					text: 'Custom',
					click: this.multiSelectionHandler()
				},
				acceptSelection: {
					text: 'Accept',
					click: this.acceptSelectionHandler()
				},
				resetSelection: {
					text: 'Reset',
					click: this.resetSelectionHandler()
				}
			},
			height: 'auto',
			allDaySlot: false,
			locale: this.locale,
			fixedWeekCount: false,
			defaultDate: new Date(),
			editable: true,
			eventLimit: true,
			events: [ ],
			selectable: false,
			dayClick(date, jsEvent, view) {
				console.log('[dayclick]', date, view);
			},
			eventClick(event, jsEvent, view) {
				console.log('[eventclick]', event, view);
			},
			eventAfterAllRender: () => {
				this.calendarRendered$.next();
			}
		};

		this.options$ = new BehaviorSubject<FullCalendarOptions>(this.optionsStore);
	}

	public onDayClick(event: MouseEvent | JQueryEventObject) {
		this.clickDelegate$.next(event.target);
	}
}



