import {Injectable} from '@angular/core';
import {Events} from "ionic-angular";
import {VojCardModel} from "../../models/voj-card.model";
import {eBusType, Passenger} from "../../models/voj-event.model";
import {VojMap} from "../../components/voj-map/voj-map";
import {SearchPage} from "../../pages/search-module/search/search";
import {SearchServiceProvider} from "../search-service/search-service";
import {MyApp} from "../../app/app.component";
import {DetailsPage} from "../../pages/details/details";
import {VojCard} from "../../components/voj-card/voj-card";

/*
  Generated class for the EventBusProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class EventBusProvider extends Events{

    //detailsCardPage: DetailsPage;
    passenger:Passenger;
    map:VojMap;
    search : SearchPage;
    app : MyApp;
    searchService : SearchServiceProvider;
    details : DetailsPage;


    constructor(private event: Events) {
        super();
        console.log('Hello EventBusProvider Provider');
        this.passenger = new Passenger();
        this.event.subscribe('currentUser:logged-in', () => this.publishAppHeader())
    }

    // MAP EVENTS
    /**
     * Send an event to set map values
     * @param {eMapType} passenger
     */
    set(passenger: eBusType) {
        if (passenger.condition)
            super.publish(passenger.topic, passenger.data);
    }

    /**
     * Get an event to load map values
     */
    get(passenger: eBusType) {
        return new Promise((resolve, reject) => {
            super.subscribe(passenger.topic, (data) => {
                if (passenger.condition)
                    resolve(data);
                else
                    reject('Passenger type is rejected for this bus line');
            });
        });
    }

    subscribeSearchService(ssp : SearchServiceProvider) {
        this.searchService = ssp;
    }

    subscribeMap(m : VojMap){
        this.map = m;
    }

    subscribeDetails(d : DetailsPage) {
        this.details = d;
    }

    publishDetails(card : VojCardModel) {
        if(this.details)
            this.details.updateCard(card);
    }

    publishMap(cards){
        if(this.map)
            this.map.updateMap(cards);
    }

    subscribeSearch(s : SearchPage) {
        this.search = s;
    }

    publishSearch(cards){
        if(this.search){
            this.search.pageHeader.actions[0].label = String(this.app.badge);
        }
    }

    publishPushEvents() {
        if(this.search) {
            this.searchService.eventMode = true;
            this.search.eventsMode = true;
            this.search.updateCards(this.searchService.eventsCards);
            this.search.loadEvents();
            this.search.badge = this.search.eventsCards.length;
        }

    }

    subscribeMainApp(a : MyApp) {
        this.app = a;
    }

    publishAppEventUpdate(mode?:string) {
        if(mode === 'read') {

            if(this.app.badge>0) {
                this.app.dataHeader.menu[0].badge--;
                this.app.badge --;
                this.search.pageHeader.actions[0].label = String(this.app.badge);
            }
            this.search.eventsMode = true;
            this.search.updateCards(this.searchService.eventsCards);
            this.search.loadEvents();
            return;
        }
        this.app.checkNotifications();
    }

    publishAppHeader(){
        if(this.app) {
            this.app.updateWebMenu();
        }
    }

    /*subscribeDetailsPage(instance: DetailsPage) {
        this.detailsCardPage = instance;
    }

    publishCardDetails(card: VojCardModel) {
        this.detailsCardPage.updateCard(card);
    }

    setDetailsView(data: { isVisible: boolean, id: number }) {
        this.event.publish('SHOW_DETAILS', data);
    }

    getDetailsView() {
        return new Promise((resolve) => {
            this.event.subscribe('SHOW_DETAILS', (data: { isVisible: boolean, id: number }) => {
                resolve(data);
            });
        });
    }
     */

}



