import {
	AfterViewInit,
	Component,
	ElementRef,
	EventEmitter,
	Input,
	ViewChild,
	SimpleChanges,
	OnInit,
	Output
} from '@angular/core';
import { CalendarComponent } from 'ap-angular2-fullcalendar';
import { EventObject as FullCalendarEventObject } from 'fullcalendar';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import * as moment from 'moment';
import { Moment } from 'moment';
import { eachDay } from 'date-fns';
import * as $ from 'jquery';

import { FullCalendarOptions, FullCalendarService } from './full-calendar.service';
import { CalendarType, CalendarTypeMode } from '../../../models/calendar.model';
import { SlotTypeInterface } from '../../../models/slot-type.interface';
import {
	MultiSelect,
	MultiSelectActive,
	MultiSelectRange,
	MultiSelectTarget,
	MultiSelectEntries
} from '../models/shared';

interface SelectOption<V extends any> {
	label: string;
	value: V;
}

export type SelectOptions<V> = Array<SelectOption<V>>;

@Component({
	selector: 'full-calendar',
	templateUrl: 'full-calendar.html',
	providers: [FullCalendarService]
})
export class FullCalendarComponent implements AfterViewInit, OnInit {
	@ViewChild(CalendarComponent)
	public calendar: CalendarComponent;

	@Input('data')
	public data: CalendarType;

	@Input('mode')
	public mode: CalendarTypeMode;

	/** Command-trigger to activate multi-select mode */
	@Output() onMultiSelectActive = new EventEmitter<MultiSelectTarget>(true);
	/** Trigger for multi-select range */
	@Output() onMultiSelect = new EventEmitter<MultiSelect>(true);
	@Output() onWeekSelect = new EventEmitter<MultiSelectRange>(true);
	@Output() onRangeSelect = new EventEmitter<MultiSelect>(true)

	public currentDate$: BehaviorSubject<Date>;
	public calendarOptions: Observable<FullCalendarOptions>;
	public currentMonth;
	public currentYear;
	public fullcalendarMode: 'agendaDay' | 'agendaWeek' | 'month';
	public years: SelectOptions<number>;
	public months: SelectOptions<number>;

	constructor(private calendarService: FullCalendarService) {
		const currentYearOffset = new Date().getFullYear() - 10;
		this.fullcalendarMode = 'month';
		this.years = new Array(20)
			.fill(null)
			.map((x, i) => ({
				label: (currentYearOffset + i).toString(),
				value: currentYearOffset + i
			}));
		this.months = [
			{ label: 'Janvier', value: 0 },
			{ label: 'Fvrier', value: 1 },
			{ label: 'Mars', value: 2 },
			{ label: 'Avril', value: 3 },
			{ label: 'Mai', value: 4 },
			{ label: 'Juin', value: 5 },
			{ label: 'Juillet', value: 6 },
			{ label: 'Aout', value: 7 },
			{ label: 'Septembre', value: 8 },
			{ label: 'Octobre', value: 9 },
			{ label: 'Novembre', value: 10 },
			{ label: 'Dcembre', value: 11 }
		];
	}

	private htmlQuery<T extends HTMLElement>(selector: string) {
		const query = document.querySelectorAll(selector);
		const result: Array<T> = query.length > 0
			? [].slice.call(query)
			: [];
		return result;
	}

	private clearSelectedRange() {
		this.calendar.fullCalendar('unselect');
		this.htmlQuery('.voj-range-selected-event')
			.forEach(elem => elem.classList.remove('voj-range-selected-event'));
		this.htmlQuery('.voj-selected-event')
			.forEach(elem => elem.classList.remove('voj-selected-event'));
	}

	private mapSlotToEventObject(slot: SlotTypeInterface): FullCalendarEventObject {
		return {
			start: slot.startDate,
			end: slot.endDate || null,
			title: slot.title,
			colot: slot.color
		}
	}

	private initRangeSelect() {
		const target = $('angular2-fullcalendar > .fc-view-container').get(0);
		const mouseUp$ = Observable.fromEvent<MouseEvent>(target, 'mouseup');
		const mouseMove$ = Observable.fromEvent<MouseEvent>(document, 'mousemove');
		const mouseDown$ = Observable.fromEvent<MouseEvent>(target, 'mousedown');

		mouseDown$
			.filter(() => !this.calendarService.isMultiSelectActive)
			.do(() => this.clearSelectedRange())
			.flatMap(elem => mouseMove$
				.takeUntil(mouseUp$)
				.distinctUntilChanged((a, b) => {
					const c = (a.target as HTMLElement).dataset.date;
					const d = (b.target as HTMLElement).dataset.date;
					return c === d;
				})
				.map(event => event.target as HTMLElement)
				.filter(target => target.classList.contains('fc-today')
							   || target.classList.contains('fc-future'))
				.do(target => target.classList.toggle('voj-range-selected-event'))
				.buffer(mouseUp$)
			)
			.map(entries => entries
				.filter(entry => entry.classList.contains('voj-range-selected-event')))
			.do(entries => console.log('[buffer:after]', entries.map(entry => entry.dataset.date)))
			.do(entries => entries.length <= 1 && this.clearSelectedRange())
			.filter(entries => entries.length > 1)
			.map(entries => entries
				.map(entry => new Date(entry.dataset.date).toISOString())
				.sort())
			.do(dates => console.log('[dates]', dates))
			.map(dates => {
				console.log('[range]', eachDay(dates[0], dates[dates.length -1]));
				return eachDay(dates[0], dates[dates.length -1]);
			})
			.subscribe(payload => {
				this.clearSelectedRange();
				this.onRangeSelect.emit({ payload });
			});

	}

	private initWeekSelect() {
		const click$ = this.calendarService.clickDelegate$.asObservable();
		this.calendarService.activateMultiSelect$
			.asObservable()
			.filter(target => target === MultiSelectTarget.Week)
			.switchMap(() => click$
				.takeUntil(this.calendarService.acceptSelectedRange$.asObservable()))
			.pluck('parentElement')
			.do(_ => this.onMultiSelectActive.emit(MultiSelectTarget.Week))
			.map((selectedRow: HTMLTableRowElement) => {
				const range = $(selectedRow)
					.closest('.fc-row.fc-week')
					.find('.fc-bg .fc-day')
					.toArray()
				return {
					start: {
						date: range[0].dataset.date,
						elem: range[0]
					},
					end: {
						date: range[range.length - 1].dataset.date,
						elem: range[range.length - 1]
					}
				};
			})
			.do(range => {
				const start = moment(range.start.date);
				const end = moment(range.end.date).add(1, 'day');
				this.clearSelectedRange();
				this.calendar.fullCalendar('select', start, end);
			})
			.switchMap(
				selectedRow => this.calendarService.acceptSelectedRange$.asObservable(),
				(range, state) => ({ range, state }))
			.subscribe(({ range, state }) => {
				this.clearSelectedRange();
				this.calendarService.disableMultiSelect();

				if (state.isAccepted && state.target === MultiSelectTarget[MultiSelectTarget.Week]) {
					this.onWeekSelect.emit({
						start: new Date(range.start.date),
						end: new Date(range.end.date)
					});
				}
			});
	}

	private initMultiSelect() {
		const cancelation$ = this.calendarService.acceptSelectedRange$.asObservable();
		const opening$ = this.calendarService.activateMultiSelect$
			.asObservable()
			.filter(target => target === MultiSelectTarget.CustomRange)
		const multiSelect$ = this.calendarService.clickDelegate$
			.asObservable()
			.takeUntil(cancelation$)
			.do((elem: HTMLElement) => elem.classList.toggle('voj-selected-event'))
			.buffer(cancelation$)
			.map((targets: Array<HTMLTableCellElement>) => targets
				.filter(targets => targets.classList.contains('voj-selected-event'))
				.map(target => {
					const { id, dataset: { date } } = target;
					return { id, date: new Date(date) };
				}));

		opening$
			.do(() => this.onMultiSelectActive.emit(MultiSelectTarget.CustomRange))
			.switchMap(() => multiSelect$)
			.withLatestFrom(cancelation$)
			.map(([ range, state ]) => ({ range, state }))
			.subscribe(({ range, state }) => {
				const isValidTarget = state.target === MultiSelectTarget[MultiSelectTarget.CustomRange];
				this.clearSelectedRange();
				this.calendarService.disableMultiSelect();

				if (state.isAccepted && isValidTarget) {
					this.onMultiSelect.emit({
						payload: range.map(item => item.date)
					});
				}
			});
	}

	public ngAfterViewInit() {
		this.calendarService.setInstance(this.calendar);
	}

	public ngOnInit() {
		const currentDate = new Date();

		this.currentDate$ = new BehaviorSubject<Date>(new Date());
		this.currentMonth = currentDate.getMonth();
		this.currentYear = currentDate.getFullYear();
		this.calendarOptions = this.calendarService.getOptions();

		this.calendarService.setOptions({
			selectAllow: info => {
				return moment(info.start).isSameOrAfter(new Date(), 'hour');
			},
			dayClick: (date, event, view) => {
				const hasHandler = typeof this.data.dayClick !== 'undefined'
					&& this.data.dayClick instanceof Function;

				this.calendarService.onDayClick(event);

				if (moment(date).isSameOrAfter(new Date())) {
					this.calendar.fullCalendar('gotoDate', date);
				}

				if (hasHandler) {
					this.data.dayClick(date, event, view);
				}
			},
			defaultDate: currentDate,
			eventClick: this.data.eventClick,
			events: this.data.slots.map(this.mapSlotToEventObject)
		});

		this.currentDate$.subscribe(date => this.setCurrentDate(date));

		this.calendarService.calendarRendered$
			.asObservable()
			.subscribe(() => {
				this.initWeekSelect();
				this.initMultiSelect();
				this.initRangeSelect();
			})
	}

	public getCurrentDate() {
		const date = $('angular2-fullcalendar').fullCalendar('getDate') as any;
		return (date as Moment).toDate();
	}

	public setCurrentDate(date) {
		$('angular2-fullcalendar').fullCalendar('gotoDate', date);
	}

	public handleCalendarModeChange(mode) {
		this.fullcalendarMode = mode;
		$('angular2-fullcalendar').fullCalendar('changeView', mode);
	}

	public handleMonthChange(event) {
		const date = new Date(this.getCurrentDate());
		date.setMonth(event);
		this.currentDate$.next(date);
	}

	public handleYearChange(event) {
		const date = new Date(this.getCurrentDate());
		date.setFullYear(event);
		this.currentDate$.next(date);
	}

}



