import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ButtonType } from '../../../models/button-type.model';
import { FieldType } from '../../../models/field-type';
import { FormType } from '../../../models/form-type.model';
import { VojPageHeaderType } from '../../../models/voj-page-header.model';

@IonicPage()
@Component({
	selector: 'page-remuneration',
	templateUrl: 'remuneration.html',
})
export class RemunerationPage {
	public formData: FormType;
	public dataPageHeader: VojPageHeaderType;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams
	) {
		this.dataPageHeader = {
			title: 'REMUNERATION.MY_REMUNERATION',
			actions: [
                {
                    id: 'help',
                    label: '',
                    icon: 'help',
                    action: () => {
                        console.log('Click help button.')
                    }
                }
            ],
            style: 'any',
            isCanceled: true
		};
		this.formData = {
			submit: {
				id: 'voj-remuneration-submit',
				icon: '',
				label: 'REMUNERATION.VALIDATE',
				action: () => this.handleSubmit()
			},
			fields: [
				{
					id: '1',
					placeholder: '',
					type: 'number',
					value: null,
					label: 'REMUNERATION.DESIRED_HOURLY_RATE',
					name: 'desiredHourlyRate',
					required: true,
					visible: true,
					readonly: false,
					min: 9.76,
				},
				{
					id: '2',
					placeholder: '',
					type: 'select',
					value: null,
					label: 'REMUNERATION.FREQUENCY_OF_REMUNERATION',
					name: 'frequencyOfRemuneration',
					visible: true,
					required: true,
					readonly: false
				}
			]
		}
	}

	private handleSubmit() {
		console.log('[remuneration:submit-handled]');
	}

}



