import {
  Component,
  ElementRef,
  OnInit,
  OnChanges,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
  ViewChild,
  ViewChildren,
  QueryList,
  TemplateRef
} from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { DatePipe } from '@angular/common';
import { Slides, Slide } from 'ionic-angular';
import { isPast } from 'date-fns';
import 'rxjs/add/observable/empty';
import 'rxjs/add/observable/of';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/from';
import 'rxjs/add/observable/timer';
import 'rxjs/add/operator/distinctuntilchanged';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/concatMap';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/buffer';
import 'rxjs/add/operator/pluck';
import 'rxjs/add/operator/bufferCount';
import 'rxjs/add/operator/takeUntil';
import 'rxjs/add/operator/withLatestFrom';

import {
  CalendarMode,
  ICalendarComponent,
  IDateFormatter,
  IEvent,
  IMonthView,
  IMonthViewDisplayEventTemplateContext,
  IMonthViewRow,
  IRange,
  ITimeSelected
} from '../calendar';
import {
  MultiSelect,
  MultiSelectActive,
  MultiSelectRange,
  MultiSelectTarget
} from '../../models/shared';
import { CalendarService } from '../calendar.service';

export interface ITimeSelectedModel {
  payload: ITimeSelected;
  target?: HTMLElement;
}

export interface MultiSelectEntry {
  id: string;
  date: Date;
}

export type MultiSelectEntries = Array<MultiSelectEntry>;

@Component({
  selector: 'monthview',
  templateUrl: 'monthview.html',
  styles: [
    `
      .text-muted {
        color: #999;
      }

      .table-fixed {
        table-layout: fixed;
      }

      .table {
        width: 100%;
        max-width: 100%;
        background-color: transparent;
        border: none !important;
      }

      .table > thead > tr > th,
      .table > tbody > tr > th,
      .table > tfoot > tr > th,
      .table > thead > tr > td,
      .table > tbody > tr > td,
      .table > tfoot > tr > td {
        padding: 8px;
        line-height: 20px;
        vertical-align: top;
      }

      .table > thead > tr > th {
        vertical-align: bottom;
        border-bottom: 2px solid #ddd;
      }

      .table > thead:first-child > tr:first-child > th, .table > thead:first-child > tr:first-child > td {
        border-top: 0
      }

      .table > tbody + tbody {
        border-top: 2px solid #ddd;
      }

      .table-bordered {
        border: 1px solid #ddd;
      }

      .table-bordered > thead > tr > th, .table-bordered > tbody > tr > th, .table-bordered > tfoot > tr > th,
      .table-bordered > thead > tr > td, .table-bordered > tbody > tr > td, .table-bordered > tfoot > tr > td {
        border: 1px solid #ddd;
      }

      .table-bordered > thead > tr > th, .table-bordered > thead > tr > td {
        border-bottom-width: 2px;
      }

      .table-striped > tbody > tr:nth-child(odd) > td, .table-striped > tbody > tr:nth-child(odd) > th {
        background-color: #f9f9f9
      }

      .monthview-primary-with-event {
        background-color: #3a87ad;
        color: white;
      }

      .monthview-current {
        background-color: #f0f0f0;
      }

      .monthview-selected {
        background-color: #009900;
        color: white;
      }

      .monthview-datetable td.monthview-disabled {
        color: lightgrey;
        cursor: default;
      }

      .monthview-datetable th {
        text-align: center;
      }

      .monthview-datetable td {
        cursor: pointer;
        text-align: center;
      }

      .monthview-secondary-with-event {
        background-color: #d9edf7;
      }

      ::-webkit-scrollbar,
      *::-webkit-scrollbar {
        display: none;
      }
    `,
    `
      .monthview-week-title {
        border-bottom: none !important;
      }

      .monthview-slide {
        padding-top: 40px;
        align-items: flex-start;
      }

      .monthview-slide table thead {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        background: #fff;
        z-index: 24810;
      }

      .monthview-slide table thead tr {
        height: 40px;
        display: flex;
        justify-content: space-around;
      }

      .monthview-slide table tbody td {
        height: 40px;
        transition: color 100ms ease-in-out;
      }

      .monthview-slide table tbody td.weekend,
      .monthview-week-title:last-child,
      .monthview-week-title:nth-last-child(2) {
        color: red;
      }

      .monthview-slide table tbody {
        transform: translateY(0px);
      }

      .monthview-current,
      .monthview-selected {
        border-radius: 100%;
        transition: border-radius 200ms ease-in-out;
      }

      .monthview-selected {
        background-color: #139dea !important;
        color: #fff !important;
      }

      .monthview-slide table tbody td.text-muted.weekend {
        color: #ad4a4a;
      }

      .monthview-primary-with-event,
      .monthview-secondary-with-event {
        background-color: transparent;
        color: #333;
      }

      .monthview-secondary-with-event {
        color: #888;
      }

      .selected-week td,
      .multiselect-col {
        background-color: #139dea !important;
        color: #fff !important;
        font-weight: bold;
        border-radius: 0;
        border: 1px solid #139dea;
      }
    `
  ]
})
export class MonthViewComponent implements ICalendarComponent, OnInit, OnChanges {
  @ViewChild('monthSlider') slider: Slides;
  @ViewChildren(Slide) monthSlides: QueryList<Slide>;

  @Input() monthviewDisplayEventTemplate: TemplateRef<IMonthViewDisplayEventTemplateContext>;
  @Input() monthviewInactiveDisplayEventTemplate: TemplateRef<IMonthViewDisplayEventTemplateContext>;
  @Input() monthviewEventDetailTemplate: TemplateRef<IMonthViewDisplayEventTemplateContext>;

  @Input() formatDay: string;
  @Input() formatDayHeader: string;
  @Input() formatMonthTitle: string;
  @Input() eventSource: IEvent[];
  @Input() startingDayMonth: number;
  @Input() showEventDetail: boolean;
  @Input() noEventsLabel: string;
  @Input() autoSelect: boolean = true;
  @Input() markDisabled: (date: Date) => boolean;
  @Input() locale: string;
  @Input() dateFormatter: IDateFormatter;
  @Input() dir: string = "";
  @Input() lockSwipeToPrev: boolean;
  @Input() lockSwipes: boolean;
  @Input() focusOffset: Observable<number>;

  /** Whether to emit or reset selected range in multi-select mode */
  @Input('acceptSelectedRange')
  public acceptSelectedRange: Observable<MultiSelectActive>;

  @Input('activateMultiSelect')
  public activateMultiSelect: Observable<MultiSelectTarget>;

  @Output() onRangeChanged = new EventEmitter<IRange>();
  @Output() onEventSelected = new EventEmitter<IEvent>();
  @Output() onTimeSelected = new EventEmitter<ITimeSelected>(true);
  @Output() onTitleChanged = new EventEmitter<string>(true);

  /** Command-trigger to activate multi-select mode */
  @Output() onMultiSelectActive = new EventEmitter<MultiSelectTarget>(true);
  /** Trigger for multi-select range */
  @Output() onMultiSelect = new EventEmitter<MultiSelect>(true);
  @Output() onWeekSelect = new EventEmitter<MultiSelectRange>(true);

  public views: IMonthView[] = [];
  public currentViewIndex = 0;
  public selectedDate: IMonthViewRow;
  public range: IRange;
  public mode: CalendarMode = 'month';
  public direction = 0;

  private moveOnSelected = false;
  private inited = false;
  private callbackOnInit = true;
  private currentDateChangedFromParentSubscription: Subscription;
  private eventSourceChangedSubscription: Subscription;
  private weekSelectSubscription: Subscription;
  private rangeSelectSubscription: Subscription;
  private formatDayLabel: (date: Date) => string;
  private formatDayHeaderLabel: (date: Date) => string;
  private formatTitle: (date: Date) => string;
  private onSlideChange$ = new EventEmitter<any>();

  private markSelectedRow() {
    Array.prototype.slice
      .call(document.querySelectorAll('tr.selected-row'))
      .forEach(elem => elem.classList.remove('selected-row'));

    const selected = this.slider
      .getElementRef().nativeElement
      .querySelector('.monthview-selected') as HTMLTableCellElement;

    if (selected != null) {
      selected.parentElement.classList.add('selected-row');
    }

  }

  constructor(private calendarService: CalendarService) {
    this.focusOffset = Observable.empty();
    this.onTimeSelected.subscribe(x => this.markSelectedRow());
  }

  ngOnInit() {
    if (this.dateFormatter && this.dateFormatter.formatMonthViewDay) {
      this.formatDayLabel = this.dateFormatter.formatMonthViewDay;
    } else {
      var dayLabelDatePipe = new DatePipe('en-US');
      this.formatDayLabel = function(date: Date) {
        return dayLabelDatePipe.transform(date, this.formatDay);
      };
    }

    if (this.dateFormatter && this.dateFormatter.formatMonthViewDayHeader) {
      this.formatDayHeaderLabel = this.dateFormatter.formatMonthViewDayHeader;
    } else {
      var datePipe = new DatePipe(this.locale);
      this.formatDayHeaderLabel = function(date: Date) {
        return datePipe.transform(date, this.formatDayHeader);
      };
    }

    if (this.dateFormatter && this.dateFormatter.formatMonthViewTitle) {
      this.formatTitle = this.dateFormatter.formatMonthViewTitle;
    } else {
      var datePipe = new DatePipe(this.locale);
      this.formatTitle = function(date: Date) {
        return datePipe.transform(date, this.formatMonthTitle);
      };
    }

    if (this.lockSwipeToPrev) {
      this.slider.lockSwipeToPrev(true);
    }

    if (this.lockSwipes) {
      this.slider.lockSwipes(true);
    }

    this.refreshView();
    this.inited = true;

    this.currentDateChangedFromParentSubscription = this.calendarService.currentDateChangedFromParent$.subscribe(currentDate => {
      this.refreshView();
    });

    this.eventSourceChangedSubscription = this.calendarService.eventSourceChanged$.subscribe(() => {
      this.onDataLoaded();
    });
  }

  ngOnDestroy() {
    if (this.currentDateChangedFromParentSubscription) {
      this.currentDateChangedFromParentSubscription.unsubscribe();
      this.currentDateChangedFromParentSubscription = null;
    }

    if (this.eventSourceChangedSubscription) {
      this.eventSourceChangedSubscription.unsubscribe();
      this.eventSourceChangedSubscription = null;
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (!this.inited) return;

    let eventSourceChange = changes['eventSource'];
    if (eventSourceChange && eventSourceChange.currentValue) {
      this.onDataLoaded();
    }

    let lockSwipeToPrev = changes['lockSwipeToPrev'];
    if (lockSwipeToPrev) {
      this.slider.lockSwipeToPrev(lockSwipeToPrev.currentValue);
    }

    let lockSwipes = changes['lockSwipes'];
    if (lockSwipes) {
      this.slider.lockSwipes(lockSwipes.currentValue);
    }

    if ('focusOffset' in changes) {
      if (typeof this.focusOffset !== 'undefined') {
        this.focusOffset = changes['focusOffset'].currentValue;
        const parent = (document.querySelector('.swiper-container') as HTMLDivElement);
        const parentHeight = parent.offsetHeight;

        const getSelectedRow$ = (offset: number) => Observable
          .of(document.querySelector('.selected-row') as HTMLTableRowElement)
          .filter(row => row != null)
          .map(elementRef => {
            /**
             * Selected row index
             * @type {number}
             */
            const rowIndex = Array.prototype.slice
              .call(elementRef.parentElement.children)
              .findIndex(row => row.classList.contains('selected-row'));
            return { offset, meta: { elementRef, rowIndex } };
          })
          .filter(({ meta: { rowIndex, elementRef } }) =>
            rowIndex >= 0 && rowIndex < elementRef.parentElement.children.length);

        this.focusOffset
          .filter(offset => offset > 0)
          .switchMap(getSelectedRow$)
          .subscribe(({ offset, meta }) => {
            const rowHeight = meta.elementRef.offsetHeight;
            const maxHeight = meta.elementRef.parentElement.offsetHeight;
            const firstRowOffset = (meta.elementRef.parentElement.parentElement.children
              .item(0).children
              .item(0) as HTMLTableRowElement).offsetHeight;
            const tableBody = meta.elementRef.parentElement as HTMLTableSectionElement;
            const heightTotal = parentHeight - rowHeight - firstRowOffset;

            const normalizedOffset = offset - firstRowOffset;
            const rowOffset = meta.rowIndex * rowHeight;
            const height = maxHeight - normalizedOffset;
            const heightRatio = (offset / heightTotal) * 100;
            const transform = rowOffset / 100 * heightRatio;

            if ( (height >= rowHeight + firstRowOffset)
              && (heightRatio > 0 && heightRatio <= 100)
              && (transform   > 0 && transform   <= rowOffset)
            ) {
              parent.style.height = height + 'px';
              tableBody.style.transform = `translateY(${ -transform }px)`;
              /*const data = [
                `o: ${ offset }`,                    // offset
                `oN: ${ normalizedOffset }`,         // normalized offset
                `h: ${ height }`,                    // height
                `hR: ${ heightRatio.toFixed(1) }%`,  // height ratio
                `t: ${ -transform.toFixed(1) }px`,   // transform
                `rO: ${ rowOffset }`,                // row offset
                `rH: ${ rowHeight }`,                // row height
                `rF: ${ firstRowOffset }`            // first row offset
              ];
              console.log(data.join(', '));*/
            }

          });
      }
    }
  }

  ngAfterViewInit() {
    let title = this.getTitle();
    this.onTitleChanged.emit(title);

    this.initWeekSelection();
    this.initMultiSelectMode();
  }

  initMultiSelectMode() {
    const flattenTouches$ = (event: TouchEvent) => {
      const touches: Array<Touch> = [].slice.call(event.changedTouches);
      return Observable.from(touches.map(touch => ({ event, touch })));
    }
    const mapTouches$ = (elem: NodeListOf<Element>, event: string) => Observable
      .fromEvent<TouchEvent>(elem, event)
      .flatMap(flattenTouches$);

    const cells = document.querySelectorAll('[data-clickable-target="true"]');

    const start$ = mapTouches$(cells, 'touchstart');
    const move$ = mapTouches$(cells, 'touchmove');
    const end$ = mapTouches$(cells, 'touchend');
    // Depends on current slide(listens cells only on the slide which was current during component initialization).
    // To fix it use delegate on document and filter by [data-clickable-target="true"] selector
    const longPress$ = start$
      .flatMap(startEvent => {
        const startTouchId = startEvent.touch.identifier;
        const moveOrEnd$ = Observable
          .merge(move$, end$)
          .filter(({ touch: {  identifier } }) => identifier === startTouchId);
        return Observable
          .timer(1000)
          .takeUntil(moveOrEnd$)
          .map(() => startEvent);
      });

    const click$ = Observable
      .fromEvent<MouseEvent>(document, 'click')
      .filter(event => (event.target as HTMLElement).dataset.clickableTarget === 'true');

    const multiSelect$ = click$
      .takeUntil(this.acceptSelectedRange)
      .map(event => event.target as HTMLElement)
      .do(elem => elem.classList.toggle('multiselect-col'))
      .buffer(this.acceptSelectedRange)
      .map(elements => elements
        .filter(elem => elem.classList.contains('multiselect-col'))
        .map(elem => {
          const { id, dataset: { clickableDate } } = elem;
          return { id, date: new Date(clickableDate) };
        }));
    const outerSubject$ = this.activateMultiSelect
      .filter(target => target === MultiSelectTarget.CustomRange)

    if (this.rangeSelectSubscription != null
      && this.rangeSelectSubscription instanceof Subscription
      && !this.rangeSelectSubscription.closed
    ) {
      this.rangeSelectSubscription.unsubscribe();
    }

    const multiSelectDelegate$ = new BehaviorSubject<MultiSelectEntries>([ ])
    const targetsDelegate$ = this.onSlideChange$
      .map(() => multiSelectDelegate$.getValue())
      .filter(values => values.length > 0)
      .do(values => values.forEach(entry => {
        const elem = document.getElementById(entry.id);

        if (elem != null && !elem.classList.contains('multiselect-col')) {
          elem.classList.add('multiselect-col');
        }
      }));

    /**
     * TODO:
     * - make delegate on document that filters table cells
     * - store selected selectors inside the observable chain
     */
    this.rangeSelectSubscription = Observable
      .merge(longPress$, outerSubject$)  // chain activators
      .do(() => this.onMultiSelectActive.emit(MultiSelectTarget.CustomRange))
      .switchMap(() => multiSelect$
        .takeUntil(this.onMultiSelect.asObservable()))
      .do(targets => multiSelectDelegate$.next(targets))
      .withLatestFrom(this.acceptSelectedRange)
      .map(([ range, state ]) => ({ range, state }))
      .subscribe(({ range, state }) => {
        this.clearMultiSelectRange();

        if (state.isAccepted && state.target === MultiSelectTarget.CustomRange) {
          this.onMultiSelect.emit({
            payload: range.map(item => item.date)
          });
        }
      });

    targetsDelegate$.subscribe();
  }

  initWeekSelection() {
    if (this.weekSelectSubscription != null
      && this.weekSelectSubscription instanceof Subscription
      && !this.weekSelectSubscription.closed
    ) {
      this.weekSelectSubscription.unsubscribe();
    }

    const cells = document.querySelectorAll('[data-clickable-target="true"]');
    const click$ = Observable.fromEvent<MouseEvent>(cells, 'click');
    const outerSubject$ = this.activateMultiSelect
      .filter(target => target === MultiSelectTarget.Week)
      .switchMap(() => click$.takeUntil(this.acceptSelectedRange));
    const doubleClick$ = click$
      .buffer(click$.debounceTime(250))
      .filter(events => events.length === 2)
      .map(events => events[0]);

    this.weekSelectSubscription = Observable
      .merge(doubleClick$, outerSubject$)
      .pluck('target', 'parentElement')
      .do((selectedRow: HTMLTableRowElement) => {
        this.clearWeekSelectRange();
        selectedRow.classList.add('selected-week');
      })
      .do(_ => this.onMultiSelectActive.emit(MultiSelectTarget.Week))
      .map((selectedRow: HTMLTableRowElement) => {
        const range: Array<Date> = [].slice
          .call(selectedRow.cells)
          .map((cell: HTMLTableCellElement) => new Date(cell.dataset.clickableDate));

        return {
          start: { date: range[0] },
          end: { date: range[range.length - 1] }
        };
      })
      .switchMap(
        selectedRow => this.acceptSelectedRange,
        (range, state) => ({ range, state }))
      .subscribe(({ range, state }) => {
        if (state.isAccepted && state.target === MultiSelectTarget.Week) {
          this.onWeekSelect.emit({
            start: range.start.date,
            end: range.end.date
          });
        }
        this.clearWeekSelectRange();
      });
  }

  htmlQuery<T extends HTMLElement>(selector: string) {
    const query = document.querySelectorAll(selector);
    const result: Array<T> = query.length > 0
      ? [].slice.call(query)
      : [];
    return result;
  }

  getMultiSelectRange(from: string, to: string) {
    /**
     * @returns
     *   0 - view(slide) index
     *   1st - row index
     *   2nd - weekday index
     */
    const parse = (token: string): Array<number> => token.split('-').map(x => Number(x));
    const range: Array<string> = [];

    const start = parse(from);
    const end = parse(to);

    for (let view = start[0]; view <= end[0]; view += 1) {
      for (let row = start[1]; row <= end[1]; row += 1) {
        const startCol = view === start[0] && row === start[1] ? start[2] : 0;
        for (let col = startCol; col < 7; col += 1) {
          range.push([view, row, col].join('-'));
          if (view === end[0] && row === end[1] && col === end[2]) {
            return range;
          }
        }
      }
    }
  }

  markMultiSelectRange(from: string, to: string) {
    const range = this.getMultiSelectRange(from, to);

    range.forEach(id => {
      const elem = document.getElementById(id);
      if (elem != null) {
        elem.classList.add('multiselect-col');
      }
    });
  }

  clearMultiSelectRange() {
    this.htmlQuery('.multiselect-col')
      .forEach(elem => elem.classList.remove('multiselect-col'));
  }

  clearWeekSelectRange() {
    this.htmlQuery<HTMLTableRowElement>('.selected-week')
      .forEach(row => row.classList.remove('selected-week'));
  }

  onSlideChanged() {
    if (this.callbackOnInit) {
      this.callbackOnInit = false;
      return;
    }

    let currentSlideIndex = this.slider.getActiveIndex(),
      direction = 0,
      currentViewIndex = this.currentViewIndex;

    currentSlideIndex = (currentSlideIndex + 2) % 3;
    if (currentSlideIndex - currentViewIndex === 1) {
      direction = 1;
    } else if (currentSlideIndex === 0 && currentViewIndex === 2) {
      direction = 1;
      this.slider.slideTo(1, 0, false);
    } else if (currentViewIndex - currentSlideIndex === 1) {
      direction = -1;
    } else if (currentSlideIndex === 2 && currentViewIndex === 0) {
      direction = -1;
      this.slider.slideTo(3, 0, false);
    }
    this.currentViewIndex = currentSlideIndex;
    this.move(direction);
  }

  move(direction: number) {
    if (direction === 0) return;

    this.direction = direction;
    if (!this.moveOnSelected) {
      let adjacentDate = this.calendarService.getAdjacentCalendarDate(this.mode, direction);
      this.calendarService.setCurrentDate(adjacentDate);
    }
    this.refreshView();
    this.direction = 0;
    this.moveOnSelected = false;
  }

  createDateObject(date: Date): IMonthViewRow {
    var disabled = false;
    if (this.markDisabled) {
      disabled = this.markDisabled(date);
    }

    return {
      date: date,
      events: [],
      label: this.formatDayLabel(date),
      secondary: false,
      disabled: disabled
    };
  }

  static getDates(startDate: Date, n: number): Date[] {
    let dates = new Array(n),
      current = new Date(startDate.getTime()),
      i = 0;
    current.setHours(12); // Prevent repeated dates because of timezone bug
    while (i < n) {
      dates[i++] = new Date(current.getTime());
      current.setDate(current.getDate() + 1);
    }
    return dates;
  }

  getViewData(startTime: Date): IMonthView {
    let startDate = startTime,
      date = startDate.getDate(),
      month = (startDate.getMonth() + (date !== 1 ? 1 : 0)) % 12;

    let dates = MonthViewComponent.getDates(startDate, 42);
    let days: IMonthViewRow[] = [];
    for (let i = 0; i < 42; i++) {
      let dateObject = this.createDateObject(dates[i]);
      dateObject.secondary = dates[i].getMonth() !== month;
      days[i] = dateObject;
    }

    let dayHeaders: string[] = [];
    for (let i = 0; i < 7; i++) {
      dayHeaders.push(this.formatDayHeaderLabel(days[i].date));
    }
    return {
      dates: days,
      dayHeaders: dayHeaders
    };
  }

  getHighlightClass(date: IMonthViewRow): string {
    let className = '';

    if (date.hasEvent) {
      if (date.secondary) {
        className = 'monthview-secondary-with-event';
      } else {
        className = 'monthview-primary-with-event';
      }
    }

    if (date.selected) {
      if (className) {
        className += ' ';
      }
      className += 'monthview-selected';
    }

    if (date.current) {
      if (className) {
        className += ' ';
      }
      className += 'monthview-current';
    }

    if (date.secondary) {
      if (className) {
        className += ' ';
      }
      className += 'text-muted';
    }

    if (date.disabled) {
      if (className) {
        className += ' ';
      }
      className += 'monthview-disabled';
    }
    return className;
  }

  getRange(currentDate: Date): IRange {
    let year = currentDate.getFullYear(),
      month = currentDate.getMonth(),
      firstDayOfMonth = new Date(year, month, 1),
      difference = this.startingDayMonth - firstDayOfMonth.getDay(),
      numDisplayedFromPreviousMonth = (difference > 0) ? 7 - difference : -difference,
      startDate = new Date(firstDayOfMonth.getTime());

    if (numDisplayedFromPreviousMonth > 0) {
      startDate.setDate(-numDisplayedFromPreviousMonth + 1);
    }

    let endDate = new Date(startDate.getTime());
    endDate.setDate(endDate.getDate() + 42);

    return {
      startTime: startDate,
      endTime: endDate
    };
  }

  onDataLoaded() {
    let range = this.range,
      eventSource = this.eventSource,
      len = eventSource ? eventSource.length : 0,
      startTime = range.startTime,
      endTime = range.endTime,
      utcStartTime = new Date(Date.UTC(startTime.getFullYear(), startTime.getMonth(), startTime.getDate())),
      utcEndTime = new Date(Date.UTC(endTime.getFullYear(), endTime.getMonth(), endTime.getDate())),
      currentViewIndex = this.currentViewIndex,
      dates = this.views[currentViewIndex].dates,
      oneDay = 86400000,
      eps = 0.0006;

    for (let r = 0; r < 42; r += 1) {
      if (dates[r].hasEvent) {
        dates[r].hasEvent = false;
        dates[r].events = [];
      }
    }

    for (let i = 0; i < len; i += 1) {
      let event = eventSource[i],
        eventStartTime = new Date(event.startTime.getTime()),
        eventEndTime = new Date(event.endTime.getTime()),
        st: Date,
        et: Date;

      if (event.allDay) {
        if (eventEndTime <= utcStartTime || eventStartTime >= utcEndTime) {
          continue;
        } else {
          st = utcStartTime;
          et = utcEndTime;
        }
      } else {
        if (eventEndTime <= startTime || eventStartTime >= endTime) {
          continue;
        } else {
          st = startTime;
          et = endTime;
        }
      }

      let timeDiff: number;
      let timeDifferenceStart: number;
      if (eventStartTime <= st) {
        timeDifferenceStart = 0;
      } else {
        timeDiff = eventStartTime.getTime() - st.getTime();
        if (!event.allDay) {
          timeDiff = timeDiff - (eventStartTime.getTimezoneOffset() - st.getTimezoneOffset()) * 60000;
        }
        timeDifferenceStart = timeDiff / oneDay;
      }

      let timeDifferenceEnd: number;
      if (eventEndTime >= et) {
        timeDiff = et.getTime() - st.getTime();
        if (!event.allDay) {
          timeDiff = timeDiff - (et.getTimezoneOffset() - st.getTimezoneOffset()) * 60000;
        }
        timeDifferenceEnd = timeDiff / oneDay;
      } else {
        timeDiff = eventEndTime.getTime() - st.getTime();
        if (!event.allDay) {
          timeDiff = timeDiff - (eventEndTime.getTimezoneOffset() - st.getTimezoneOffset()) * 60000;
        }
        timeDifferenceEnd = timeDiff / oneDay;
      }

      let index = Math.floor(timeDifferenceStart);
      while (index < timeDifferenceEnd - eps) {
        dates[index].hasEvent = true;
        let eventSet = dates[index].events;
        if (eventSet) {
          eventSet.push(event);
        } else {
          eventSet = [];
          eventSet.push(event);
          dates[index].events = eventSet;
        }
        index += 1;
      }
    }

    for (let r = 0; r < 42; r += 1) {
      if (dates[r].hasEvent) {
        dates[r].events.sort(this.compareEvent);
      }
    }

    if (this.autoSelect) {
      let findSelected = false;
      for (let r = 0; r < 42; r += 1) {
        if (dates[r].selected) {
          this.selectedDate = dates[r];
          findSelected = true;
          break;
        }
      }

      if (findSelected) {
        this.onTimeSelected.emit({
          selectedTime: this.selectedDate.date,
          events: this.selectedDate.events,
          disabled: this.selectedDate.disabled
        });
      }
    }
  };

  refreshView() {
    this.range = this.getRange(this.calendarService.currentDate);

    if (this.inited) {
      let title = this.getTitle();
      this.onTitleChanged.emit(title);
    }
    this.calendarService.populateAdjacentViews(this);
    this.updateCurrentView(this.range.startTime, this.views[this.currentViewIndex]);
    this.calendarService.rangeChanged(this);
    setTimeout(() => {
      this.markSelectedRow();
      this.initWeekSelection();
      this.onSlideChange$.emit();
      // this.initMultiSelectMode();
    }, 200);
  }

  getTitle(): string {
    let currentViewStartDate = this.range.startTime,
      date = currentViewStartDate.getDate(),
      month = (currentViewStartDate.getMonth() + (date !== 1 ? 1 : 0)) % 12,
      year = currentViewStartDate.getFullYear() + (date !== 1 && month === 0 ? 1 : 0),
      headerDate = new Date(year, month, 1);
    return this.formatTitle(headerDate);
  }

  private compareEvent(event1: IEvent, event2: IEvent): number {
    if (event1.allDay) {
      return 1;
    } else if (event2.allDay) {
      return -1;
    } else {
      return (event1.startTime.getTime() - event2.startTime.getTime());
    }
  }

  handleDayPress(event) {
    // this.onMultiSelectActive.emit();
  }

  select(event: MouseEvent, viewDate: IMonthViewRow) {
    if (!this.views) return;

    let selectedDate = viewDate.date,
      events = viewDate.events;

    if (isPast(selectedDate)) {
      console.log('[monthview:past-date-selected]');
      return;
    }

    const selectedWeekQuery = this.htmlQuery('.selected-week');
    if (selectedWeekQuery.length > 0) {
      selectedWeekQuery.forEach(row => row.classList.remove('selected-week'));
    }

    if (!viewDate.disabled) {
      let dates = this.views[this.currentViewIndex].dates,
        currentCalendarDate = this.calendarService.currentDate,
        currentMonth = currentCalendarDate.getMonth(),
        currentYear = currentCalendarDate.getFullYear(),
        selectedMonth = selectedDate.getMonth(),
        selectedYear = selectedDate.getFullYear(),
        direction = 0;

      (event.target as HTMLTableDataCellElement).parentElement;

      if (currentYear === selectedYear) {
        if (currentMonth !== selectedMonth) {
          direction = currentMonth < selectedMonth ? 1 : -1;
        }
      } else {
        direction = currentYear < selectedYear ? 1 : -1;
      }

      this.calendarService.setCurrentDate(selectedDate);
      if (direction === 0) {
        let currentViewStartDate = this.range.startTime,
          oneDay = 86400000,
          selectedDayDifference = Math.floor((selectedDate.getTime() - currentViewStartDate.getTime() - (selectedDate.getTimezoneOffset() - currentViewStartDate.getTimezoneOffset()) * 60000) / oneDay);

        for (let r = 0; r < 42; r += 1) {
          dates[r].selected = false;
        }

        if (selectedDayDifference >= 0 && selectedDayDifference < 42) {
          dates[selectedDayDifference].selected = true;
          this.selectedDate = dates[selectedDayDifference];
        }
      } else {
        this.moveOnSelected = true;
        this.slideView(direction);
      }
    }

    this.onTimeSelected.emit({ selectedTime: selectedDate, events: events, disabled: viewDate.disabled });
  }

  slideView(direction: number) {
    if (direction === 1) {
      this.slider.slideNext();
    } else if (direction === -1) {
      this.slider.slidePrev();
    }
  }

  updateCurrentView(currentViewStartDate: Date, view: IMonthView) {
    let currentCalendarDate = this.calendarService.currentDate,
      today = new Date(),
      oneDay = 86400000,
      selectedDayDifference = Math.floor((currentCalendarDate.getTime() - currentViewStartDate.getTime() - (currentCalendarDate.getTimezoneOffset() - currentViewStartDate.getTimezoneOffset()) * 60000) / oneDay),
      currentDayDifference = Math.floor((today.getTime() - currentViewStartDate.getTime() - (today.getTimezoneOffset() - currentViewStartDate.getTimezoneOffset()) * 60000) / oneDay);

    for (let r = 0; r < 42; r += 1) {
      view.dates[r].selected = false;
    }

    if (selectedDayDifference >= 0 && selectedDayDifference < 42 && !view.dates[selectedDayDifference].disabled && (this.autoSelect || this.moveOnSelected)) {
      view.dates[selectedDayDifference].selected = true;
      this.selectedDate = view.dates[selectedDayDifference];
    } else {
      this.selectedDate = {
        date: null,
        events: [],
        label: null,
        secondary: null,
        disabled: false
      };
    }

    if (currentDayDifference >= 0 && currentDayDifference < 42) {
      view.dates[currentDayDifference].current = true;
    }
  }

  eventSelected(event: IEvent) {
    this.onEventSelected.emit(event);
  }
}



