import { Component} from '@angular/core';

import {VojConfiguration} from "../../models/voj-configuration.model";
import {VojPageHeaderType} from "../../models/voj-page-header.model";
import {FormType} from "../../models/form-type.model";
import {Configuration} from "../../config/voj-configuration";
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'documents-page',
  templateUrl: 'documents.html',
})
export class DocumentsPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;

  constructor(public configurationService: Configuration,
              private translate: TranslateService) {
      this.config = this.configurationService.configuration;
      this.translate.get('DOCUMENTS').subscribe(
          translation => {
              this.headerData = {
                  title: translation.TITLE,
                  style: 'any',
                  actions: [{
                      id: 'xdcfgvyhubijnklbhvg',
                      label: 'help',
                      icon: 'help',
                      action: function () {
                          console.log('button works')
                      }
                  }],
                  isCanceled: true
              };
          });
      this.formData = {
          fields: [
              {
                  type: 'customUploadSelect2',
                  value: '',
                  label: 'Add file',
                  visible: true,
                  id: '',
                  name: 'documentsUploadSelect2'
              },
          ],
          submit: {
              id: 'xdcfgvyhubijnkfwlbhvg',
              label: 'Valider',
              icon: 'help',
              action: function () {
                  console.log('Navigation to J-S-CV-V1-01 Information Resume')
              }
          }
      }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DocumentsPage');
  }

}



