import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { SearchPage } from './search';
import {VojPageHeaderModule} from "../../../components/voj-page-header/voj-page-header.module";
import {VojSearchHeaderModule} from "../../../components/voj-search-header/voj-search-header.module";
import {VojCardModule} from "../../../components/voj-card/voj-card.module";
import {VojMapModule} from "../../../components/voj-map/voj-map.module";

@NgModule({
  declarations: [
    SearchPage,
  ],
  imports: [
    VojSearchHeaderModule,
    VojPageHeaderModule,
    VojCardModule,
    VojMapModule,
    IonicPageModule.forChild(SearchPage),
  ],
  exports: [
    SearchPage
  ]
})
export class SearchPageModule {}



