import {OnInit, Component, Input, ElementRef, Renderer, Output, EventEmitter} from '@angular/core';
import { IonicPage, Platform } from 'ionic-angular';
import {SearchHeaderData} from "../../models/search-header.model";
import {SearchQuery} from "../../models/search-query.model";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {Configuration} from "../../config/voj-configuration";

@IonicPage()
@Component({
	selector: 'voj-search-header',
	templateUrl: 'voj-search-header.html',
})
export class VojSearchHeader implements OnInit {
	platform: Platform;

	@Input('data') searchHeaderData : SearchHeaderData;
	@Input('criteria-mode') criteriaMode : boolean = false;
    @Output() searchType = new EventEmitter<boolean>();
	headerSegment: string = 'liste';
	isWeb: boolean = false;

	fullText : string;
	job : string;
	location : string;
	timeslot : string;
	name : string;
	searchModeLabel : string;
    config: VojConfiguration;


	constructor(public element: ElementRef, public renderer: Renderer, platform: Platform, private confService:Configuration) {
		this.platform = platform;
		this.isWeb = !this.platform.is('cordova');
        this.config = this.confService.configuration;
	}

	ngOnInit() {
        this.searchType.emit(this.criteriaMode);
		this.searchModeLabel = this.criteriaMode?"- Moins de critres":"+ Plus de critres";
	}

	/**
	 * trigger the search process, this proxy method will pass down the search arguments to
	 * the calling controller in order to perform the search
	 * @returns {any}
	 */
	triggerSearch() {
		let query = new SearchQuery();
		query.freeText = this.fullText;
		query.job = this.job;
		query.location = this.location;
		query.timeslot = this.timeslot;
		query.name = this.name;
		query.queryType = this.criteriaMode?'criteria':'full_text';
		return this.searchHeaderData.action(query);
	}

	/**
	 * Change search mode from full text search to criteria search
	 */
	toggleSearchMode() {
		if(this.criteriaMode && this.job) {
			this.fullText = this.job;
		}
		if(!this.criteriaMode && this.fullText) {
			this.job = this.fullText;
		}
		this.criteriaMode = !this.criteriaMode;
		this.searchModeLabel = this.criteriaMode?"- Moins de critres":"+ Plus de critres";
        this.searchType.emit(this.criteriaMode);
	}

    searchEvent(event: any) {
        if (event.keyCode === 13) {
        	this.triggerSearch();
            }
    }

}



