import {Injectable} from "@angular/core";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {VojTuple} from "../../models/voj-tuple.model";
import {EnterpriseDto} from "../../dtos/enterprise.dto";

@Injectable()
export class EnterpriseService{
	config: VojConfiguration;

	constructor(public http: HttpRequestProvider,
				public configuration: Configuration) {
		this.config = this.configuration.configuration;
	}

	/**
	 * Function to get companies list via siren or name
	 * @param {String} type : type can take only one of these values : siren or company
	 * @param {String} value : it is the value entered by the user
	 * @returns {Promise<any>}: a promise containing a list of EntrepriseDTO type == 'company', an EntrepriseDTO if type == 'siren', or a StatusResponseDTO if there is an error
	 */
	getCompanies(type: String, value: String) {
		let payload =
			{
				'service': "GET_CORPORAMA_DATA",
				'type': type,
				'value': value
			};
		let url = "";
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "entreprise") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}

	saveCompany(enterprise: EnterpriseDto, employerId: number){
		let payload =
			{
				'service': "SAVE_COMPANY",
				'employerId': employerId.toString(),
				'name': enterprise.name,
				'siret': enterprise.siret,
				'naf': enterprise.naf,
				'civility': enterprise.employer.identity.civility,
				'firstname': enterprise.employer.identity.firstName,
				'lastname': enterprise.employer.identity.lastName

			};
		let url = "";
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "entreprise") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}
}


