import {Component} from '@angular/core';
import {NavParams, Platform, ViewController} from 'ionic-angular';
import { ToastController } from 'ionic-angular';

import {Configuration} from '../../../config/voj-configuration';
import {FormType} from '../../../models/form-type.model';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {BankDTO} from "../../../dtos/bank.dto";
import {ProfileService} from "../../../providers/profile-service/profile-service";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {VojFormModel} from "../../../models/voj-form.model";
import {NavigationService} from "../../../services/navigation.service";
import {ObjectUtils} from "../../../utils/utils";
import {ToastService} from "../../../services/toast.service";
import {SearchPage} from "../../search-module/search/search";
import {VojPage} from "../../../models/voj-page";

@Component({
	selector: 'bank-settings-page',
	templateUrl: 'bank-settings.html',
})
export class BankSettingsPage{
	config: VojConfiguration;
	formData: VojFormModel;
	headerData: VojPageHeaderType;
	isInscription: boolean;
	currentUser: CurrentUserDto;
    bankNameField: string;
    ownerNameField: string;
    iBanField: string;
    bicField: string;

	constructor(public configurationService: Configuration,
							public platform: Platform,
							private navParams: NavParams,
							private storage: Storage,
							private profileService: ProfileService,
							private navigationService: NavigationService,
							private viewCtrl: ViewController,
                			public toastCtrl: ToastController,
                			public toast: ToastService) {
		this.config = this.configurationService.configuration;

		this.isInscription = this.navParams.data.isInscription;

		this.headerData = {
			title: 'BANK_SETTINGS.TITLE',
			style: 'any',
			actions: [{
				id: 'xdcfgvyhubijnklbhvg',
				label: 'help',
				icon: 'help',
				action: function () {
					console.log('button works')
				}
			}],
			isCanceled: true
		};

		this.formData = {
			fields: [
				{
					type: 'text',
					value: '',
					label: 'BANK_SETTINGS.BANK_NAME',
					readonly: false,
					visible: true,
					id: '',
					name: 'bankname',
					required: !this.isInscription
				},
				{
					type: 'text',
					label: 'BANK_SETTINGS.OWNER_NAME',
					readonly: true,
                    value: '',
                    visible: true,
					id: '',
					name: 'ownername',
					required: !this.isInscription
				},
				{
					type: 'text',
					label: 'BANK_SETTINGS.IBAN',
					readonly: false,
                    value: '',
					visible: true,
					id: '',
					name: 'iban',
					required: !this.isInscription
				},
				{
					type: 'text',
					label: 'BANK_SETTINGS.BIC',
					readonly: false,
                    value: '',
					visible: true,
					id: '',
					name: 'bic',
					required: !this.isInscription
				}
			],
			submit: {
				id: 'xdcfgvyhubijnkfwlbhvg',
				label: (this.isInscription ? 'BANK_SETTINGS.FOLLOWING_BTN' : 'BANK_SETTINGS.VALIDATE_BTN'),
				icon: 'help',
				action: (event) => {
					this.saveBankData();
				}
			}
		}
	}

	ngOnInit() {
        this.bankNameField = this.formData.fields[0].value;
        this.iBanField = this.formData.fields[2].value;
        this.bicField = this.formData.fields[3].value;
        this.storage.get("currentUser").then((val) => {
			this.currentUser = JSON.parse(val);
			if (!this.currentUser) {
				return;
			}
			this.formData.fields.filter(f => f.name === "ownername")[0].value = this.currentUser.identity.lastName.trim() + " " + this.currentUser.identity.middleName.trim() + " " + this.currentUser.identity.firstName.trim();
            this.ownerNameField = this.formData.fields[1].value;
            console.log('this.ownerNameField-->', this.ownerNameField);

            this.initializationPersonalDetails(this.currentUser.id);
        });
	}

    checkEditedFields() {
        if ((this.formData.fields[0].value !== this.bankNameField || this.formData.fields[2].value !== this.iBanField
				|| this.formData.fields[3].value !== this.bicField)
            && (this.bankNameField !== '' && this.iBanField !== '' && this.bicField !== '')) {
            return true;
        }
        return false
    }

    checkEnteredFields() {
        if (this.bankNameField === '' && this.iBanField === '' && (this.ownerNameField === '' || this.ownerNameField)  && this.bicField === '') {
            return true;
        }
        return false
    }

    initializationPersonalDetails(roleId) {
		this.profileService.getBankData(roleId).then((data) => {
			for (let key in data) {
				console.log(key);
				key === 'accountOwner' ? this.formData.fields[0].value = data[key] : null;
				key === 'bankName' ? this.formData.fields[1].value = data[key] : null;
				key === 'bic' ? this.formData.fields[2].value = data[key] : null;
				key === 'iban' ? this.formData.fields[3].value = data[key] : null;
			}
		});
	}

	saveBankData() {
        if (this.checkEditedFields())  {
            this.toast.presentEditingToast();
        }
        else if (this.checkEnteredFields()) {
            this.toast.presentEnteredToast();
        }
		//if all fields are empty, ignore this step and go to the next page
		let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep){
			this.navigationService.dismiss(this.viewCtrl);
			this.displaySuccessNotif();
			return;
		}

		let bankData: BankDTO = {
			id: null,
			bankName: this.formData.fields.filter(f => f.name === "bankname")[0].value,
			iban: this.formData.fields.filter(f => f.name === "iban")[0].value,
			bic: this.formData.fields.filter(f => f.name === "bic")[0].value,
			accountOwner: this.ownerNameField
		};

		this.profileService.saveBankSetting(this.currentUser.id, bankData).then((data: StatusResponseDto) => {
			if (data.status == "success") {
				if (this.isInscription) {
                    let vojNav: VojPage = { page: SearchPage, isRoot: true }
                    this.config.isLargeScreen ? this.navigationService.dismiss(this.viewCtrl) : this.navigationService.navigate('list', vojNav);
                    this.displaySuccessNotif();
				} else {
					//TODO
				}
			} else {
				console.log("une erreur est survenue lors de l'enregistrement des donnes");
				console.log(data.error);
			}
		});
	}

	displaySuccessNotif(){
        let toast = this.toastCtrl.create({
            message: 'Bienvenue sur Vit-On-Job, votre compte a bien t cr',
            duration: 5000,
			position: 'top'
        });
        toast.present();
	}
}



