import {Injectable} from "@angular/core";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {Configuration} from "../../config/voj-configuration";
import {VojTuple} from "../../models/voj-tuple.model";
import {HttpRequestProvider} from "../http-request/http-request";
import {BirthDataDTO} from "../../dtos/birth-data.dto";
import {IdentityCardDataDto} from "../../dtos/identity-card-data.dto";
import {AddressDto} from "../../dtos/address.dto";
import {BankDTO} from "../../dtos/bank.dto";
import {DatasetType} from '../../models/dataset-type';
import {MunicipalityDto} from '../../dtos/municipality.dto';
import {StringUtils} from "../../utils/utils";
import {IdentityDto} from "../../dtos/identity.dto";

@Injectable()
export class ProfileService{
	config: VojConfiguration;
	url: string;

	constructor(private configuration: Configuration,
							private http: HttpRequestProvider) {
		this.config = this.configuration.configuration;
		this.url = this.config.urls.filter(u => u.key == "profile")[0].value;
	}

	saveIdentity(identityDto: IdentityDto, roleId: number){
		let payload = {
			"service": "IDENTITY_SAVE",
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"civility": identityDto.civility,
			"lastName": identityDto.lastName,
			"middleName": identityDto.middleName,
			"firstName": identityDto.firstName,
			"roleId": roleId.toString()
		};
		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}

	getAccountData(accountId: number) {
		let payload = {
			"service": "GET_ACCOUNT_RESUME",
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"accountId": accountId.toString()
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "profile") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}

    getPersonalDetails(jobyerId: number) {
        let payload = {
            "service": "GET_PERSONAL_DETAILS",
            "jobyerId": jobyerId.toString()
        };

        let url;
        for (let i = 0; i < this.config.urls.length; i++) {
            let t: VojTuple = this.config.urls[i];
            if (t.key == "profile") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data: any) => {
                resolve(data);
            });
        });
	}

    getPersonalIdentityDetails(jobyerId: number) {
        let payload = {
            "service": "GET_PERSONAL_DETAILS_IDENTITY",
            "jobyerId": jobyerId.toString()
        };

        let url;
        for (let i = 0; i < this.config.urls.length; i++) {
            let t: VojTuple = this.config.urls[i];
            if (t.key == "profile") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data: any) => {
                resolve(data);
            });
        });
    }

    getBankData(roleId: number) {
        let payload = {
            "service": "GET_BANK_DATA",
            "role": this.config.customer == "J" ? "jobyer" : "employeur",
			"roleId": roleId.toString()
        };

        let url;
        for (let i = 0; i < this.config.urls.length; i++) {
            let t: VojTuple = this.config.urls[i];
            if (t.key == "profile") {
                url = t.value;
                break;
            }
        }

        return new Promise(resolve => {
            this.http.call(payload, url, "", true).subscribe((data: any) => {
                resolve(data);
            });
        });
    }

	/***
	 * Method retuning a list of municipalities according to a given department
	 * @param {number} departmentId : the id of the department
	 * @returns {Promise<any>} list of the municipalities
	 */
	getMunicipalitiesByDepartment(departmentId: number) {
		let payload = {
			"service": "GET_MUNICIPALITIES_BY_DEPARTMENT",
			"departmentId": departmentId.toString()
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "profile") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: MunicipalityDto[]) => {
				resolve(data);
			});
		});
	}

	convertMunicipalitiesToDataset(municipalities: MunicipalityDto[]) {
		let dataset: Array<DatasetType> = [];
		for (let i = 0; i < municipalities.length; i++) {
			let ds = {
				id: Number(municipalities[i].id),
				key: municipalities[i].name.toString(),
				value: municipalities[i].id.toString()
			};
			dataset.push(ds);
		}
		return dataset;
	}

	savePersonalDetails(jobyerId: number, birthData: any, numSS: string) {
		let payload = {
			"service": "SAVE_PERSONAL_DETAILS",
			"jobyerId": jobyerId.toString(),
			"numSS": numSS,
			"birthDate": birthData.birthDate,
			"birthCountryId": birthData.country.toString(),
			"departmentId": birthData.department.toString(),
			"municipalityId": birthData.municipality.toString()
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "profile") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}

	savePersonalDetailsIdentity(jobyerId: number, identityCardData: any) {
		let payload = {
			"service": "SAVE_PERSONAL_DETAILS_IDENTITY",
			"jobyerId": jobyerId.toString(),
			"nationalityId": identityCardData.nationality,
			//"regionId": identityCardData.nationality.codeRegion,
			"typeIDCard": identityCardData.typeIDCard,
			"numberIDCard": identityCardData.numberIDCard,
			"deliveredDate": identityCardData.deliveredDate,
			"prefectureId": identityCardData.prefecture,
			"validFrom": identityCardData.validFrom,
			"validTo": identityCardData.validTo
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "profile") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}

	saveBankSetting(roleId: number, bankData: BankDTO) {
		let payload = {
			"service": "SAVE_BANK_DATA",
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"roleId": roleId.toString(),
			"bankDetailsId": "",
			"bankName": StringUtils.preventNull(bankData.bankName),
			"bic": StringUtils.preventNull(bankData.bic),
			"iban": StringUtils.preventNull(bankData.iban)
		};
		//let url = (this.config.urls.filter(tuple => tuple.key === "identity")).value;
		let url;
		for (let i = 0; i < this.config.urls.length; i++) {
			let t: VojTuple = this.config.urls[i];
			if (t.key == "profile") {
				url = t.value;
				break;
			}
		}

		return new Promise(resolve => {
			this.http.call(payload, url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}
}


