import { Component} from '@angular/core';
import { NavController } from 'ionic-angular';

import {Configuration} from '../../../config/voj-configuration';
import {FormType} from '../../../models/form-type.model';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import { InformationResumePage } from '../information-resume/information-resume';

@Component({
  selector: 'my-cv-page',
  templateUrl: 'my-cv.html',
})
export class MyCvPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;

  constructor(public configurationService: Configuration, public navCtrl: NavController) {
      this.config = this.configurationService.configuration;

      this.headerData = {
          title: 'MY_CV.MY_CV',
          style: 'any',
          actions: [ ],
          isCanceled: true
      };

      this.formData = {
          fields: [
              {
                  type: 'customUploadSelect',
                  value: '',
                  label: 'MY_CV.ADD_FILE',
                  visible: true,
                  id: '',
                  name: 'customMyCvSelect'
              },
          ],
          submit: {
              id: 'xdcfgvyhubijnkfwlbhvg',
              label: 'MY_CV.VALIDATE',
              icon: 'help',
              action: () => this.navCtrl.push(InformationResumePage)
          }
      }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MyCvPage');
  }

}



