import { Component} from '@angular/core';

import {Configuration} from '../../../config/voj-configuration';
import {FormType} from '../../../models/form-type.model';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import { NavigationService } from '../../../services/navigation.service';
import { InformationResumePage } from '../information-resume/information-resume';

@Component({
  selector: 'languages-page',
  templateUrl: 'languages.html',
})
export class LanguagesPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;

  constructor(
      public configurationService: Configuration,
      public navService: NavigationService
    ) {
      this.config = this.configurationService.configuration;

      this.headerData = {
          title: 'LANGUAGES.MY_LANGUAGES',
          style: 'any',
          actions: [{
              id: 'xdcfgvyhubijnklbhvg',
              label: 'help',
              icon: 'help',
              action: function () {
                  console.log('button works')
              }
          }],
          isCanceled: true
      };

      this.formData = {
          fields: [
              {
                  type: 'customTwoSelects',
                  value: 'select 1',
                  value2: 'select 2',
                  label: 'LANGUAGES.ADD_A_LANGUAGE',
                  dataset: [
                      {id: 4, key: 'example 1', value: 'example1'},
                      {id: 5, key: 'example 2', value: 'example2'},
                      {id: 6, key: 'example 3', value: 'example3'},
                      {id: 7, key: 'example 4', value: 'example4'}
                  ],
                  dataset2: [
                      {id: 8, key: 'test 1', value: 'test1'},
                      {id: 9, key: 'test 2', value: 'test2'},
                      {id: 10, key: 'test 3', value: 'test3'},
                  ],
                  required: true,
                  visible: true,
                  id: '',
                  name: 'customlanguagesSelect'
              },
          ],
          submit: {
              id: 'xdcfgvyhubijnkfwlbhvg',
              label: 'Validate',
              icon: 'help',
              action: () => this.navService.navigate('main', { page: InformationResumePage })
          }
      }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LanguagesPage');
  }

}



