import { NgModule, LOCALE_ID } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { DesktopCalendar } from './desktop-calendar';
import { CalendarModule, CalendarEventTitleFormatter } from 'angular-calendar';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CustomEventTitleFormatter } from './components/custom-title-formatter';
import { DemoUtilsModule } from "./components/demo-utils.module";
// import '../../../../node_modules/angular-calendar/dist/css/angular-calendar.css';

@NgModule({
	declarations: [
		DesktopCalendar
	],
	imports: [
		CommonModule,
		FormsModule,
		BrowserAnimationsModule,
		DemoUtilsModule,
		CalendarModule.forRoot(),
		NgbModalModule.forRoot(),
		IonicPageModule.forChild(DesktopCalendar)
	],
	exports: [
		DesktopCalendar
	],
	entryComponents: [],
	providers: [
		{ provide: LOCALE_ID, useValue: 'fr-FR' },
		{
			provide: CalendarEventTitleFormatter,
			useClass: CustomEventTitleFormatter
		}
	]
})
export class DesktopCalendarModule {
}


