import { Component, Input, OnInit } from '@angular/core';
import { IonicPage, Platform, Events } from 'ionic-angular';
import { Storage } from "@ionic/storage";
import {CurrentUserDto} from "../../dtos/current-user.dto";

@IonicPage()
@Component({
	selector: 'voj-header-web',
	templateUrl: 'voj-header-web.html',
})
export class VojHeaderWeb implements OnInit{
	@Input('data') data;
    showHeader: boolean = false;
    showUserInfo: boolean = false;
    profileInfo: {
        firstName: string;
        lastName: string;
        thumbnail: string;
    };

	constructor(public event: Events,
	            private platform: Platform,
                public storage: Storage) {
        platform.is('core') ? this.showHeader=true : this.showHeader=false;
		this.getProfileInfo();
	}

    getProfileInfo() {
        this.storage.get("currentUser").then((data: any)=> {

            if (data !== null) {
                let parseData = JSON.parse(data);

                this.showUserInfo = true;
                this.profileInfo = {
                    firstName: parseData.identity.firstName,
                    lastName: parseData.identity.lastName,
                    thumbnail: 'http://www.tii.org/public/plugins/organisations/organsiations/images/default_avatar_male.jpg'
                }
            }

        });
	}

    // update information when user update
    updateUserInfo() {
        this.event.subscribe('user:update', (userUpdate: CurrentUserDto) => {

        	this.profileInfo = {
                firstName: userUpdate.identity.firstName,
                lastName: userUpdate.identity.lastName,
                thumbnail: 'http://www.tii.org/public/plugins/organisations/organsiations/images/default_avatar_male.jpg'
            }

        });
    }

	ngOnInit(){
		this.updateUserInfo();
	}

}



