import { Component} from '@angular/core';


import {Configuration} from '../../../config/voj-configuration';
import {FormType} from '../../../models/form-type.model';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';

@Component({
  selector: 'qualities-page',
  templateUrl: 'qualities.html',
})
export class QualitiesPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;

  constructor(public configurationService: Configuration) {
      this.config = this.configurationService.configuration;

      this.headerData = {
          title: 'QUALITIES.MY_QUALITIES',
          style: 'any',
          actions: [{
              id: 'xdcfgvyhubijnklbhvg',
              label: 'help',
              icon: 'help',
              action: function () {
                  console.log('button works')
              }
          }],
          isCanceled: true
      };

      this.formData = {
          fields: [
              {
                  type: 'customSelect',
                  label: 'QUALITIES.ADD_QUALITY',
                  value: '',
                  dataset: [
                      {id: 4, key: 'example 1', value: 'example1'},
                      {id: 5, key: 'example 2', value: 'example2'},
                      {id: 6, key: 'example 3', value: 'example3'},
                      {id: 7, key: 'example 4', value: 'example4'},
                      {id: 41, key: 'example 5', value: 'example5'},
                      {id: 51, key: 'example 6', value: 'example6'},
                      {id: 61, key: 'example 7', value: 'example7'},
                      {id: 71, key: 'example 8', value: 'example8'},
                  ],
                  required: false,
                  visible: true,
                  id: "customQualitiesSelect",
                  name: 'customQualitiesSelect'
              },
          ],
          submit: {
              id: 'xdcfgvyhubijnkfwlbhvg',
              label: 'QUALITIES.VALIDATE',
              icon: 'help',
              action: function () {
                  console.log('Navigation to J-S-CV-V1-01 Information Resume')
              }
          }
      }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad QualitiesPage');
  }

}



