import {VojCardModel} from "./voj-card.model";

export type eBusType = {
    topic: string,
    data: any,
    condition: boolean }

export class Passenger {
    type: Map<string, eBusType> = new Map<string, eBusType>();

    constructor() {
        /**
         * Map events
         */
        this.type.set(
            'map',
            {
                topic: 'MAP_DETAILS',
                data: new Array<VojCardModel>(),
                condition: true
            }
        );

        /**
         * Others
         */
        this.type.set(
            'details',
            {
                topic: 'SHOW_DETAILS',
                data: VojCardModel,
                condition: true
            }
        );

    }

}



