import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';

import { AvailabilitiesAddDaysPage } from '../availabilities-add-days/availabilities-add-days';
import { AvailabilitiesAddSlotPage } from '../../availabilities-add-slot/availabilities-add-slot';
import { VojPageHeaderType } from '../../../models/voj-page-header.model';
import { VojFormModel } from '../../../models/voj-form.model';

// Availabilities 2/4
@IonicPage()
@Component({
	selector: 'jobyer-availability-choice-page',
	templateUrl: 'jobyer-availability-choice.html'
})
export class JobyerAvailabilityChoicePage {
    public formData: VojFormModel;
    public dataPageHeader: VojPageHeaderType;

	constructor(
        public navCtrl: NavController
	) {
		// Set page header title
		this.dataPageHeader = {
            title: 'JOBYER_AVAILABILITY_CHOICE.ADD_AVAILABILITY',
            titleLocalized: true,
            style: 'any',
            isCanceled: false,
            actions: [
                {
                    id : 'help',
                    icon : 'help',
                    action : () => { console.log('Click help button.') },
                    label : ''
                }
            ]
        };
        this.formData = {
            fields: [
                {
                    type: 'radio',
                    label: '',
                    id: '',
                    name: 'availability',
                    visible: true,
                    dataset: [
                        {
                            id: 1,
                            key: 'JOBYER_AVAILABILITY_CHOICE.TOTALLY',
                            value: 'totally'
                        },
                        {
                            id: 2,
                            key: 'JOBYER_AVAILABILITY_CHOICE.PARTIALL',
                            value: 'partial'
                        }
                    ]
                },
            ],
            submit: {
                label: 'JOBYER_AVAILABILITY_CHOICE.NEXT',
                id: '',
                action: () => {
                    const field = this.formData.fields.find(field => field.name === 'availability');
                    console.log('-----------------', field);
                    if (field.value) {
                        this.presentPage(field.value);
                    }
                }
            }
        }
    }

    presentPage(pageName) {
        switch (pageName) {
            case 'totally': return this.navCtrl.push(AvailabilitiesAddDaysPage);
            default: return this.navCtrl.push(AvailabilitiesAddSlotPage);
        }
	}

}



