import {
  Component,
  EventEmitter,
  Input,
  Output,
  ViewEncapsulation
} from '@angular/core';
import { IWeekViewHeaderItem, IWeekView } from '../calendar';

@Component({
  selector: 'weekview-header',
  templateUrl: 'weekview-header.html',
  encapsulation: ViewEncapsulation.None,
  styles: [
    `
      .table-weekview-header {
        box-shadow: 0 1px 10px rgba(0,0,0,0.2);
        position: relative;
        z-index: 2;
      }
      .table-weekview-header > thead > tr:first-child {
        font-size: 12px;
      }
      .table-weekview-header > thead > tr:last-child .calendar-hour-column-icon {
        background-image: url('/assets/delivery.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        display: inline-block;
        width: 75%;
        padding-top: 75%;
      }
      .table-weekview-header > thead > tr > th {
        padding: 0 !important;
        vertical-align: middle !important;
      }
      .table-weekview-header > thead > tr > th .day-button .button-inner {
        position: relative;
      }
      .table-weekview-header > thead > tr > th .has-events-dot {
        display: none;
        position: absolute;
        bottom: -15px;
      }
      .table-weekview-header > thead > tr > th.current .has-events-dot {
        bottom: -5px;
      }
      .table-weekview-header > thead > tr > th.has-events .has-events-dot {
        display: block;
      }
      .table-weekview-header > thead > tr > th.current {
        color: #488aff;
      }
      .table-weekview-header > thead > tr > th.current .day-button .button-inner {
        border: 1px solid #488aff;
        border-radius: 50%;
        background-color: #488aff;
        color: #fff;
        min-height: 42px;
      }
      .table-weekview-header > thead > tr > th.current .has-events-dot {
        color: #fff;
      }
      .table-weekview-header > thead > tr > th.weekend,
      .table-weekview-header > thead > tr > th.weekend .day-button {
        color: red;
      }
      .table-weekview-header > thead > tr > th .day-button {
        color: #222;
        height: auto;
        margin: 0;
        padding: 14px 0;
        width: 100%;
      }
      .table-weekview-header > thead > tr > th .day-button .button-inner {
        flex-direction: column;
        font-size: 2rem;
      }
      .table-weekview-header > thead > tr:first-child > th {
        padding: 0;
        border-bottom-width: 1px;
      }
      .table-weekview-header > thead > tr > th {
        vertical-align: top;
      }
    `
  ]
})
export class WeekViewHeaderComponent {
  @Input('data')
  public data: IWeekView;

  @Output('onSelect')
  public onSelect: EventEmitter<Date>;

  constructor() {
    this.onSelect = new EventEmitter<Date>(true);
  }

  public select(date: Date) {
    this.onSelect.emit(date);
  }
}



