import {AlertController, ModalController, ViewController, ToastController } from 'ionic-angular'
import {Injectable} from '@angular/core';

import {Configuration} from '../config/voj-configuration';
import {SignUpPage} from '../pages/account-module/sign-up/sign-up';
import {SignUpType} from '../models/sign-up-type.model';
import {Page} from "ionic-angular/navigation/nav-util";
import {VojPage} from "../models/voj-page";
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class ModalService{

	constructor(public configurationService: Configuration,
				public modalCtrl: ModalController,
                public alertCtrl: AlertController,
                public toastCtrl: ToastController,
                private translate: TranslateService) {
	}

	// header - optional parameter. If false - without header
	present(vojPage: VojPage, header?: boolean): void {
		console.log('header:', header);

			// Options for modal window
		let modalOptions = {
			cssClass: 'voj-modal',
			//enableBackdropDismiss: signUpData.backdropDismiss
			enableBackdropDismiss: true
		};

        // If user open modal from login or sign-up pages
        vojPage.params.closeConfirmation ? modalOptions.enableBackdropDismiss = false : modalOptions.enableBackdropDismiss = true;

		// Create modal window
		let modal = this.modalCtrl.create(vojPage.page, vojPage.params, modalOptions);

		// Show modal window
		modal.present().then(() => {
			// Call on mobile screen
			if (this.configurationService.configuration.isLargeScreen === true) {
				this.setModalHeight(header);
			}

            if (modalOptions.enableBackdropDismiss === false) {
                let confirmModal = document.getElementsByClassName('backdrop-no-tappable')[0];

                confirmModal.addEventListener('click', ()=> {

                    this.translate.get('MODAL_SERVICE').subscribe(
                        translation => {
                            let alert = this.alertCtrl.create({
                                message: translation.CLOSE_ALERT_TEXT,
                                buttons: [
                                    {
                                        text: translation.CANCEL_BTN,
                                        role: 'cancel',
                                        handler: () => {
                                            console.log('alert close');
                                        }
                                    },
                                    {
                                        text: translation.OK_BTN,
                                        handler: () => {
                                            this.closeConfirmationModal(modal);
                                        }
                                    }
                                ]
                            });

                            alert.present()

                        })

                })
            }

        });
	}

	closeConfirmationModal(page) {
        page.dismiss();
        this.translate.get('MODAL_SERVICE').subscribe(
            translation => {

                let toast = this.toastCtrl.create({
                    message: translation.CLOSE_TOAST_TEXT,
                    duration: 3000,
                    position: 'top'
                });

                toast.present();
            }
        )
	}

	setModalHeight(header): void {
		console.log('setModalHeight');
		// Content of page
		let vojContents = document.getElementsByClassName('voj-content');
		let modalContent = vojContents[vojContents.length - 1];
		// Modal window
		let modalWrapper = document.getElementsByClassName('modal-wrapper')[0];

		// Set height and show modal
		(modalWrapper as HTMLElement).style.display = 'block';
		let modalHeader = (header === false) ? 0 : 56;
		(modalWrapper as HTMLElement).style.height = modalContent.clientHeight + modalHeader + 'px';
	}

}



