import { Component } from '@angular/core';

import { VojFormModel } from '../../../models/voj-form.model';
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {AccountService} from "../../../providers/account-service/account-service";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {MasterdataService} from "../../../providers/masterdata-service/masterdata-service";
import {CountryDto} from "../../../dtos/country.dto";
import {Configuration} from "../../../config/voj-configuration";
import {AlertController, ViewController} from "ionic-angular";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";

@Component({
    selector: 'reset-password-page',
    templateUrl: 'reset-password.html',
})
export class ResetPasswordPage {

    isErrorShown: boolean;
    config: VojConfiguration;
    mailFormData: VojFormModel;
    phoneFormData: VojFormModel;
    radioFormData: VojFormModel;
    dataPageHeader: VojPageHeaderType;

    constructor(private accountService: AccountService,
				public configurationService: Configuration,
				private masterdataService: MasterdataService,
				private viewCtrl: ViewController,
				private alertCtrl: AlertController) {

        this.config = this.configurationService.configuration;

        this.dataPageHeader = {
            title: 'Mot de passe oubli',
            actions: [],
            style: 'any',
            isCanceled: true
        };

        // Set radio form data
        this.radioFormData = {
            title: '',
            fields: [
                {
                    type: 'radio',
                    value: 'sms',
                    label: '',
                    dataset: [
                        { id: 1, key: 'RESET_PASSWORD.SMS_BTN', value: 'sms' },
                        { id: 2, key: 'RESET_PASSWORD.MAIL_BTN', value: 'mail' }
                    ],
                    required: true,
                    visible: true,
                    id: '',
                    name: 'radioType'
                }
            ]
        };

        // Phone
        this.phoneFormData = {
            title: '',
            fields: [
                // Phone code
                {
                    type: 'select',
                    value: null,
                    label: 'FORM.COUNTRY_CODE',
                    dataset: [],
                    required: true,
                    visible: true,
                    id: 'phone-code-field',
                    name: 'code'
                },
                {
                    //placeholder: 'PLACEHOLDER.ENTER_PHONE',
                    type: 'tel',
                    value: '',
                    label: 'FORM.PHONE',
                    required: true,
                    readonly: false,
                    visible: true,
                    id: 'phone-field',
                    min: 9,
                    max: 10,
                    regex: '^(06|07)[0-9]*$',
                    name: 'userPhone',
                    validationMsg: 'VALIDATION.PHONE_VALIDATION_MSG',
                    action: () => {
                        // Hide error
                        this.hideError();
                    }
                }
            ],
            submit: {
                id: '',
                icon: '',
							label: 'RESET_PASSWORD.VALIDATE_BTN',
                action: () => {
                    console.log('Check user phone.');
                    //this.checkUserData('telephone number');
									let code = this.phoneFormData.fields.filter(f => f.name === "code" )[0].value;
									let phone = this.phoneFormData.fields.filter(f => f.name === "userPhone" )[0].value;
									let phoneWithCode = "+" + code + phone.substr(phone.length - 9);
									this.accountService.sendPassword(phoneWithCode,"").then((data: StatusResponseDto) => {
										if(data.status == "success"){
											this.viewCtrl.dismiss().then(() => {
												this.presentConfirmation("sms");
											});
										}else{
											this.presentError(data.code, "sms");										}
									});
                }
            }
        };

        // Mail
        this.mailFormData = {
            title: '',
            fields: [
                {
                    //placeholder: 'PLACEHOLDER.ENTER_MAIL',
                    type: 'mail',
                    value:'',
                    label: 'FORM.EMAIL',
                    required: true,
                    readonly: false,
                    visible: true,
                    name: 'userEmail',
                    id: 'user-mail',
                    min: 5,
                    validationMsg: 'VALIDATION.EMAIL_VALIDATION_MSG',
                    regex: '^(([^<>()\\[\\]\\\\.,;:\\s@"]+(\\.[^<>()\\[\\]\\\\.,;:\\s@"]+)*)|(".+"))@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}])|(([a-zA-Z\\-0-9]+\\.)+[a-zA-Z]{2,}))$',
                    action: () => {
                        // Hide error
                        this.hideError();
                    }
                }
            ],
            submit: {
                id: '',
                icon: '',
                label: 'RESET_PASSWORD.VALIDATE_BTN',
                action: () => {
                    console.log('Check user email.');
                    //this.checkUserData('e-mail address');
										this.accountService.sendPassword("", this.mailFormData.fields[0].value).then((data: StatusResponseDto) => {
											if(data.status == "success"){
												this.viewCtrl.dismiss().then(() => {
													this.presentConfirmation("mail");
												});
											}else{
												this.presentError(data.code, "mail");
											}
										});
                }
            }
        };

        // Error init
        this.isErrorShown = false;
    }

	ngOnInit(){
		//load phone code list
		this.masterdataService.loadCountriesList().then((data: CountryDto[]) => {
			//set dataset of phone codes of the form
			this.phoneFormData.fields.filter(f => f.name === "code" )[0].dataset = this.masterdataService.convertCountriesListToDataset(data);
			//the default phone code is France code (33)
			this.phoneFormData.fields.filter(f => f.name === "code" )[0].value = "33";
		});
	}

	checkUserData(type) {
        // Hide error if shown
        if (this.isErrorShown === true) {
            this.hideError();
        }

        // Show error
        //this.presentError(type);
    }

    presentError(msg, type) {
        // Show only one error message
        if (this.isErrorShown === true) {
            return;
        }

        this.isErrorShown = true;

        let errorDiv = document.createElement('div');

        errorDiv.setAttribute('id','voj-error-msg');

        errorDiv.innerHTML = '<p>' + msg + '</p>';

        errorDiv.className = 'voj-validation-error-msg';

        let parentId = (type === 'sms' ? '2' : '3');

        document.getElementById(parentId).appendChild(errorDiv);
    }

    hideError() {
        // Hide error if it exists
        if (this.isErrorShown === false) {
            return;
        }
        if(document.getElementById('voj-error-msg')) {
					document.getElementById('voj-error-msg').remove();
				}
        this.isErrorShown = false;
    }

	presentConfirmation(method: string) {
		console.log('Show Present Confirmation modal.');

		let alert = this.alertCtrl.create({
			subTitle: (method == 'sms' ? 'RESET_PASSWORD.CONFIRMATION_MESSAGE' : 'RESET_PASSWORD.CONFIRMATION_MESSAGE'),
			cssClass: '',
			buttons: ['RESET_PASSWORD.VALIDATE_BTN']
		});
		alert.present();
	}

}



