import { Component, ElementRef, EventEmitter, Input } from '@angular/core';
import _ from "lodash";
import { AlertController, IonicPage, PopoverController } from 'ionic-angular';
import { PopoverPage } from './components/voj-popover.page';
import { CalendarType } from '../../../models/calendar.model';
declare let Hammer: any;

interface MonthSelectOption {
  label: string;
  value: any;
}

@IonicPage()
@Component({
    selector: 'smart-calendar',
    templateUrl: 'smart-calendar.html'
})
export class SmartCalendar {
    @Input('data')
    public data: CalendarType;

    elRef: ElementRef;
    popoverCtrl: PopoverController;

    calendarCollapse: boolean = false;
    calendar = {
        mode: 'month',
        currentDate: new Date()
    };
    viewTitle;
    eventSource;
    eventsSource: any[] = [];

    public currentDate$: EventEmitter<Date>;
    public currentDate: Date;
    public currentMonth: number;
    public parentOffsetHeight: number;
    public months: Array<MonthSelectOption>;

    public get selectPopoverOptions() {
        return {
            cssClass: 'calendar-header-select-popover'
        }
    }

    constructor(
        elRef: ElementRef,
        popoverCtrl: PopoverController,
        public alertCtrl: AlertController,
    ) {
        this.elRef = elRef;
        this.popoverCtrl = popoverCtrl;
        this.currentDate$ = new EventEmitter<Date>();
        this.parentOffsetHeight = 0;
        this.months = [
            { label: 'Janvier', value: 0 },
            { label: 'Fvrier', value: 1 },
            { label: 'Mars', value: 2 },
            { label: 'Avril', value: 3 },
            { label: 'Mai', value: 4 },
            { label: 'Juin', value: 5 },
            { label: 'Juillet', value: 6 },
            { label: 'Aout', value: 7 },
            { label: 'Septembr', value: 8 },
            { label: 'Octobre', value: 9 },
            { label: 'Novembre', value: 10 },
            { label: 'Dcembre', value: 11 }
        ];

        this.eventSource = [];
    }

    public handleMonthChange(event) {
        this.currentDate.setMonth(event);
        this.currentDate$.emit(this.currentDate);
    }

    public handleCurrentDateChanged($event) {
        this.currentDate = $event;
    }

    public handleMultiSelectRange($event) {
        this.alertCtrl.create({
            title: 'MultiSelect range',
            message: `<pre>${JSON.stringify($event, null, 2)}</pre>`,
            buttons: ['Ok']
        }).present()
    }

    public handleWeekSelect($event) {
        this.alertCtrl.create({
            title: 'Week range',
            message: `<pre>${JSON.stringify($event, null, 2)}</pre>`,
            buttons: ['Ok']
        }).present()   
    }

    public ngOnInit() {
        this.currentDate = new Date();
        this.currentMonth = this.currentDate.getMonth();
        this.eventSource = this.data.slots || [ ];
    }

    ngAfterViewInit() {
        this.parentOffsetHeight = (document
            .querySelector('.container-calendar') as HTMLDivElement)
            .offsetTop;
    }

    presentPopover(myEvent) {
        let popover = this.popoverCtrl.create(PopoverPage);
        popover.onDidDismiss(data => {
            if (data != null) {
                this.calendar.mode = data.mode;
            }
        });
        popover.present({
            ev: myEvent
        });
    }

    onViewTitleChanged(title) {
        this.viewTitle = title;
    }

    onEventSelected(event) {
        console.log(event);
        console.log('Event selected:' + event.startTime + '-' + event.endTime + ',' + event.title);
    }

    changeMode(mode) {
        this.calendar.mode = mode;
    }

    today() {
        this.calendar.currentDate = new Date();
    }

    onTimeSelected(ev) {
        // console.log(ev);
        if (this.calendar.mode === 'month') {
            return;
        }

        // let selectedTime: any = new Date(ev.selectedTime);
        let selected: any = Date.parse(ev.selectedTime);

        const event = _.find(ev.events, event => {
            let timeEvent = Date.parse(event['startTime']);
            return timeEvent == selected
        });

        console.log(event);
    }
}



