import {
  Component,
  EventEmitter,
  ElementRef,
  Input,
  OnInit,
  Output,
  TemplateRef,
  Inject,
  LOCALE_ID,
  ViewChild
} from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Toolbar, PopoverController } from 'ionic-angular';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/pairwise';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/concatMap';
import 'rxjs/add/operator/takeUntil';

import { CalendarService } from './calendar.service';
import { CalendarModePopoverPage } from './mode-popover/calendar-mode.popover';
import {
  CalendarMode,
  IDateFormatter,
  IDisplayAllDayEvent,
  IDisplayEvent,
  IEvent,
  IMonthViewDisplayEventTemplateContext,
  IMonthViewEventDetailTemplateContext,
  IRange,
  ITimeSelected,
  QueryMode,
  SelectOptions,
  Step
} from './calendar.models';
import {
  MultiSelect,
  MultiSelectActive,
  MultiSelectRange,
  MultiSelectTarget
} from '../models/shared';
export * from './calendar.models';

@Component({
  selector: 'calendar',
  templateUrl: `calendar.html`,
  styles: [
  `
    .calendar-wrapper,
    .weekview-container,
    .dayview-container {
      height: 100%;
    }

    .menu-toggle-wrapper {
      position: absolute;
      right: 4px;
      top: 50%;
      transform: translateY(-50%);
    }

    .event-detail-container {
      border-top: 2px darkgrey solid;
    }

    .no-events-label {
      font-weight: bold;
      color: darkgrey;
      text-align: center;
    }

    .event-detail {
      cursor: pointer;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .week-view-title {
      margin-top: 0;
    }

    .monthview-eventdetail-timecolumn {
      width: 110px;
      overflow: hidden;
    }

    .calendar-event-inner {
      overflow: hidden;
      background-color: rgba(150, 225, 194, .5);
      border-radius: 6px;
      color: white;
      height: 100%;
      width: 100%;
      padding: 8px 2px;
      line-height: 15px;
      font-weight: 700;
    }

    @media (max-width: 750px) {
      .calendar-event-inner {
        font-size: 24px;
      }
      weekview .calendar-event-inner {
        font-size: 14px;
      }
    }
  `,
  `
    .month-view-select-container {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .month-view-toolbar-title.title-ios {
      padding: 0 60px 1px;
    }
    .month-view-date-select {
      position: relative;
      // margin: auto;
      color: #fff;
      font-size: 1.8rem;
      font-weight: 500;
      vertical-align: middle;
    }

    /deep/ .month-view-date-select .select-icon {
      height: 2.3rem;
    }

    /deep/ .month-view-date-select .select-icon .select-icon-inner {
      color: #fff;
    }
    .month-view-date-select.select-ios {
      max-width: 90%;
    }
    .month-view-date-select .select-text {
      text-align: center;
    }
  `],
  providers: [CalendarService]
})
export class CalendarComponent implements OnInit {
  @Input()
  get currentDate(): Date {
    return this._currentDate;
  }

  set currentDate(val: Date) {
    if (!val) {
      val = new Date();
    }

    this._currentDate = val;
    this.calendarService.setCurrentDate(val, true);
    this.onCurrentDateChanged.emit(this._currentDate);
  }

  @Input('currentDateStream') currentDateStream: Observable<Date>;

  @Input() eventSource: IEvent[] = [];
  @Input() calendarMode: CalendarMode = 'month';
  @Input() formatDay: string = 'd';
  @Input() formatDayHeader: string = 'EEE';
  @Input() formatDayTitle: string = 'MMMM dd, yyyy';
  @Input() formatWeekTitle: string = 'MMMM yyyy, Week $n';
  @Input() formatMonthTitle: string = 'MMMM yyyy';
  @Input() formatWeekViewDayHeader: string = 'd';
  @Input() formatWeelViewWeekDayHeader: string = 'E';
  @Input() formatHourColumn: string = 'j';
  @Input() formatWeekViewHourColumn: string = 'H';
  @Input() showEventDetail: boolean = true;
  @Input() startingDayMonth: number = 0;
  @Input() startingDayWeek: number = 0;
  @Input() allDayLabel: string = 'all day';
  @Input() noEventsLabel: string = 'No Events';
  @Input() queryMode: QueryMode = 'local';
  @Input() step: Step = Step.Hour;
  @Input() autoSelect: boolean = true;
  @Input() markDisabled: (date: Date) => boolean;

  @Input() menuTemplate: TemplateRef<any>;
  @Input() monthviewDisplayEventTemplate: TemplateRef<IMonthViewDisplayEventTemplateContext>;
  @Input() monthviewInactiveDisplayEventTemplate: TemplateRef<IMonthViewDisplayEventTemplateContext>;
  @Input() monthviewEventDetailTemplate: TemplateRef<IMonthViewEventDetailTemplateContext>;
  @Input() weekviewAllDayEventTemplate: TemplateRef<IDisplayAllDayEvent>;
  @Input() weekviewNormalEventTemplate: TemplateRef<IDisplayEvent>;
  @Input() dayviewAllDayEventTemplate: TemplateRef<IDisplayAllDayEvent>;
  @Input() dayviewNormalEventTemplate: TemplateRef<IDisplayEvent>;

  @Input() dateFormatter: IDateFormatter;
  @Input() dir: string = "";
  @Input() scrollToHour: number = 0;
  @Input() preserveScrollPosition: boolean = false;
  @Input() lockSwipeToPrev: boolean = false;
  @Input() lockSwipes: boolean = false;
  @Input() parentOffsetHeight: number = 0;

  @Output() onCurrentDateChanged = new EventEmitter<Date>();
  @Output() onRangeChanged = new EventEmitter<IRange>();
  @Output() onEventSelected = new EventEmitter<IEvent>();
  @Output() onTimeSelected = new EventEmitter<ITimeSelected>();
  @Output() onTitleChanged = new EventEmitter<string>();
  @Output() onMultiSelect = new EventEmitter<MultiSelectRange>();
  @Output() onWeekSelect = new EventEmitter<MultiSelectRange>();

  @ViewChild('monthViewHost') monthViewHost: ElementRef;
  @ViewChild('scrollableWrapper') scrollableWrapper: ElementRef;
  @ViewChild('calendarToolbar') public toolbar

  public focusOffset$: Observable<number>;

  private _currentDate: Date;
  public currentMonth;
  public currentYear;
  public years: SelectOptions<number>;
  public months: SelectOptions<number>;
  private hourParts = 1;
  private currentDateChangedFromChildrenSubscription: Subscription;
  private currentDateStreamSubscription: Subscription;

  private multiSelectTarget = null;
  public multipleSelectMenuActive$ = new BehaviorSubject<boolean>(false);
  public acceptSelectedRange$ = new EventEmitter<MultiSelectActive>();
  public activateMultiSelect$ = new EventEmitter<MultiSelectTarget>();

  constructor(
    @Inject(LOCALE_ID) private locale: string,
    private elementRef: ElementRef,
    private calendarService: CalendarService,
    public popoverCtrl: PopoverController
  ) {
    this.parentOffsetHeight = 0;
    this.months = [
      { label: 'Janvier', value: 0 },
      { label: 'Fvrier', value: 1 },
      { label: 'Mars', value: 2 },
      { label: 'Avril', value: 3 },
      { label: 'Mai', value: 4 },
      { label: 'Juin', value: 5 },
      { label: 'Juillet', value: 6 },
      { label: 'Aout', value: 7 },
      { label: 'Septembr', value: 8 },
      { label: 'Octobre', value: 9 },
      { label: 'Novembre', value: 10 },
      { label: 'Dcembre', value: 11 }
    ];
    this.years = new Array(30)
      .fill(null)
      .map((x, i) => ({
        label: (2000 + i).toString(),
        value: 2000 + i
      }));
  }

  ngOnInit() {
    if (this.autoSelect) {
      if (this.autoSelect.toString() === 'false') {
        this.autoSelect = false;
      } else {
        this.autoSelect = true;
      }
    }

    this.hourParts = 60 / this.step;
    this.calendarService.queryMode = this.queryMode;
    this.currentDate = new Date();
    this.currentMonth = this.currentDate.getMonth();
    this.currentYear = this.currentDate.getFullYear();

    this.currentDateChangedFromChildrenSubscription = this.calendarService.currentDateChangedFromChildren$.subscribe(currentDate => {
      this._currentDate = currentDate;
      this.onCurrentDateChanged.emit(currentDate);
    });
  }

  ngAfterViewInit() {
    this.initMonthViewSwipe();
  }

  ngOnDestroy() {
    if (this.currentDateChangedFromChildrenSubscription) {
      this.currentDateChangedFromChildrenSubscription.unsubscribe();
      this.currentDateChangedFromChildrenSubscription = null;
    }
  }


  private initMonthViewSwipe() {
    const extractY = (event: TouchEvent) => event.touches[0].clientY;

    const wrapper: HTMLDivElement = this.scrollableWrapper.nativeElement;
    const height = wrapper.clientHeight;

    const touchStart$ = Observable.fromEvent<TouchEvent>(wrapper, 'touchstart');
    const touchMove$  = Observable.fromEvent<TouchEvent>(wrapper, 'touchmove');
    const touchEnd$   = Observable.fromEvent<TouchEvent>(wrapper, 'touchend');

    this.parentOffsetHeight = this.toolbar.nativeElement.offsetHeight;

    this.focusOffset$ = touchStart$
      .concatMap(start => touchMove$
        .pairwise<TouchEvent>()
        .map(events => events
          .map(extractY)
          .map(y => y - this.parentOffsetHeight))
        .map(([ a, b ]) => height - b)
        .takeUntil(touchEnd$));
  }

  /** Handler for the case when child component requests multi-select mode */
  public handleMultiSelect(from: MultiSelectTarget) {
    this.multipleSelectMenuActive$.next(true);
    this.multiSelectTarget = from;
  }

  /** Proxy output from child component to the parent */
  public handleMultiSelectRange(range: MultiSelectRange) {
    this.onMultiSelect.emit(range);
  }

  /** Proxy output from child component to the parent */
  public handleWeekSelectRange(range: MultiSelectRange) {
    this.onWeekSelect.emit(range);
  }

  public discardSelectedRange() {
    this.multipleSelectMenuActive$.next(false);
    this.acceptSelectedRange$.emit({ isAccepted: false });
  }

  public acceptSelectedRange() {
    this.multipleSelectMenuActive$.next(false);
    this.acceptSelectedRange$.emit({
      isAccepted: true,
      target: this.multiSelectTarget
    });
    this.multiSelectTarget = null;
  }

  public handleMonthChange(event) {
    const date = new Date(this.currentDate);
    date.setMonth(event);
    this.currentDate = date;
  }

  public handleYearChange(event) {
    const date = new Date(this.currentDate);
    date.setFullYear(event);
    this.currentDate = date;
  }

  public handleCurrentDateChanged($event) {
    this.calendarService.setCurrentDate($event);
  }

  public presentPopover(event) {
    const popover = this.popoverCtrl.create(CalendarModePopoverPage, {
      menuTemplate: this.menuTemplate
    });
    popover.present({
      ev: event
    });
    popover.onDidDismiss(data => {
      if (data != null) {
        switch(data.mode) {
          case 'month':
          case 'week':
          case 'day': {
            this.calendarMode = data.mode;
            if (this.calendarMode === 'month') {
              setTimeout(() => this.initMonthViewSwipe(), 200);
            }
            break;
          }
          case 'today': {
            return this.calendarService.setCurrentDate(new Date(Date.now()), true)
          };
          case 'weekSelect': {
            return this.activateMultiSelect$.emit(MultiSelectTarget.Week)
          }
          case 'multiSelect': {
            return this.activateMultiSelect$.emit(MultiSelectTarget.CustomRange)
          }
        }
      }
    });
  }

  rangeChanged(range: IRange) {
    this.onRangeChanged.emit(range);
  }

  eventSelected(event: IEvent) {
    this.onEventSelected.emit(event);
  }

  timeSelected(timeSelected: ITimeSelected) {
    this.onTimeSelected.emit(timeSelected);
  }

  titleChanged(title: string) {
    this.onTitleChanged.emit(title);
  }

  loadEvents() {
    this.calendarService.loadEvents();
  }

  handleMonthViewScroll(event) {
    console.log(event);
  }
}



