import {Strategy} from "./strategy";
import {MyApp} from "../app.component";

export class EmployerStrategy implements Strategy{

    loadHeaderData(app : MyApp,
                   notificationFunction : Function,
                   searchFunction : Function,
                   annonceFunction : Function,
                   missionFunction : Function,
                   accountFunction : Function){
        return {
            menu : [
                {
                    icon : 'search',
                    label : 'Recherche',
                    badge : 0,
                    action : ()=>{searchFunction(app)},
                    notificationFunction : notificationFunction
                },
                {
                    icon : 'search',
                    label : 'Annonces',
                    badge : -1,
                    action : ()=>{annonceFunction(app)}
                },
                {
                    icon : 'search',
                    label : 'Missions',
                    badge : -1,
                    action : ()=>{missionFunction(app)}
                },
                {
                    self: this,
                    icon : 'search',
                    label : 'Compte',
                    badge : -1,
                    action : ()=>{accountFunction(app)}
                }
            ],
            logo: 'assets/img/vitonjob.png',
            searchbar: {
                placeholder: 'string'
            },
            segments : [
                {
                    title:	{
                        icon : 'string',
                        label : '0'
                    },
                    subtitle: {
                        icon : 'string',
                        label : 'Contrats en attente'
                    },
                    action: function(){
                        console.log('works 1')
                    },
                    style : 'style2'
                },
                {
                    title:	{
                        icon : 'string',
                        label : '0'
                    },
                    subtitle: {
                        icon : 'string',
                        label : 'Pointages  valider'
                    },
                    action: function(){
                        console.log('works 2')
                    },
                    style : 'style2'
                },
                {
                    title:	{
                        icon : 'string',
                        label : '0'
                    },
                    subtitle: {
                        icon : 'string',
                        label : 'Missions  clturer'
                    },
                    action: function(){
                        console.log('works 3')
                    },
                    style : 'style2'
                }
            ]
        };
    }
}


