import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavParams, Platform, ViewController} from 'ionic-angular';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {MasterdataService} from "../../../providers/masterdata-service/masterdata-service";
import { ModalService } from '../../../services/modal.service';
import { PersonalDetailsIdentityPage } from '../personal-details-identity/personal-details-identity';
import { ProfileService } from '../../../providers/profile-service/profile-service';
import { DatasetType } from '../../../models/dataset-type';
import { CountryDto } from "../../../dtos/country.dto";
import { DepartmentDto } from '../../../dtos/department.dto';
import { MunicipalityDto } from '../../../dtos/municipality.dto';
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {VojFormModel} from "../../../models/voj-form.model";
import {Configuration} from "../../../config/voj-configuration";
import {NavigationService} from "../../../services/navigation.service";
import {BirthDataDTO} from "../../../dtos/birth-data.dto";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {Storage} from "@ionic/storage";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {VojPage} from "../../../models/voj-page";
import {ObjectUtils, StringUtils} from "../../../utils/utils";
import {ToastService} from "../../../services/toast.service";

@IonicPage()
@Component({
    selector: 'personal-details-page',
    templateUrl: 'personal-details.html',
})
export class PersonalDetailsPage {
    config: VojConfiguration;
    isInscription: boolean;
    headerData: VojPageHeaderType;
    formData: VojFormModel;
    foreignDepartmentList: Array<DatasetType>;
    franceDepartmentList: Array<DatasetType>;
    currentUser: CurrentUserDto;
    dateOfBirth: string;
    countryBirth: string;
    departmentOfBirth: string;
    placeOfBirth: string;
    socialSecurityNumber: string;

    constructor(
        private configuration: Configuration,
        public platform: Platform,
        private profileService: ProfileService,
        private masterdataService: MasterdataService,
				private navParams: NavParams,
				public viewCtrl: ViewController,
				private navigationService: NavigationService,
        private storage: Storage,
        public toast: ToastService
    ) {
        this.config = this.configuration.configuration;

			this.isInscription = this.navParams.data.isInscription;


        this.headerData = {
            title: 'Mes donnes sociales',
            style: 'any',
            isCanceled: true,
            actions: [{
                id: 'xdfgchvbjknm',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],

        };

        this.formData = {
            fields: [
                {
                    required: !this.isInscription,
                    type: 'date',
                    label: '',
                    value: null,
                    placeholder: 'Date de naissance',
                    visible: true,
                    disabled: false,
                    id: 'date-of-birth',
                    max: new Date(),
                    //validationMsg: 'Date of birth error',
                    name: 'dateOfBirth'
                },
                {
                    required: !this.isInscription,
                    type: 'select',
                    value: '',
                    label: 'Pays de naissance',
                    dataset: [],
                    visible: true,
                    //validationMsg: 'Country of birth error',
                    id: 'country-of-birth',
                    name: 'countryBirth',
                    action: () => {
                        this.setDepartmentList();
                    }
                },
                {
                    required: !this.isInscription,
                    type: 'select',
                    value: '',
                    label: 'Dpartement de naissance',
                    dataset: [],
                    readonly: false,
                    visible: true,
                    //validationMsg: 'Department of birth error',
                    id: 'department-of-birth',
                    name: 'departmentOfBirth',
                    action: () => {
                        this.changeDepartment();
                    }
                },
                {
                    required: !this.isInscription,
                    type: 'select',
                    value: '',
                    label: 'Lieu de naissance',
                    dataset: [],
                    readonly: false,
                    visible: true,
                    //validationMsg: 'Place of birth error',
                    id: 'place-of-birth',
                    name: 'placeOfBirth'
                },
                {
                    required: !this.isInscription,
                    type: 'text',
                    value: '',
                    label: 'Numro de scurit sociale',
                    readonly: false,
                    visible: true,
                    //validationMsg: 'Social security number error',
                    id: 'social-security-number',
                    name: 'socialSecurityNumber'
                }
            ],
            submit: {
							label: (this.isInscription ? 'Suivant' : 'Valider'),
                id: 'valid-btn',
                icon: 'no',
                action: () => {
                    this.savePersonalDetails();
                }
            }
        };

        this.formData.fields[0].max = this.setMaxBirthDay();

        // Initialization
        this.foreignDepartmentList = [
            {
                value: '99',
                key: 'Etranger',
                id: 99
            }
        ];

        this.franceDepartmentList = [];

			this.storage.get("currentUser").then((val) => {
				this.currentUser = JSON.parse(val);
                this.initializationPersonalDetails();
			});
    }

    checkEditedFields() {
        if ((this.formData.fields[0].value !== this.dateOfBirth || this.formData.fields[1].value !== this.countryBirth ||
                this.formData.fields[2].value !== this.departmentOfBirth || this.formData.fields[3].value !== this.placeOfBirth
            || this.formData.fields[4].value !== this.socialSecurityNumber)
            && (this.dateOfBirth !== null && this.countryBirth !== '' && this.departmentOfBirth !== '' && this.placeOfBirth !== ''
            && this.socialSecurityNumber !== '')) {
            return true;
        }
        return false
    }
    checkEnteredFields() {
        if (this.dateOfBirth === null && this.countryBirth === '' && this.departmentOfBirth === '' && this.placeOfBirth === ''
        && this.socialSecurityNumber === '') {
            return true;
        }
        return false
    }

	savePersonalDetails(){
        if (this.checkEditedFields())  {
            this.toast.presentEditingToast();
        }
        else if (this.checkEnteredFields()) {
            this.toast.presentEnteredToast();
        }
		//if all fields are empty, ignore this step and go to the next page
		let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep){
			this.gotoPersonalDetailsIdentity();
			return;
		}

		let countryPhoneCode = this.formData.fields.filter(f => f.name === "countryBirth")[0].value;
		let birthData: any;
    //if the user is french, save his department and municipality, else don't save them
		if(countryPhoneCode == "33"){
			birthData = {
				birthDate: StringUtils.preventNull(this.formData.fields.filter(f => f.name === "dateOfBirth")[0].value),
				country: this.formData.fields[1].dataset.filter(c => c.value === countryPhoneCode)[0].id,
				department: this.formData.fields.filter(f => f.name === "departmentOfBirth")[0].value,
				municipality: this.formData.fields.filter(f => f.name === "placeOfBirth")[0].value
			};
    }else{
			birthData = {
				birthDate: this.formData.fields.filter(f => f.name === "dateOfBirth")[0].value,
				country: this.formData.fields[1].dataset.filter(c => c.value === countryPhoneCode)[0].id,
				department: "",
				municipality: ""
			};
    }

		let numSS = this.formData.fields.filter(f => f.name === "socialSecurityNumber")[0].value
		this.profileService.savePersonalDetails(this.currentUser.id, birthData, numSS).then((data: StatusResponseDto) => {
			if (data.status == "success") {
				if (this.isInscription) {
					this.gotoPersonalDetailsIdentity();
				} else {
					//TODO: display a notification of success
				}
			} else {
				console.log("une erreur est survenue lors de l'enregistrement des donnes");
				console.log(data.error);
			}
		});
	}
    // Set data for form component
    initializationPersonalDetails() {
        this.profileService.getPersonalDetails(this.currentUser.id).then((data) => {
            let country = '';

            for (let key in data) {
                key === 'birthDate' ? this.formData.fields[0].value = new Date(data[key]) : null;
                key === 'numSS' ? this.formData.fields[4].value = data[key] : '';
                key === 'birthPlace' ? country = data[key].country.gid : '';
                key === 'birthPlace' ? this.formData.fields[2].value  = data[key].department.gid : '';
                key === 'birthPlace' ? this.formData.fields[3].value  = data[key].municipality.id : '';
            }

            for (let key in this.formData.fields[1].dataset) {
                if (this.formData.fields[1].dataset[key].id === Number(country)) {
                    this.formData.fields[1].value = this.formData.fields[1].dataset[key].value;
                }
            }

        });
    }

	gotoPersonalDetailsIdentity() {
    	this.navigationService.dismiss(this.viewCtrl);
        let vojPage: VojPage = {page: PersonalDetailsIdentityPage, isWebModal: true, params: {isInscription: this.isInscription}};
        this.navigationService.navigate("main", vojPage);
    }

    setDepartmentList() {
        if (this.franceDepartmentList.length === 0) {
            return;
        }

        let chosenCountry = this.formData.fields[1].value;

        // If France
        if (chosenCountry === '33') {
            this.formData.fields[2].readonly = false;
            this.formData.fields[3].readonly = false;

            this.formData.fields[2].dataset = this.franceDepartmentList;
            this.formData.fields[2].value = this.formData.fields[2].dataset[0].value; // Chosen by default

        }
        // If not France
        else if(chosenCountry !== '33') {
            this.formData.fields[2].readonly = true;
            this.formData.fields[3].readonly = true;

            this.formData.fields[2].dataset = this.foreignDepartmentList;
        }

        this.getMunicipalities(this.formData.fields[2].dataset[0].id);
    }

    setMaxBirthDay() {
        let [date, month, year] = [String(new Date().getDate()), String(new Date().getMonth()),
            String(new Date().getFullYear() - 18)];

        Number(date) < 10 ? date = '0' + date : date;
        Number(month) < 10 ? month = '0' + month : month;

        return new Date(`${year}-${month}-${date}`);
    }

    showWarningBirthDayMessage() {
        let warningDiv = document.createElement('div');
        warningDiv.setAttribute('id','voj-warning-msg');
        warningDiv.innerHTML = '<p>Vous devez avoir plus de 18 ans</p>';
        warningDiv.className = 'voj-warning-msg';

        let parentId = 'date-of-birth';
        document.getElementById(parentId).appendChild(warningDiv);
    }

    getMunicipalities(departmentId) {
        // Show loading spinner
        //let loading = this.loadingCtrl.create();
        //loading.present();

        this.profileService.getMunicipalitiesByDepartment(departmentId).then((data: MunicipalityDto[]) => {

            this.formData.fields[3].dataset = this.profileService.convertMunicipalitiesToDataset(data);

            // If France - first value by default
            if (this.formData.fields[1].value === '33') {
                this.formData.fields[3].value = this.formData.fields[3].dataset[0].value;
            }

            // Hide loading spinner
            //loading.dismiss();
        });
    }

    changeDepartment() {
        // Get Municipalities for Department
        for(let key in this.formData.fields[2].dataset) {
            if(this.formData.fields[2].dataset[key].value === this.formData.fields[2].value) {
                this.getMunicipalities(this.formData.fields[2].dataset[key].id);
            }
        }
    }

    ngOnInit() {
        this.dateOfBirth = this.formData.fields[0].value;
        this.countryBirth = this.formData.fields[1].value;
        this.departmentOfBirth = this.formData.fields[2].value;
        this.placeOfBirth = this.formData.fields[3].value;
        this.socialSecurityNumber = this.formData.fields[4].value;
        // Show loading spinner
        //let loading = this.loadingCtrl.create();
        //loading.present();

        this.masterdataService.loadCountriesList().then((data: CountryDto[]) => {
            //set dataset of nationalities of the form
            this.formData.fields.filter(f => f.name === "countryBirth" )[0].dataset = this.masterdataService.convertCountriesListToDataset(data);
            //the default nationality is "France"

            console.log('Countries list dataset:', this.formData.fields[1].dataset);

            // Get Department List
            this.masterdataService.loadDepartmentList().then((data: DepartmentDto[]) => {
                this.franceDepartmentList = this.masterdataService.convertDepartmentsListToDataset(data);

                // Set Department List
                this.setDepartmentList();

                // Hide loading spinner
                //loading.dismiss();
            });
        })
    }

    ionViewDidLoad() {
        this.showWarningBirthDayMessage();
    }
}



