import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { IonicPageModule } from 'ionic-angular';
import { VojCalendar } from './voj-calendar';
import { SmartCalendarModule } from './smart-calendar/smart-calendar.module';
import { DesktopCalendarModule } from './desktop-calendar/desktop-calendar.module';
import { FullCalendarComponentModule } from './full-calendar/full-calendar.module';

@NgModule({
	declarations: [
		VojCalendar
	],
	imports: [
		FormsModule,
		SmartCalendarModule,
		DesktopCalendarModule,
		FullCalendarComponentModule,
		IonicPageModule.forChild(VojCalendar)
	],
	exports: [
		VojCalendar
	],
	entryComponents: []
})
export class VojCalendarModule {
}


