import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { IonicPage } from 'ionic-angular';

@IonicPage()
@Component({
	selector: 'voj-header-segment',
	templateUrl: 'voj-header-segment.html',
})
export class VojHeaderSegment implements OnInit {
	@Input('data') data;
    @Input() activeTab: number;
    @Output() activeTabChange = new EventEmitter<number>();
    @Output() enterpriseSearchEvent = new EventEmitter<string>();

	constructor() {	}

    setTab(tabNam): void {
        this.callEnterpriseSearchPage();
        this.activeTab = tabNam;
        this.activeTabChange.emit(tabNam);
    }

    callEnterpriseSearchPage() {
        this.enterpriseSearchEvent.next();
    }

	ngOnInit() { }
}



