import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';

import { VojCard } from './voj-card';
import { VojMenuModule } from '../voj-menu/voj-menu.module';
import { VojSegmentModule } from '../voj-segment/voj-segment.module';
import { VojMapModule } from '../voj-map/voj-map.module';
import { VojPageHeaderModule } from '../voj-page-header/voj-page-header.module';

@NgModule({
	declarations: [
		VojCard,
	],
	imports: [
		VojMenuModule,
		VojSegmentModule,
		VojMapModule,
		VojPageHeaderModule,
		IonicPageModule.forChild(VojCard),
	],
	exports: [
		VojCard
	]
})
export class VojCardModule {
}



