import {Configuration} from "../../config/voj-configuration";
import {HttpRequestProvider} from "../http-request/http-request";
import {MissionServiceStrategy} from "./mission-service-strategy";
import {MissionServiceJobyer} from "./mission-service-jobyer";

export class MissionServiceFactory {
    constructor(private http: HttpRequestProvider,
                private config : Configuration) {

    }

    create() : MissionServiceStrategy {
        if(this.config.configuration.customer=='J')
            return new MissionServiceJobyer(this.http, this.config);
        return new MissionServiceJobyer(this.http, this.config);
    }
}


