import { Component, OnInit } from '@angular/core';
import { IonicPage, NavParams,  ViewController } from 'ionic-angular';
import { VojFormModel } from "../../models/voj-form.model";
import { DatasetType } from '../../models/dataset-type';

@IonicPage()
@Component({
	selector: 'voj-search-select',
	templateUrl: 'voj-search-select.html',
})
export class VojSearchSelect implements OnInit{
    searchSelectData: Array <DatasetType> = [];

    formData: VojFormModel;
    selectedItem: number;
    selectedCode: string;

	constructor(public params: NavParams,
                public viewCtrl: ViewController) {
        this.formData = {
            fields: [
                // Search text
                {
                    type: 'text',
                    value: '',
                    label: '',
                    required: true,
                    visible: true,
                    id: 'select-search',
                    name: 'selectSearch',
                    action: () => {
                        this.searchItem();
                    }
                }
            ]
        };

        // Search select initialization
        this.searchSelectInitialization();
	}

    cancelSelect() {
        let choosenData = {
            data: this.selectedCode
        };

        this.viewCtrl.dismiss(choosenData);
    }

    confirmSelect() {
	    let choosenData = {
	        data: this.selectedCode,
            action: 'confirm'
	    };

        this.viewCtrl.dismiss(choosenData);
    }

    searchItem() {
        if (this.searchSelectData.length) {
            let selectList = document.getElementsByClassName('select-item');
            let val = this.formData.fields[0].value.toLowerCase();

            for (let i = 0; i < selectList.length; i++) {
                if ((selectList[i] as HTMLElement).innerText.toLowerCase().indexOf(val) === -1) {
                    (selectList[i] as HTMLElement).style.display = 'none';
                }
                else {
                    (selectList[i] as HTMLElement).style.display = 'block';
                }
            }
        }
    }

    setSearchValue(selectedCoutry) {
        this.selectedCode = selectedCoutry.value;
    }

    searchSelectInitialization() {

        // Get data for the select options
        let selectData = this.params.get('dataset');
        this.searchSelectData = selectData.optionsList;

        // Set current value in search select
        for (let option in this.searchSelectData) {
            if (this.searchSelectData[option].value === selectData.currentValue) {
                this.selectedItem = this.searchSelectData[option].id;
                this.selectedCode = this.searchSelectData[option].value;
            }
        }
    }

	ngOnInit(){

	}

}



