import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { TranslateModule } from '@ngx-translate/core';

import { VojPageHeader } from './voj-page-header';

@NgModule({
	declarations: [
		VojPageHeader,
	],
	imports: [
		IonicPageModule.forChild(VojPageHeader),
        TranslateModule.forChild()
	],
	exports: [
		VojPageHeader
	]
})
export class VojPageHeaderModule {
}



