import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, Platform} from 'ionic-angular';
import {FieldType} from '../../models/field-type';
import {VojPageHeaderType} from '../../models/voj-page-header.model';


@IonicPage()
@Component({
    selector: 'page-options',
    templateUrl: 'options.html',
})
export class OptionsPage {
    isMobile: boolean;
    headerData: VojPageHeaderType;
    formData: {
        fields: Array<FieldType>;
    };

    constructor(public platform: Platform) {
        platform.is('core') ? this.isMobile = false : this.isMobile = true;
        this.headerData = {
            title: 'Options',
            style: 'any',
            isCanceled: true,
            actions: [{
                id: 'xdcfjhkbjvhbnkm',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }]
        };
        this.formData = {
            fields: [{
                type: 'text',
                label: null,
                value: 'Some text 1',
                readonly: false,
                visible: true,
                id: 'tbrf',
                name: 'Some text 1'
            },
                {
                    type: 'text',
                    value: 'Some text 2',
                    label: null,
                    readonly: false,
                    visible: true,
                    id: '',
                    name: 'Some text 2'
                },
                {
                    type: 'text',
                    value: 'Some text 3',
                    label: null,
                    readonly: false,
                    visible: true,
                    id: '',
                    name: 'Some text 3'
                },
                {
                    type: 'text',
                    value: 'Some text 4',
                    label: null,
                    readonly: false,
                    visible: true,
                    id: '',
                    name: 'Some text 4'
                },
                {
                    type: 'text',
                    value: 'Some text 5',
                    label: null,
                    readonly: false,
                    visible: true,
                    id: '',
                    name: 'Some text 5'
                }
            ]
        }
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad OptionsPage');
    }

}



