import { NgModule } from '@angular/core';
import { IonicModule } from 'ionic-angular';
import { OffsetModule } from 'ngx-offset';
import 'intl';
import 'intl/locale-data/jsonp/fr';
import { MonthViewComponent } from './monthview/monthview';
import { WeekViewComponent } from './weekview/weekview';
import { WeekViewHeaderComponent } from './weekview-header/weekview-header';
import { DayViewComponent } from './dayview/dayview';
import { CalendarComponent } from './calendar';
import { CalendarService } from './calendar.service';
import { initPositionScrollComponent } from './init-position-scroll';
import { CalendarModePopoverPage } from './mode-popover/calendar-mode.popover';
import { Pipes } from './pipes';

@NgModule({
  declarations: [
    MonthViewComponent,
    WeekViewComponent,
    WeekViewHeaderComponent,
    DayViewComponent,
    CalendarComponent,
    initPositionScrollComponent,
    CalendarModePopoverPage,
    Pipes,
  ],
  imports: [IonicModule,OffsetModule],
  exports: [CalendarComponent, Pipes],
  entryComponents: [CalendarComponent, CalendarModePopoverPage]
})
export class NgCalendarModule {
}



