import {SearchDTOStrategy} from "./search-dto-strategy";
import {ButtonType} from "../../../models/button-type.model";
import {TextType} from "../../../models/text-type";
import {SegmentType} from "../../../models/segment-type";
import {VojCardModel} from "../../../models/voj-card.model";
export class SearchDTOJobyer implements SearchDTOStrategy {
    generateCardButtons() : Array<ButtonType> {
        return [{
            id : 'recruite',
            icon:'',
            label:"Cette offre m'intresse",
            action:()=> {
                console.log('REC');
            },
            style : {'background-color' : '#eca613', 'color' : '#ffffff'}
        },{
            id : 'details',
            icon:'',
            label:'Dtails',
            action:()=> {
                console.log('DTL');
            },
            style : {'background-color' : '#D339F8', 'color' : '#ffffff'}
        }];
    }

    generateDetailedCardButtons() : Array<ButtonType>{
        return [{
            id : 'recruite',
            icon:'',
            label:"Cette offre m'intresse",
            action:()=> {
                console.log('REC');
            },
            style : {'background-color' : '#eca613', 'color' : '#ffffff'}
        }];
    }

    generateSubtitles(dataSource : any) : Array<TextType> {
        let subtitles : Array<TextType> = [];
        let st : TextType = new TextType();
        st.label = dataSource.restrictedAddress;
        st.rendered = true;
        st.icon = 'pin';
        st.id = 'restricted_adress';
        if(st.label && st.label.length>0)
            subtitles.push(st);

        st = new TextType();
        st.label = "<span class='card-rating'>&starf;&nbsp;&starf;&nbsp;&starf;&nbsp;&starf;&nbsp;&starf; (17 avis)</span>";
        st.rendered = false;
        st.icon = '';
        st.id = 'mission_rating';
        subtitles.push(st);

        st = new TextType();
        st.label = "Mission de "+ dataSource.result.days+"  compter du "+dataSource.result.start;
        st.rendered = true;
        st.icon = 'calendar';
        st.id = 'mission_calendar';
        subtitles.push(st);

        st = new TextType();
        st.label = "Taux horaire brut (/heure) : "+dataSource.result.hourly_wage.toLocaleString("fr-FR", {minimumFractionDigits: 2, maximumFractionDigits: 2});
        st.rendered = false;
        st.icon = 'cash';
        st.id = 'hourly_wage';
        subtitles.push(st);

        st = new TextType();
        st.label = "Nombre de jours : "+dataSource.result.days;
        st.rendered = false;
        st.icon = 'calendar';
        st.id = 'days';
        subtitles.push(st);

        st = new TextType();
        st.label = "Total () : "+dataSource.result.gains.toLocaleString("fr-FR", {minimumFractionDigits: 2, maximumFractionDigits: 2});
        st.rendered = false;
        st.icon = 'cash';
        st.id = 'total';
        subtitles.push(st);

        return subtitles;
    }

    generateSegments(dataSource : any, card : VojCardModel) : Array<SegmentType> {
        card.title.label = dataSource.jobs[0].label+((dataSource.jobs[0].level == 1)?' dbutant':' expert');

        let segments : Array<SegmentType> = [];
        let s : SegmentType = new SegmentType();
        s.title = {
            icon : '',
            label : dataSource.result.days.split(' ')[0]
        };
        s.subtitle = {
            icon : '',
            label : 'Jour(s)'
        };
        s.stateful = true;
        s.action = ()=> {
            card.segments[0].stateful = true;
            card.segments[1].stateful = false;
            card.segments[2].stateful = false;
            for(let i = 0 ; i < card.subtitles.length ; i++)
                if(card.subtitles[i].id === 'restricted_adress'
                    ||  card.subtitles[i].id === 'mission_calendar')
                    card.subtitles[i].rendered = true;
                else
                    card.subtitles[i].rendered = false;
        };
        segments.push(s);

        s = new SegmentType();
        s.title = {
            icon : '',
            label : '5'
        };
        s.subtitle = {
            icon : '',
            label : 'Etoile(s)'
        };
        s.action = ()=> {
            card.segments[0].stateful = false;
            card.segments[1].stateful = true;
            card.segments[2].stateful = false;
            for(let i = 0 ; i < card.subtitles.length ; i++)
                if(card.subtitles[i].id === 'mission_rating')
                    card.subtitles[i].rendered = true;
                else
                    card.subtitles[i].rendered = false;
        };
        segments.push(s);

        s = new SegmentType();
        s.title = {
            icon : '',
            label : dataSource.result.gains.toLocaleString("fr-FR", {minimumFractionDigits: 2, maximumFractionDigits: 2}) + ' '
        };
        s.subtitle = {
            icon : '',
            label : 'Gains estims'
        };
        s.action = ()=> {
            card.segments[0].stateful = false;
            card.segments[1].stateful = false;
            card.segments[2].stateful = true;
            for(let i = 0 ; i < card.subtitles.length ; i++)
                if(card.subtitles[i].id === 'hourly_wage'
                    ||  card.subtitles[i].id === 'days'
                    ||  card.subtitles[i].id === 'total')
                    card.subtitles[i].rendered = true;
                else
                    card.subtitles[i].rendered = false;
        };
        segments.push(s);

        return segments;
    }
}


