import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { VojPageHeaderType } from '../../../models/voj-page-header.model';
import { VojFormModel } from '../../../models/voj-form.model';
import { InformationResumePage } from '../information-resume/information-resume';
import { NavigationService } from '../../../services/navigation.service';

@IonicPage()
@Component({
  selector: 'page-keywords',
  templateUrl: 'keywords.html',
})
export class KeywordsPage {
  public headerData: VojPageHeaderType;
  public formData: VojFormModel;

  constructor(
    public navService: NavigationService
  ) {
    this.headerData = new VojPageHeaderType();
    this.headerData.title = 'KEYWORDS.MY_KEYWORDS';
    this.headerData.titleLocalized = true;

    this.formData = {
      submit: {
        id: 'submit',
        label: 'KEYWORDS.VALIDATE',
        labelLocalized: true,
        action: () => this.navService.navigate('main', {
          page: InformationResumePage
        })
      },
      fields: [
        {
          label: 'KEYWORDS.ADD_A_KEYWORD',
          labelLocalized: true,
          name: 'ajouter-un-mot-cl',
          id: 'ajouter-un-mot-cl',
          visible: true,
          type: 'customSelect',
          dataset: [
            {
              id: 4,
              key: 'KEYWORDS.AVAILABLE',
              value: 'Disponible'
            },
            {
              id: 5,
              key: 'KEYWORDS.TRACK_AGENT',
              value: 'Agent de piste'
            }
          ]
        }
      ]
    };
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad KeywordsPage');
  }

}



