import { Component } from '@angular/core';

@Component({
    selector: 'find-job-page',
    templateUrl: 'find-job.html',
})
export class FindJobPage {
    jobsList: Array<string>;

    constructor() {
        this.initializeJobs();
    }

    getJobs(ev: any) {
        // Reset array back to all of the items
        this.initializeJobs();

        // set val to the value of the searchbar
        let val = ev.target.value;

        // if the value is an empty string don't filter the items
        if (val && val.trim() != '') {
            this.jobsList = this.jobsList.filter((item) => {
                return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
            })
        }
    }

    initializeJobs() {
        this.jobsList = [
            'Dentist',
            'Nurse Practitioner',
            'Physician Assistant',
            'Statistician',
            'Computer Systems Analyst',
            'Software Developer'
        ]
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad FindJobPage');
    }

}



