import { Pipe, PipeTransform } from '@angular/core';

import { CalendarService } from '../calendar.service';
import { isEqual } from 'date-fns';

@Pipe({
  name: 'markCurrent'
})
export class MarkCurrentPipe implements PipeTransform {
  constructor(public calendarService: CalendarService) { }

  public transform(week, currentDate: Date) {
    return week.map(day => Object.assign({}, day, {
      isCurrent: day.date.toISOString().substring(0, 10) === this.calendarService.currentDate.toISOString().substring(0, 10)
    }));
  }
}



