import {Injectable} from "@angular/core";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {AddressDto} from "../../dtos/address.dto";

@Injectable()
export class AddressService{
	config: VojConfiguration;
	url: string;

	constructor(public http: HttpRequestProvider,
							public configuration: Configuration){
		this.config = this.configuration.configuration;
		this.url = this.config.urls.filter(u => u.key == "address")[0].value;
		}

	getAddress(roleId: number, type: string) {
		let payload = {
			"service": "ADDRESS_GET",
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"roleId": roleId.toString(),
			"type": type
		};

		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: any) => {
				resolve(data);
			});
		});
	}

	insertAddress(addressDto: AddressDto, roleId){
		let payload = {
			"service": "ADDRESS_INSERT",
			"zipCode": addressDto.zipCode.toString(),
			"number": addressDto.number,
			"name": addressDto.name,
			"city": addressDto.city,
			"street": addressDto.street,
			"type": addressDto.adressType,
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"id": roleId,
		};

		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: any) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}

	updateAddress(addressDto: AddressDto){
		let payload = {
			"service": "ADDRESS_UPDATE",
			"zipCode": addressDto.zipCode.toString(),
			"number": addressDto.number,
			"name": addressDto.name,
			"city": addressDto.city,
			"street": addressDto.street,
			"role": this.config.customer == "J" ? "jobyer" : "employeur",
			"id": addressDto.id,
		};

		return new Promise(resolve => {
			this.http.call(payload, this.url, "", true).subscribe((data: any) => {
				console.log(JSON.stringify(data));
				resolve(data);
			});
		});
	}
}


