import { Component, ViewChild } from '@angular/core';
import {NavController, App, ViewController, Events} from 'ionic-angular';
import { ContactPage } from "../contact/contact";
import { IonicPage } from 'ionic-angular';

import { JobyerType } from '../../../models/jobyer-type';
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {ButtonType} from "../../../models/button-type.model";

@IonicPage()
@Component({
	selector: 'page-about',
	templateUrl: 'about.html'
})
export class AboutPage {
	title: string;
	content: string;
	actionName: string;
	cards: any;
	listTitle: string;
	actions: any;
    footerData: {style: string, actions: Array<ButtonType>};

	gmapView: boolean = true;
    pageHeader: VojPageHeaderType;
    jobyerData: JobyerType;
	@ViewChild('contentMenu') nav;

	constructor(public navCtrl: NavController,
				public appCtrl: App,
				public viewCtrl: ViewController,
				private configuration: VojConfiguration,
				private event:Events) {
        this.footerData = {
            style:'',
            actions : [{
                id : '',
                label : '',
                icon : 'information-circle',
                action : ()=> {
                    console.log('test');
                }
            }

            ]
        };
        //  Page header
        this.pageHeader =  new VojPageHeaderType();
        this.pageHeader.title = "Golocatisation";
        this.pageHeader.actions = [
            {
                id: 'map',
                icon: 'map',
                action: ()=>{
                    if(!this.configuration.isLargeScreen) {
                        this.gmapView = !this.gmapView;
                        this.event.publish('MAP_DATA', this.cards);
                    }

                },
                label: ''
            },
            {
                id: 'help',
                icon: 'help',
                action: ()=>{
                    console.log("Help");
                },
                label: ''
            }
        ];

        // Testing JobyerStrategy Data
		this.jobyerData = {
			title: {
                label: 'Title'
            },
			content: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry\'s standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.',
			cover: 'http://www.turfgrass.ie/wp-content/uploads/2014/07/T-Projects-Lubker-1.jpg',
			thumbnail: 'http://diysolarpanelsv.com/images/clip-art-user-14.png',
			subtitles: [
                {

					icon: 'logo-angular',
                    label: 'Subtitle 1'
                },
                {
					icon: 'logo-angular',
                    label: 'Subtitle 2'
                }
			],
			actions: [
                {
					id:'',
					icon: 'build',
                    label: 'First action',
                    action: () => {
                        this.test()
                    }
                }
			],
			socialButton: [
                {
					id:'',
					icon: 'logo-googleplus',
                    label: 'Google+',
                    action: () => {
                        this.test()
                    }
                }
			],
			menu: [
                {
					id:'',
					icon: 'home',
                    label: 'Home',
                    action: () => {
                        this.test()
                    }
                }
			],
			badges: [
                {
					id:'',
					icon: 'star',
                    label: 'Star',
                    action: () => {
                        this.test()
                    }
                }
			],
			segments: [
                {
                    title: {
                        icon: 'star',
                        label: 'Title 1'
                    },
                    subtitle: {
                        icon: 'star',
                        label: 'Subtitle 1'
                    },
                    action: function(){
                        console.log('works 1')
                    },
                    style : 'style1'
                },
                {
                    title: {
                        icon: 'star',
                        label: 'Title 2'
                    },
                    subtitle: {
                        icon: 'star',
                        label: 'Subtitle 2'
                    },
                    action: function(){
                        console.log('works 2')
                    },
                    style : 'style1'
                }
			]
		};

		this.title = "un autre titre dynamique";
		this.content = "un autre texte de dtails..";
		this.actionName = "Action 2";

		this.listTitle = "Ma liste des cartes";

		this.actions = [{
			label: "test",
			action: () => {
				this.goToPage()
			}
		}, {
			label: "test2",
			action: () => {
				this.goToPage2()
			}
		}
		];


		this.cards = [{
			title: "JobyerStrategy 1",
			content: "contenu 1",
			actionName: "action 1"
		}, {
			title: "JobyerStrategy 2",
			content: "contenu 2",
			actionName: "action 2"
		}, {
			title: "JobyerStrategy 3",
			content: "contenu 3",
			actionName: "action 3"
		}, {
			title: "JobyerStrategy 4",
			content: "contenu 4",
			actionName: "action 4"
		}, {
			title: "JobyerStrategy 5",
			content: "contenu 5",
			actionName: "action 5"
		}]


	}

	goToPage() {
		//
		this.appCtrl.getRootNav().push(ContactPage);
		//this.viewCtrl.getNav().push(ContactPage);

		//this.navCtrl.push(ContactPage);
	}

	goToPage2() {
		//
		//this.appCtrl.getRootNav().push(ContactPage);
		//this.viewCtrl.getNav().push(ContactPage);

		this.navCtrl.push(ContactPage);
	}

    test() {
        console.log("works")
    }

}



