import { Component } from '@angular/core';
import {IonicPage, NavParams, ViewController} from 'ionic-angular';

import { VojPageHeaderType } from '../../../models/voj-page-header.model';
import { VojFormModel } from '../../../models/voj-form.model';
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {Configuration} from "../../../config/voj-configuration";
import {EnterpriseDto} from "../../../dtos/enterprise.dto";
import {Storage} from "@ionic/storage";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {ObjectUtils} from "../../../utils/utils";
import {EnterpriseService} from "../../../providers/enterprise-service/enterprise-service";
import {StatusResponseDto} from "../../../dtos/status-response.dto";

@IonicPage()
@Component({
    selector: 'company-page',
    templateUrl: 'company.html',
})
export class CompanyPage {
    isInscription: boolean;
    dataPageHeader: VojPageHeaderType;
    formData: VojFormModel;
    currentCompanyNumber: string;
    config: VojConfiguration;
    corporamaEnterprise: EnterpriseDto;
    currentUser: CurrentUserDto;
    enterprise: EnterpriseDto;

    constructor(private configurationService: Configuration,
                private navParams: NavParams,
                private storage: Storage,
                private enterpriseService: EnterpriseService,
                private viewCtrl: ViewController) {
		this.config = this.configurationService.configuration;
		this.isInscription = this.navParams.data.isInscription;
        this.enterprise = (!this.navParams.data.corporamaEnterprise ? new EnterpriseDto() : this.navParams.data.corporamaEnterprise);

		// Header
        this.dataPageHeader = {
            title: 'COMPANY.TITLE',
            actions: [
                {
                    id: 'help',
                    label: '',
                    icon: 'help',
                    action: () => {
                        console.log('Click help button.')
                    }
                }
            ],
            style: 'any',
            isCanceled: true
        };

        // Form data
        this.formData = {
            title: '',
            fields: [
                // Search button
                // {
                //     type: 'button',
                //     value: '',
                //     label: 'COMPANY.SEARCH_BTN',
                //     visible: true,
                //     id: 'search-button',
                //     name: 'searchButton',
                //     action: () => {
                //         console.log('Click search button');
                //     }
                // },
                // Text for company name
                {
                    type: 'text',
                    value: this.enterprise.name,
                    label: 'COMPANY.COMPANY_NAME',
                    required: false,
                    visible: true,
                    regex: '^[A-z0-9 ]*$',
                    id: 'company-name',
                    name: 'companyName'
                    //validationMsg: 'COMPANY.COMPANY_NAME_ERR'
                },
                // Radio for civility
                {
                    type: 'radio',
                    value: this.enterprise.employer.identity.civility,
                    label: 'COMPANY.CIVILITY',
                    dataset: [
                        {id: 1, key: 'COMPANY.MRS', value: 'Mrs'},
                        {id: 2, key: 'COMPANY.MR', value: 'Mr'}
                    ],
                    required: false,
                    visible: true,
                    id: 'civility',
                    name: 'civility',
                },
                // Text for user name
                {
                    placeholder: '',
                    type: 'text',
                    value: this.enterprise.employer.identity.lastName,
                    label: 'COMPANY.NAME',
                    required: false,
                    visible: true,
                    regex: '[A-z]*$',
                    id: 'user-name',
                    name: 'userName',
                    validationMsg: 'COMPANY.NAME_ERR'
                },
                // Text for first name
                {
                    placeholder: '',
                    type: 'text',
                    value: this.enterprise.employer.identity.firstName,
                    label: 'COMPANY.FIRST_NAME',
                    required: false,
                    visible: true,
                    regex: '[A-z]*$',
                    id: 'first-name',
                    name: 'firstName',
                    validationMsg: 'COMPANY.FIRST_NAME_ERR'
                },
                // Company number
                {
                    placeholder: '999 999 999 99999',
                    type: 'text',
                    value: this.enterprise.siret,
                    label: 'COMPANY.COMPANY_SIRET',
                    required: false,
                    visible: true,
                    regex: '^[0-9 ]*$',
                    id: 'company-number',
                    min: 17,
                    max: 17,
                    name: 'companyNumber',
                    validationMsg: 'COMPANY.COMPANY_SIRET_ERR',
                    action: () => {
                        //this.maskCompanyNumber();
                    }
                },
                // Text for Naf
                {
                    placeholder: '9999A',
                    type: 'text',
                    value: this.enterprise.naf,
                    label: 'COMPANY.NAF',
                    required: false,
                    readonly: false,
                    visible: true,
                    min: 5,
                    max: 5,
                    regex: '^[0-9]{4}[A-Za-z]{1}',
                    id: 'naf',
                    name: 'naf',
                    validationMsg: 'COMPANY.NAF_ERR'
                }
            ],
            submit: {
                id: '',
                icon: '',
                label: 'COMPANY.VALIDATE_BTN',
                action: (event) => {
                    this.saveCompany();
                }
            }
        };

        this.currentCompanyNumber = this.formData.fields[5].value;
    }

	ngOnInit() {
		this.storage.get("currentUser").then((data: string) => {
			if (!data) {
				return;
			}
			this.currentUser = JSON.parse(data);
		});
	}

	saveCompany(){
		this.enterpriseService.saveCompany(this.enterprise, this.currentUser.id).then((data: StatusResponseDto) => {
			if (data && data.status == "success") {
				this.viewCtrl.dismiss()
			} else {
				console.log("une erreur est survenue lors de l'enregistrement des donnes");
				console.log(data.error);
			}
		});
    }



    maskCompanyNumber() {
        // Mask company number
        let newValue = this.formData.fields[5].value;

        if (newValue > this.currentCompanyNumber) {
            if (newValue.length === 3 || newValue.length === 7 || newValue.length === 11) {
                this.formData.fields[5].value += ' ';
            } else if ((newValue.length === 4 || newValue.length === 8 || newValue.length === 12) &&
                newValue[newValue.length - 2] !== ' ') {
                this.formData.fields[5].value = newValue.slice(0, newValue.length - 1) + ' ' + newValue[newValue.length - 1];
            }
        } else if (newValue < this.currentCompanyNumber) {
            if (newValue.length === 4 || newValue.length === 8 || newValue.length === 12) {
                this.formData.fields[5].value = this.formData.fields[5].value.slice(0, newValue.length - 1);
            }
        }

        this.currentCompanyNumber = this.formData.fields[5].value;
    }

}



