import {Injectable} from "@angular/core";
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {SearchQuery} from "../../models/search-query.model";
import {MissionServiceFactory} from "./mission-service-factory";
import {MissionServiceStrategy} from "./mission-service-strategy";

@Injectable()
export class MissionServiceProvider {
    constructor(public http : HttpRequestProvider,
                private configuration : Configuration) {

    }

    loadMissions(idTier : number,
                 offset : number,
                 mode : string,
                 constraints : SearchQuery) {
        let factory : MissionServiceFactory = new MissionServiceFactory(this.http, this.configuration);
        let strategy : MissionServiceStrategy = factory.create();
        return strategy.loadContracts(idTier, offset, mode, constraints);
    }
}


