import {AddressDto} from "./address.dto";
import {EmployerDto} from "./employer.dto";

export class EnterpriseDto{
	id: number;
	name: string;
	siret: string;
	naf: string;
	adresses: Array<AddressDto>;
	employer: EmployerDto;

	constructor(){
		this.id = 0;
		this.name = "";
		this.naf = "";
		this.siret = "";
		this.adresses = [];
		this.employer = new EmployerDto();
	}
}


