import {Component} from '@angular/core';

import {Configuration} from '../../config/voj-configuration';
import {FormType} from '../../models/form-type.model';
import {NavigationService} from '../../services/navigation.service';
import {VojConfiguration} from '../../models/voj-configuration.model';
import {VojPage} from '../../models/voj-page';
import {AvailabilitiesPage} from '../cv-offer-module/availabilities/availabilities';
import {VojPageHeaderType} from '../../models/voj-page-header.model';


@Component({
    selector: 'availabilities-add-slot-page',
    templateUrl: 'availabilities-add-slot.html',
})
export class AvailabilitiesAddSlotPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    formData: FormType;

    constructor(public configurationService: Configuration,
                public navigationService: NavigationService) {
        this.config = this.configurationService.configuration;

        this.headerData = {
            title: 'AVAILABILITIES_ADD_SLOT.ADD_AVAILABILITY',
            style: 'any',
            actions: [{
                id: 'xdcfggjhbj',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };

        this.formData = {
            fields: [
                {
                    required: true,
                    type: 'text',
                    label: 'AVAILABILITIES_ADD_SLOT.AVAILABLE_FROM',
                    value: '',
                    placeholder: '05/24/1990',
                    readonly: false,
                    visible: true,
                    id: '',
                    regex: '(?:0?2[/.-](?:[12][0-9]|0?[1-9])|(?:0?[469]|11)' +
                    '[/.-](?:30|[12][0-9]|0?[1-9])|(?:0?[13578]|1[02])[/.-](?:3[01]|[12][0-9]|0?[1-9]))[/.-][0-9]{4}',
                    name: 'Date from',
                },
                {
                    required: true,
                    type: 'text',
                    value: '',
                    label: 'AVAILABILITIES_ADD_SLOT.AVAILABLE_TILL',
                    placeholder: '05/24/1990',
                    readonly: false,
                    visible: true,
                    id: '',
                    regex: '(?:0?2[/.-](?:[12][0-9]|0?[1-9])|(?:0?[469]|11)' +
                    '[/.-](?:30|[12][0-9]|0?[1-9])|(?:0?[13578]|1[02])[/.-](?:3[01]|[12][0-9]|0?[1-9]))[/.-][0-9]{4}',
                    name: 'Date to',
                },
                {
                    value: 'wholeDay',
                    type: 'radioWithInputs',
                    label: 'Radio label',
                    dataset3: [
                        {
                            id: 1,
                            key: 'AVAILABILITIES_ADD_SLOT.FULL_DAY',
                            value: 'wholeDay'
                        },
                        {
                            id: 2,
                            key: '',
                            value: 'rangeHours',
                            subdata: {
                                key1: 'AVAILABILITIES_ADD_SLOT.FROM',
                                placeholder1: '10:45',
                                value1: '',
                                name1: 'from hours',
                                key2: 'AVAILABILITIES_ADD_SLOT.TO',
                                value2: '',
                                name2: 'to hours',
                                placeholder2: '11:15',
                            }
                        }
                    ],
                    visible: true,
                    required: true,
                    regex: '([01]\\d|2[0-3]):([0-5]\\d)',
                    name: 'EntireDay',
                    id: '',
                },
            ],
            submit: {
                id: 'xdcfgvyhubijnkfwlbhvg',
                label: 'AVAILABILITIES_ADD_SLOT.VALIDATE',
                icon: 'build',
                action: () => this.validateDateFunc()
            }
        }
    }

    validateDateFunc() {
        if (new Date(this.formData.fields[0].value) < new Date() ||
            new Date(this.formData.fields[1].value) < new Date(this.formData.fields[0].value)) {
            console.log('You can not enter a date earlier than the current date and ' +
                'also you can not enter second date earlier than the first date')
        } else {
            let vojNav: VojPage = {page: AvailabilitiesPage, isRoot: false};
            this.navigationService.navigate('list', vojNav);
        }
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad AvailabilitiesAddSlotPage');
    }

}



