import {Component, ElementRef, Input, OnInit, ViewChild} from '@angular/core';
import {IonicPage, Platform} from 'ionic-angular';
import {VojCardModel} from "../../models/voj-card.model";
import {EventBusProvider} from "../../providers/voj-events/voj-events";
import {SearchServiceProvider} from "../../providers/search-service/search-service";
import {VojPage} from "../../models/voj-page";
import {DetailsPage} from "../../pages/details/details";
import {NavigationService} from "../../services/navigation.service";
import {eBusType} from "../../models/voj-event.model";
import { Geolocation } from '@ionic-native/geolocation';
import {} from '@types/googlemaps';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../../pages/account-module/login/login";

declare var google: any;

@IonicPage()

@Component({
    selector: 'voj-map',
    templateUrl: 'voj-map.html',
})

export class VojMap implements OnInit {
    @Input('data')
    set data(value: Array<VojCardModel>) {
        let mapPassenger: eBusType = this.eventBus.passenger.type.get('map');
        this.eventBus.get(mapPassenger).then((eventValue: any) => {
            if (eventValue) {
                this.mapData = eventValue;
                this.initMap();
            } else if (value) {
                this.mapData = value;
                this.initMap();
            }
        });
    }

    @ViewChild('map') mapElement: ElementRef;
    map: any;
    hiddenMap: boolean = true;
    mapData: Array<VojCardModel>;
    position : any = null;
    latitude : number = 48.855168;
    longitude : number = 2.344813;
    markers : Array<google.maps.Marker>;

    constructor(public platform: Platform,
                private storage : Storage,
                private searchService: SearchServiceProvider,
                private geolocation: Geolocation,
                private eventBus: EventBusProvider,
                private navigationService: NavigationService) {
        this.markers = [];
        this.geolocation.getCurrentPosition().then((resp) => {
            if(resp && resp.coords){
                this.latitude = resp.coords.latitude;
                this.longitude = resp.coords.longitude;
            }
            this.initMap();
        }).catch((data:any)=>{
            //console.clear();
            console.log(JSON.stringify(data));
        });

        // TODO: Hotfix for the broken event problem to remove once the bus is fixed
        this.eventBus.subscribeMap(this);
    }

    ngOnInit() {
        this.initMap();
    }

    // TODO: Hotfix for the broken event problem to remove once the bus is fixed
    updateMap(data : Array<VojCardModel>){
        this.mapData = data;
        this.initMap();
    }

    initMap() {

        for(let i =0 ; i < this.markers.length ; i++){
            let m : google.maps.Marker;
            m = this.markers[i];
            m.setMap(null);
        }
        let latLng = new google.maps.LatLng(this.latitude, this.longitude);
        this.loadJSMap(latLng);
        this.hiddenMap = false;
        if (this.mapData && this.mapData.length > 0) {

            for (let i = 0; i < this.mapData.length; i++) {
                let card: VojCardModel = this.mapData[i];
                this.addMarker(card);
            }
        }
        

    }

    addMarker(card: VojCardModel) {
        let marker = new google.maps.Marker({
            position: {lat: card.address.lat, lng: card.address.lng},
            map: this.map,
            icon:  'https://maps.google.com/mapfiles/ms/icons/green-dot.png',
            title: card.address.title
        });
        let infoWindow = new google.maps.InfoWindow({
            content: '<div id="myInfoWinDiv_' + card.id + '">' +
            '<img src="' + card.thumbnail + '" style="width:50px;height:50px"><br />' +
            '<strong>' + card.title.label + '</strong><br />' +
            card.subtitles[0].label
            + '</div>'
        });
        google.maps.event.addListener(marker, 'click', function () {
            infoWindow.open(this.map, marker);
            google.maps.event.addDomListener(document.getElementById('myInfoWinDiv_' + card.id), 'click', function () {
                this.updateDetails(card);
            }.bind(this));
        }.bind(this));

        this.markers.push(marker);

    }

    loadJSMap(latLng: any) {

        let mapOptions = {
            center: latLng,
            zoom: 9,
            mapTypeId: google.maps.MapTypeId.ROADMAP
        };
        this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);


        let largeCircle = new google.maps.Circle({
            strokeColor: '#488aff',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: '#488aff',
            fillOpacity: 0.10,
            map: this.map,
            center: {lat: this.latitude, lng: this.longitude},
            radius: 50000
        });

        let mediumCircle = new google.maps.Circle({
            strokeColor: '#14baa6',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: '#14baa6',
            fillOpacity: 0.20,
            map: this.map,
            center: {lat: this.latitude, lng: this.longitude},
            radius: 30000
        });

        let shortCircle = new google.maps.Circle({
            strokeColor: '#757575',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: '#757575',
            fillOpacity: 0.30,
            map: this.map,
            center: {lat: this.latitude, lng: this.longitude},
            radius: 10000
        });
    }

    updateDetails(card: VojCardModel) {

        //  Check if user is authenticated
        this.storage.get("currentUser").then((sdata:any)=>{
            let data = JSON.parse(sdata);
            if(data && data.account) {

                //  OK user is authenticated
                let results = this.searchService.eventMode?this.searchService.events:this.searchService.results;
                console.log("Details : " + card.id);
                let c : VojCardModel = null;
                for(let i = 0 ; i < results.length ; i++){
                    if(results[i].id == card.id){
                        c = results[i].toCardDetails();
                        break;
                    }
                }

                let vojNav: VojPage = {page: DetailsPage, params:{card:c}, isRoot: true};
                this.navigationService.navigate('details', vojNav);
                this.eventBus.publishDetails(c);
            } else {
                //  These are not the droids you are looking for
                let vojPageList: VojPage = {page: LoginPage, isRoot: true, isWebModal: true};
                this.navigationService.navigate("list", vojPageList);
            }
        });


    }

}


