import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, Platform} from 'ionic-angular';
import {FieldType} from "../../../models/field-type";
import {ButtonType} from "../../../models/button-type.model";
import {VojPageHeaderType} from "../../../models/voj-page-header.model";


/**
 * Generated class for the OpenApplicationPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-open-application',
  templateUrl: 'open-application.html',
})
export class OpenApplication {
    headerData: VojPageHeaderType;
    formData: {
        fields: Array<FieldType>;
        submit: ButtonType
    };
    isMobile: boolean = false;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public platform: Platform) {
      platform.is('core') ? this.isMobile=false : this.isMobile=true;
      this.headerData = {
          title: 'Recevoir des candidatures',
          style: 'any',
          actions: [{
              id: 'xdcfgvyhubijnkwdwlbhvg',
              label: 'help',
              icon: 'help',
              action: function () {
                  console.log('button works')
              }
          }],
          isCanceled:true
      };
      this.formData = {
          fields: [{
              type: 'radio',
              value: 'Oui',
              label: 'Acceptez',
              dataset: [
                  {id: 4, key: 'Oui', value: 'Oui'},
                  {id: 5, key: 'Non', value: 'Non'},
              ],
              readonly: false,
              visible: true,
              id: '111',
              name: 'Acceptez'
          }],
          submit: {
              id: 'xdcfgvyhubijnkfwlbhvg',
              label: 'Valider',
              icon: '',
              action: function () {
                  console.log('button works')
              }
          }
      }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad OpenApplicationPage');
  }

}



