import {Component} from '@angular/core';

import {AvailabilitiesDataType} from '../../../models/availabilities-data-type';
import {Configuration} from '../../../config/voj-configuration';
import {JobyerAvailabilityChoicePage} from '../jobyer-availability-choice/jobyer-availability-choice';
import {NavigationService} from '../../../services/navigation.service';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojConfiguration} from '../../../models/voj-configuration.model';
import {VojPage} from '../../../models/voj-page';

// Availabilities 1/4
@Component({
    selector: 'availabilities-page',
    templateUrl: 'availabilities.html',
})
export class AvailabilitiesPage {
    config: VojConfiguration;
    headerData: VojPageHeaderType;
    testAvailabilitiesData: Array<AvailabilitiesDataType>;

    constructor(public configurationService: Configuration,
                public navigationService: NavigationService) {
        this.config = this.configurationService.configuration;

        this.headerData = {
            title: 'AVAILABILITIES.MY_AVAILABILITIES',
            titleLocalized: true,
            style: 'any',
            actions: [{
                id: 'xdcfgvyhubijnklbhvg',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };

        this.testAvailabilitiesData = [
            {
                fromDay: new Date(1503679162783),
                toDay: new Date(1503679162783 + 86400000),
                fromHour: 12,
                toHour: 1,
                entireDay: true
            },
            {
                fromDay: new Date(1503699162783),
                toDay: new Date(1503699162783 + 86400000),
                fromHour: 12,
                toHour: 1,
                entireDay: true
            },
        ];
    }

    deleteAvailability(index) {
        this.testAvailabilitiesData.splice(index, 1);
    }

    goToJobyerAvailabilityChoice() {
            let vojNav: VojPage = {page: JobyerAvailabilityChoicePage, isRoot: false};
            this.navigationService.navigate('list', vojNav);
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad BankSettingsPage');
    }

}



