import {SearchQuery} from './search-query.model';

type SearchCall = (query: SearchQuery) => any;

/**
 * Search header data is an input model specifying placeholders and the function performing actual search
 */
export class SearchHeaderData {
    placeholders: {
        main: string,
        what: string,
        where: string,
        when: string,
        who: string
    };
    action: SearchCall ;
}


