import {Injectable} from "@angular/core";
import {ToastController} from 'ionic-angular';
import {VojConfiguration} from "../models/voj-configuration.model";
import {Configuration} from "../config/voj-configuration";

@Injectable()
export class ToastService {
    config: VojConfiguration;
    isMobile: boolean = false;

    constructor(public configurationService: Configuration,
                public toastCtrl: ToastController) {
        this.config = this.configurationService.configuration;
    }

    presentEditingToast() {
        let toast = this.toastCtrl.create({
            message: 'Vos informations ont bien t modifies',
            duration: 5000,
            position: this.config.isLargeScreen ? 'top' : 'mobile',
            cssClass: this.config.isLargeScreen ? 'toast-top-desktop' : 'toast-bottom-mobile',
            showCloseButton: true,
            closeButtonText: 'x'
        });
        toast.present();
    }

    presentEnteredToast() {
        let toast = this.toastCtrl.create({
            message: 'Vos informations ont bien t enregistres.',
            duration: 5000,
            position: this.config.isLargeScreen ? 'top' : 'mobile',
            cssClass: this.config.isLargeScreen ? 'toast-top-desktop' : 'toast-bottom-mobile',
            showCloseButton: true,
            closeButtonText: 'x'
        });
        toast.present();
    }
}


