import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { TranslateModule } from '@ngx-translate/core';

import { FormsModule } from '@angular/forms';
import { VojForm } from './voj-form';
import { MdButtonModule, MdCheckboxModule, MdDatepickerModule, MdNativeDateModule, MdInputModule } from '@angular/material';

@NgModule({
	declarations: [
		VojForm
	],
	imports: [
		IonicPageModule.forChild(VojForm),
        TranslateModule.forChild(),
		FormsModule,
        MdButtonModule,
        MdCheckboxModule,
        MdDatepickerModule,
        MdNativeDateModule,
        MdInputModule
	],
	exports: [
		VojForm
	]
})
export class VojFormModule {
}



