import {Component, OnInit} from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {SearchHeaderData} from "../../../models/search-header.model";
import {VojCardModel} from "../../../models/voj-card.model";
import {Storage} from "@ionic/storage";
import {MissionServiceProvider} from "../../../providers/mission-service/mission-service";
import {SearchQuery} from "../../../models/search-query.model";
import {TextType} from "../../../models/text-type";
import {DateUtils} from "../../../utils/utils";

/**
 * Generated class for the MyMissionsListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-my-missions-list',
  templateUrl: 'my-missions-list.html',
})
export class MyMissionsListPage implements OnInit  {
  pageHeader : VojPageHeaderType;
  searchHeaderData : SearchHeaderData;
  cards : Array<VojCardModel>;
  activeCards : Array<VojCardModel>;
  finishedCards : Array<VojCardModel>;
  archivedCards : Array<VojCardModel>;
  missionType : string = 'active';
  mobileStyle: any;
  searchType: boolean;
  searchQuery : SearchQuery;

  constructor(public navCtrl: NavController,
              public missionService : MissionServiceProvider,
              public storage : Storage,
              public navParams: NavParams) {
    this.pageHeader = new VojPageHeaderType();
    this.pageHeader.actions = [];
    this.pageHeader.title = 'Mes missions';

    this.searchHeaderData = new SearchHeaderData();
    this.searchHeaderData.action = (q: SearchQuery) => {
      this.performSearch(q);
    }
    this.searchHeaderData.placeholders = {
          main: 'Rechercher dans mes missions',
          what: 'Job',
          where: 'Numro de contrat',
          when: 'Date',
          who: 'Tiers'
    };

    this.storage.get("currentUser").then((sdata: any) => {
      let ac = JSON.parse(sdata);
      let sq : SearchQuery = new SearchQuery();
      this.missionService.loadMissions(ac.id, 0, 'signed_contracts',sq).then((data : any)=>{
        this.cards = [];
        this.activeCards = [];
        this.finishedCards = [];
        this.archivedCards = [];
        for(let i = 0 ; i < data.length ; i++) {
          let o = data[i];
          let c : VojCardModel = new VojCardModel();
          c.id = o.id;
          c.title = null;
          c.subtitles = [];
          let sb : TextType;

          sb = new TextType();
          sb.label = 'Du '+DateUtils.sqlToDisplay(o.start)+' au '+DateUtils.sqlToDisplay(o.end);
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = o.title;
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = o.enterprise.name;
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = 'Contrat n'+o.number;
          c.subtitles.push(sb);

          c.actions = [{
            id : 'details',
            icon:'',
            label:"Details",
            action:()=> {
              console.log('REC');
            },
            style : {'background-color' : '#D339F8', 'color' : '#ffffff'}
          }];

          if(o.status == "En cours") {
            this.activeCards.push(c);
          } else if (o.status == 'Archiv') {
            this.archivedCards.push(c);
          } else if (o.status == 'Termin') {
            this.finishedCards.push(c);
          }
        }
        this.cards = this.activeCards;
      });
    });

  }

  performSearch(query : SearchQuery) {
    this.searchQuery = query;
    this.storage.get("currentUser").then((sdata: any) => {
      let ac = JSON.parse(sdata);
      this.missionService.loadMissions(ac.id, 0, 'signed_contracts_search',this.searchQuery).then((data : any)=>{
        this.cards = [];
        this.activeCards = [];
        this.finishedCards = [];
        this.archivedCards = [];
        for(let i = 0 ; i < data.length ; i++) {
          let o = data[i];
          let c : VojCardModel = new VojCardModel();
          c.id = o.id;
          c.title = null;
          c.subtitles = [];
          let sb : TextType;

          sb = new TextType();
          sb.label = 'Du '+DateUtils.sqlToDisplay(o.start)+' au '+DateUtils.sqlToDisplay(o.end);
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = o.title;
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = o.enterprise.name;
          c.subtitles.push(sb);
          sb = new TextType();
          sb.label = 'Contrat n'+o.number;
          c.subtitles.push(sb);

          c.actions = [{
            id : 'details',
            icon:'',
            label:"Details",
            action:()=> {
              console.log('REC');
            },
            style : {'background-color' : '#D339F8', 'color' : '#ffffff'}
          }];

          if(o.status == 'En cours') {
            this.activeCards.push(c);
          } else if (o.status == 'Archiv') {
            this.archivedCards.push(c);
          } else if (o.status == 'Termin') {
            this.finishedCards.push(c);
          }
        }
        if(this.missionType == 'active')
          this.cards = this.activeCards;
        else if(this.missionType == 'finished')
          this.cards = this.finishedCards;
        else
          this.cards = this.archivedCards;
      });
    });
  }

  handleSearchType(searchType){
    this.searchType = searchType;
  }

  ngOnInit() {

  }

  showMissionByType(e) {
    if(this.missionType == 'active')
      this.cards = this.activeCards;
    else if (this.missionType == 'finished')
      this.cards = this.finishedCards;
    else
      this.cards = this.archivedCards;
  }

  loadMoreCards(scrollEvent){
    if(!this.cards || this.cards.length==0
        || this.cards.length%10!=0) {
      scrollEvent.complete();
      return;
    }

    setTimeout(() => {
      this.storage.get("currentUser").then((sdata: any) => {
        let ac = JSON.parse(sdata);
        this.missionService.loadMissions(ac.id, this.cards.length, 'signed_contracts_search',this.searchQuery).then((data : any)=>{
          this.cards = [];
          this.activeCards = [];
          this.finishedCards = [];
          this.archivedCards = [];
          for(let i = 0 ; i < data.length ; i++) {
            let o = data[i];
            let c : VojCardModel = new VojCardModel();
            c.id = o.id;
            c.title = null;
            c.subtitles = [];
            let sb : TextType;

            sb = new TextType();
            sb.label = 'Du '+DateUtils.sqlToDisplay(o.start)+' au '+DateUtils.sqlToDisplay(o.end);
            c.subtitles.push(sb);
            sb = new TextType();
            sb.label = o.title;
            c.subtitles.push(sb);
            sb = new TextType();
            sb.label = o.enterprise.name;
            c.subtitles.push(sb);
            sb = new TextType();
            sb.label = 'Contrat n'+o.number;
            c.subtitles.push(sb);

            c.actions = [{
              id : 'details',
              icon:'',
              label:"Details",
              action:()=> {
                console.log('REC');
              },
              style : {'background-color' : '#D339F8', 'color' : '#ffffff'}
            }];

            if(o.status == 'En cours') {
              this.activeCards.push(c);
            } else if (o.status == 'Archiv') {
              this.archivedCards.push(c);
            } else if (o.status == 'Termin') {
              this.finishedCards.push(c);
            }
          }
          if(this.missionType == 'active')
            this.cards = this.activeCards;
          else if(this.missionType == 'finished')
            this.cards = this.finishedCards;
          else
            this.cards = this.archivedCards;
        });
      });
    }, 500);

  }

  ionViewDidLoad() {

  }

}



