import { Injectable } from '@angular/core';
import {Storage} from "@ionic/storage";
import 'rxjs/add/operator/map';
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {VojTuple} from "../../models/voj-tuple.model";
import {SearchResultDTO} from "../../dtos/search-result.dto";
import {VojCardModel} from "../../models/voj-card.model";
import {SearchQuery} from "../../models/search-query.model";
import {EventBusProvider} from "../voj-events/voj-events";
import {ButtonType} from "../../models/button-type.model";
import {SearchDTOFactory} from "../../dtos/dto-strategy/search/search-dto-factory";


@Injectable()
export class SearchServiceProvider {

  results : Array<SearchResultDTO>;
  eventMode : boolean = false;
  events : Array<SearchResultDTO> = [];
  eventsCards : Array<VojCardModel> = [];

  constructor(public http: HttpRequestProvider,
              private dtoFactory : SearchDTOFactory,
              private eventBus : EventBusProvider,
              public configuration : Configuration,
              private storage : Storage) {
    this.eventBus.subscribeSearchService(this);
  }

  /**
   * Mark an event as read on the database
   * @param card  the associated card
   * @returns {Promise<T>}
   */
  markAsRead(card : VojCardModel) {

    if(!this.eventMode)
      return;

    let id = card.id;
    let idEvent = 0;
    let index = -1;
    for(let i = 0 ; i < this.events.length ; i++) {
      if(this.events[i].dataSource.result.id == id) {
        idEvent = this.events[i].eventId;
        index = i;
        break;
      }
    }

    if(idEvent == 0)
      return;

    if(card.badges && card.badges.length>0)
      for(let i = 0 ; i < card.badges.length ; i++) {
        if(card.badges[i].id=='read-mark') {
          card.badges[i].icon = 'mail-open';
        }
      }

    let payload = {
      "service": "READ_PUSH",
      "eventId": idEvent
    };
    let url = null;
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }


    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        resolve(data);
      });
    });
  }

  /**
   * Retrieve the most recent jobyers/annonces
   * @param loadMore new search or loading more
   * @returns {Promise<T>}
   */
  retrieveRecent(loadMore : boolean, offset?:number) {
    this.eventMode = false;

    offset = loadMore?offset:0;

    let payload = {
      "service": "RECENT",
      "role": this.configuration.configuration.customer == "J"?"jobyer":"employer",
      "offset":offset
    };
    let url = "https://hermes.vitonjob.com/search/";
    try {
      for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
        let t : VojTuple = this.configuration.configuration.urls[i];
        if (t.key == "search") {
          url = t.value;
          break;
        }
      }
    }catch(exception){
      console.log("notification occured before initlaizing configuration");
    }

    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        this.eventMode = false;
        if(!loadMore)
          this.results = [];
        let cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          if(!data[i] || !data[i].result) {
            continue;
          }

          let r : SearchResultDTO = new SearchResultDTO(data[i], this.storage, this.dtoFactory);
          let c = r.toVojCard();
          cards.push(c);
          this.results.push(r);
        }
        resolve(cards);
      });
    });
  }

  /**
   *
   * @param accountId
   * @param loadMore new search or loading more
   * @returns {Promise<T>}
   */
  checkEvents(accountId : number, loadMore : boolean) {
    if(!this.configuration.configuration.customer)  // Not yet initialized
      return;

    let offset = loadMore?this.events.length:0;

    let payload = {
      "service": "CHECK",
      "accountId": accountId,
      "offset": offset
    };
    let url = "https://hermes.vitonjob.com/search/";
    try {
      for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
        let t : VojTuple = this.configuration.configuration.urls[i];
        if (t.key == "search") {
          url = t.value;
          break;
        }
      }
    }catch(exception){
      console.log("notification occured before initlaizing configuration");
    }

    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{

        if(!loadMore) {
          this.events = [];
          this.eventsCards = [];
        }
        let cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          if(!data[i] || !data[i].subject) {
            continue;
          }
          let r : SearchResultDTO = null;

          if(this.configuration.configuration.customer == "J")
            r = new SearchResultDTO(data[i].subject.annonce, this.storage, this.dtoFactory);
          else
            r = new SearchResultDTO(data[i].subject.jobyer, this.storage, this.dtoFactory);

          r.eventId = data[i].id_event;
          r.read = data[i].read;
          r.badges = [];
          let b : ButtonType = new ButtonType();
          b.icon = data[i].read?'mail-open':'mail';
          r.badges.push(b);
          b.id='read-mark';
          let c = r.toVojCard();
          cards.push(c);
          this.events.push(r);
        }
        this.eventsCards = cards;
        resolve(cards);
      });
    });
  }

  /**
   * Seek offers/jobyers based on composit query
   * @param query SearchQuery object
   * @param loadMore new search or loading more
   * @returns {Promise<T>}
   */
  criteriaSearch(query : SearchQuery, loadMore : boolean) {
    this.eventMode = false;
    let offset = loadMore?this.results.length:0;
    let payload = {
      "service": "CRITERIA",
      "role": this.configuration.configuration.customer == "J" ? "jobyer" : "employer",
      "query": query,
      "offset": offset
    };
    let url = "";
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }


    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        this.eventMode = false;
        if(!loadMore)
          this.results = [];
        let cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          let r : SearchResultDTO = new SearchResultDTO(data[i], this.storage, this.dtoFactory);
          let c = r.toVojCard();
          cards.push(c);
          this.results.push(r);
        }
        resolve(cards);
      });
    });
  }

  /**
   * Seek offers/jobyers based on natural language query
   * @param fullText
   * @param loadMore new search or loading more
   * @returns {Promise<T>}
   */
  semanticSearch(fullText : string, loadMore : boolean) {
    this.eventMode = false;
    let offset = loadMore?this.results.length:0;
    let payload = {
      "service": "FULL_TEXT",
      "role": this.configuration.configuration.customer == "J" ? "jobyer" : "employer",
      "fullText": fullText,
      "offset": offset
    };

    let url = "";
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }
    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        this.eventMode = false;
        if(!loadMore)
          this.results = [];
        let cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          let r : SearchResultDTO = new SearchResultDTO(data[i], this.storage, this.dtoFactory);
          let c = r.toVojCard();
          cards.push(c);
          this.results.push(r);
        }
        resolve(cards);
      });
    });
  }

}



