import { ActionSheetController, PopoverController } from 'ionic-angular'
import { Component, Input, OnInit } from '@angular/core';
import { IonicPage } from 'ionic-angular';

import { SocialButtonsType } from '../../models/social-buttons-type';
import { VojMenu } from '../voj-menu/voj-menu';
import {VojCardModel} from "../../models/voj-card.model";
import {VojPageHeaderType} from "../../models/voj-page-header.model";

@IonicPage()
@Component({
	selector: 'voj-card',
	templateUrl: 'voj-card.html',
})

export class VojCard implements OnInit {
	@Input('data') data : VojCardModel;
	@Input('vojData') vojData : Array<VojCardModel>;
    @Input('mode') mode : string;
    reduced : boolean;
    modelForSegment: number;
    socialButtons: Array<SocialButtonsType> = [];

	dataPageHeader: VojPageHeaderType;


	constructor(
		private actionSheetCtrl: ActionSheetController,
		private popoverCtrl: PopoverController
	) {
	}

	openMenu(myEvent) {
		let popover = this.popoverCtrl.create(VojMenu, {data: this.data.menu});
		popover.present({
			ev: myEvent
		});
	}

	presentActionSheet() {
		let actionSocial = this.actionSheetCtrl.create({
			title: 'Social networks',
			buttons: this.socialButtons
		});
		actionSocial.present();
	}

    setClasses(data) {
		// Set classes if json field does not exist
        return {
            'voj-indent': !data.title,
            'voj-without-menu': !data.cover && !data.menu,
            'voj-without-badges': !data.cover && !data.badges,
            'voj-without-menu-and-badges': !data.cover && !data.menu && !data.badges,
			'voj-card-without-header': !data.cover && !data.menu && !data.badges && !data.thumbnail
        }
    }

    ngOnInit(){
        // Data for header of card
        this.reduced = this.mode =='reduced';
        /*this.dataPageHeader = this.data.header;

        if(!this.data.header) {
            this.data.header = new VojPageHeaderType();
        }
        this.dataPageHeader = this.data.header;*/


        // Social buttons for share
        if (this.data && this.data.socialButtons && this.data.socialButtons.length > 0) {
            for (let i = 0; i < this.data.socialButtons.length; i++) {
                this.socialButtons.push(
                    {
                        text: this.data.socialButtons[i].label,
                        handler: this.data.socialButtons[i].action,
						icon: this.data.socialButtons[i].icon
                    }
                )
            }
        }

        this.modelForSegment = 0;
    }

}



