import {Strategy} from "./strategy";
import {VojPageHeaderType} from "../../../../models/voj-page-header.model";

export class JobyerStrategy implements Strategy{

    loadHeaderData(): VojPageHeaderType{
        return {
            title: 'USER_INFORMATION.TITLE_JOBYER',
            actions: [
                {
                    id: 'help',
                    label: '',
                    icon: 'help',
                    action: () => {
                        console.log('Click help button.')
                    }
                }
            ],
            style: 'any',
            isCanceled: true
        };
    }
}


