import {SearchDTOStrategy} from "./search-dto-strategy";
import {ButtonType} from "../../../models/button-type.model";
import {TextType} from "../../../models/text-type";
import {VojCardModel} from "../../../models/voj-card.model";
import {SegmentType} from "../../../models/segment-type";
export class SearchDTOEmployer implements SearchDTOStrategy {
    generateCardButtons() : Array<ButtonType> {
         return [{
             id : 'recruite',
             icon:'',
             label:'Recruter',
             action:()=> {
                 console.log('REC');
             },
             style : {'background-color' : '#eca613', 'color' : '#ffffff'}
         },{
             id : 'details',
             icon:'',
             label:'Dtails',
             action:()=> {
                 console.log('DTL');
             },
             style : {'background-color' : '#D339F8', 'color' : '#ffffff'}
         }];
    }

    generateDetailedCardButtons() : Array<ButtonType>{
        return [{
            id : 'recruite',
            icon:'',
            label:'Recruter',
            action:()=> {
                console.log('REC');
            },
            style : {'background-color' : '#eca613', 'color' : '#ffffff'}
        }];
    }

    generateSubtitles(dataSource : any) : Array<TextType> {
        let subtitles : Array<TextType> = [];
        let st : TextType = new TextType();
        st.label = dataSource.restrictedAddress;
        st.rendered = true;
        st.icon = 'pin';
        st.id = 'restricted_adress';
        if(st.label && st.label.length>0)
            subtitles.push(st);

        st = new TextType();
        st.label = "Disponible  partir de   compter du "+dataSource.result.start;
        st.rendered = true;
        st.icon = 'calendar';
        st.id = 'mission_calendar';
        subtitles.push(st);

        st = new TextType();
        st.label = "&starf;&nbsp;&starf;&nbsp;&starf;&nbsp;&starf;&nbsp;&starf; (17 avis)";
        st.rendered = false;
        st.icon = '';
        st.id = 'mission_rating';
        subtitles.push(st);

        return subtitles;
    }

    generateSegments(dataSource : any, card : VojCardModel) : Array<SegmentType> {
        card.title.label = dataSource.result.restrictedName;
        let segments : Array<SegmentType> = [];
        let s : SegmentType = new SegmentType();
        s.title = {
            icon : '',
            label : dataSource.result.start
        };
        s.subtitle = {
            icon : '',
            label : 'Disponibilit'
        };
        s.stateful = true;
        s.action = ()=> {
            card.segments[0].stateful = true;
            card.segments[1].stateful = false;

            for(let i = 0 ; i < card.subtitles.length ; i++)
                if(card.subtitles[i].id === 'restricted_adress'
                    ||  card.subtitles[i].id === 'mission_calendar')
                    card.subtitles[i].rendered = true;
                else
                    card.subtitles[i].rendered = false;
        };
        segments.push(s);

        s = new SegmentType();
        s.title = {
            icon : '',
            label : '5'
        };
        s.subtitle = {
            icon : '',
            label : 'Etoile(s)'
        };
        s.action = ()=> {
            card.segments[0].stateful = false;
            card.segments[1].stateful = true;

            for(let i = 0 ; i < card.subtitles.length ; i++)
                if(card.subtitles[i].id === 'mission_rating')
                    card.subtitles[i].rendered = true;
                else
                    card.subtitles[i].rendered = false;
        };
        segments.push(s);

        return segments;
    }
}


