import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { IonicPage } from 'ionic-angular';

@IonicPage()
@Component({
	selector: 'voj-segment',
	templateUrl: 'voj-segment.html',
})
export class VojSegment implements OnInit {
	@Input('data') data;
	@Input('segmentModel') segmentModel: any;
	@Input('isStats') isStats: boolean;
	@Output() segmentModelChange = new EventEmitter<any>();

	constructor() {

	}

	ngOnInit() {

	}
}



