import { NgModule, LOCALE_ID } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { NgCalendarModule } from '../calendar/calendar.module';
import { PopoverPage } from './components/voj-popover.page';
import { SmartCalendar } from './smart-calendar';

@NgModule({
	declarations: [
		SmartCalendar,
		PopoverPage
	],
	imports: [
		NgCalendarModule,
		IonicPageModule.forChild(SmartCalendar)
	],
	exports: [
		SmartCalendar
	],
	entryComponents: [
		PopoverPage
	],
	providers: [
		{ provide: LOCALE_ID, useValue: 'fr-FR' }
	]
})
export class SmartCalendarModule {
}


