import {Component, Input} from '@angular/core';
import { Platform } from "ionic-angular";
import {ButtonType} from "../../models/button-type.model";
import {VojConfiguration} from "../../models/voj-configuration.model";
import {Configuration} from "../../config/voj-configuration";

@Component({
  selector: 'voj-footer',
  templateUrl: 'voj-footer.html'
})
export class VojFooter {

  config: VojConfiguration;

  @Input() data: {style: string, actions: Array<ButtonType>};
  @Input() testVar: string;


  constructor(public configurationService: Configuration) {
    console.log('Hello VojFooter Component');
    this.config = this.configurationService.configuration;
  }
}



