import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, Platform} from 'ionic-angular';
import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {ButtonType} from "../../../models/button-type.model";
import {FieldType} from "../../../models/field-type";

/**
 * Generated class for the EnterpriseDetails page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-enterprise-details',
    templateUrl: 'enterprise-details.html',
})
export class EnterpriseDetails {
    isMobile: boolean = false;
    headerData: VojPageHeaderType;
    formData: {
        fields: Array<FieldType>;
        submit: ButtonType
    };

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public platform : Platform) {
        platform.is('core') ? this.isMobile = false : this.isMobile = true;
        this.headerData = {
            title: 'Mess Donnees',
            style: 'any',
            actions: [{
                id: 'xdcfgvyhubijnklbhvg',
                label: 'help',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }],
            isCanceled: true
        };
        this.formData = {
            fields: [{
                type: 'select',
                value: 'Convention collective1',
                label: 'Convention collective1',
                dataset: [
                    {id: 4, key: 'russian', value: 'Russian'},
                    {id: 5, key: 'ukrainian', value: 'Ukrainian'},
                    {id: 6, key: 'english', value: 'English'}
                ],
                readonly: false,
                visible: true,
                id: '',
                name: 'Convention collective'
            },
                {
                    type: 'number',
                    value: 'Convention collective2',
                    label: 'Convention collective2',
                    readonly: false,
                    visible: true,
                    id: '',
                    name: 'Convention collective'
                },
                {
                    type: 'select',
                    value: 'Convention collective3',
                    label: 'Convention collective3',
                    dataset: [
                        {id: 4, key: 'russian', value: 'Russian'},
                        {id: 5, key: 'ukrainian', value: 'Ukrainian'},
                        {id: 6, key: 'english', value: 'English'}
                    ],
                    readonly: false,
                    visible: true,
                    id: '',
                    name: 'Convention collective'
                }],
            submit: {
                id: 'xdcfgvyhubijnkfwlbhvg',
                label: 'Suivant',
                icon: 'help',
                action: function () {
                    console.log('button works')
                }
            }
        }
    }

    ionViewDidLoad() {

        console.log('ionViewDidLoad EnterpriseDetails');
    }

}



