import {SearchDTOStrategy} from "./search-dto-strategy";
import {Configuration} from "../../../config/voj-configuration";
import {SearchDTOJobyer} from "./search-dto-jobyer";
import {SearchDTOEmployer} from "./search-dto-employer";
import {Injectable} from "@angular/core";

@Injectable()
export class SearchDTOFactory {

    constructor(private config : Configuration) {

    }

    create():SearchDTOStrategy {
        if(this.config.configuration.customer=='J')
            return new SearchDTOJobyer();
        else
            return new SearchDTOEmployer();
    }
}


