import {Component} from '@angular/core';
import {LoadingController, NavParams, ViewController} from 'ionic-angular';
import {Http} from '@angular/http';
import 'rxjs/add/operator/toPromise';

import {VojPageHeaderType} from '../../../models/voj-page-header.model';
import {VojFormModel} from '../../../models/voj-form.model';
import {AddressDto} from "../../../dtos/address.dto";
import {AccountService} from "../../../providers/account-service/account-service";
import {CurrentUserDto} from "../../../dtos/current-user.dto";
import {StatusResponseDto} from "../../../dtos/status-response.dto";
import {Storage} from "@ionic/storage";
import {PersonalDetailsPage} from '../personal-details/personal-details';
import {ProfileService} from "../../../providers/profile-service/profile-service";
import {NavigationService} from "../../../services/navigation.service";
import {VojPage} from "../../../models/voj-page";
import {Configuration} from "../../../config/voj-configuration";
import {VojConfiguration} from "../../../models/voj-configuration.model";
import {ObjectUtils} from "../../../utils/utils";
import {ToastService} from "../../../services/toast.service";
import {AddressService} from "../../../providers/address-service/address-service";

@Component({
	selector: 'main-address-page',
	templateUrl: 'main-address.html',
})
export class MainAddressPage{
	isInscription: boolean;
	dataPageHeader: VojPageHeaderType;
	formData: VojFormModel;
	currentUser: CurrentUserDto;
	config: VojConfiguration;
    fieldNumber: string;
    fieldStreet: string;
	fieldZipCode: string;
	fieldCity: string;
	addressId: number;
    addressName: string;

	constructor(public http: Http,
							public loadingCtrl: LoadingController,
							public configurationService: Configuration,
							private storage: Storage,
							public viewCtrl: ViewController,
							private navParams: NavParams,
							private navigationService: NavigationService,
                			private addressService: AddressService,
                			public toast: ToastService) {
		this.isInscription = this.navParams.data.isInscription;
		this.config = this.configurationService.configuration;

		// Set header data
		this.dataPageHeader = {
			title: 'Mon adresse',
			actions: [
				{
					id: 'help',
					icon: 'help',
					action: () => {
						console.log('Click help button.')
					}
				}
			],
			isCanceled: false
		};

		// Set form data
		this.formData = {
			fields: [
				// Button
				{
					type: 'button',
					label: 'Me golocaliser',
					visible: true,
					id: 'geolocate-me',
					action: () => {
						console.log('Click Geolocalise me.')
						this.getCurrentAddress();
					}
				},
				// Text
				/*{
						placeholder: '',
						type: 'text',
						value: 'Domicile',
						label: 'Name of address',
						required: true,
						readonly: false,
						visible: true,
						min: 0,
						max: 100,
						regex: '^[A-Za-z0-9 ]*$',
						validationMsg: 'Name of address error',
						id: '',
						name: 'name'
				},*/
				// Text
				{
					type: 'text',
					value: '',
					label: 'N',
					required: true,
					visible: true,
					//TODO: add slash, comma and single quote to the regex
					regex: '^[A-Za-z0-9- ]*$',
					//TODO: translate all the text
					validationMsg: 'Veuillez saisir un numro de rue valide',
					id: 'number',
					name: 'number'
				},
				// Text
				{
					type: 'text',
					value: '',
					label: 'Rue',
					required: true,
					visible: true,
					//TODO: add slash, comma and single quote to the regex
					regex: '^[A-Za-z0-9- ]*$',
					validationMsg: 'Veuillez saisir une rue valide',
					id: 'street',
					name: 'street'
				},
				// Number
				{
					type: 'text',
					value: '',
					label: 'Code postal',
					required: true,
					visible: true,
					min: 5,
					max: 5,
					regex: '^[0-9]*$',
					validationMsg: 'Veuillez saisir un code postal valide',
					id: 'zipCode',
					name: 'zipCode'
				},
				// Text
				{
					type: 'text',
					value: '',
					label: 'Ville',
					required: true,
					visible: true,
					//TODO: add single quote to the regex
					regex: '^[A-Za-z- ]*$',
					validationMsg: 'Veuillez saisir une ville valide',
					id: 'city',
					name: 'city'
				},
				// Button
				/*{
						type: 'button',
						label: 'Add an address',
						visible: true,
						id: 'add-address-btn',
						action: () => {
								console.log('Click Add an address button.')
						}
				}*/
			],
			submit: {
				id: 'submit',
				label: (this.isInscription ? 'Suivant' : 'Valider'),
				action: (event) => {
					this.saveAddress();
				}
			}
		};
	}

	ngOnInit(){
		//TODO: prevent access to this page for disconnected users
		this.storage.get("currentUser").then((val) => {
			if (ObjectUtils.isEmpty(val)) {
				return;
			}
			this.currentUser = JSON.parse(val);

			this.initializationMainAddress(this.currentUser.id);
		});
	}

    /*checkEditedFields() {
        if ((this.formData.fields[1].value !== this.fieldNumber || this.formData.fields[2].value !== this.fieldStreet ||
                this.formData.fields[3].value !== this.fieldZipCode || this.formData.fields[4].value !== this.fieldCity)
			&& (this.fieldNumber !== '' && this.fieldStreet !== '' && this.fieldZipCode !== '' && this.fieldCity !== '')) {
            return true;
        }
        return false
    }

    checkEnteredFields() {
        if (this.fieldNumber === '' && this.fieldStreet === '' && this.fieldZipCode === '' && this.fieldCity === '') {
            return true;
        }
        return false
    }*/

    saveAddress() {
        /*if (this.checkEditedFields())  {
            this.toast.presentEditingToast();
        }
        else if (this.checkEnteredFields()) {
            this.toast.presentEnteredToast();
        }*/
		//if all fields are empty, ignore this step and go to the next page
		/*let ignoreThisStep = !ObjectUtils.isFormNotEmpty(this.formData.fields);
		if(ignoreThisStep){
			this.goToPersonalDetailsPage();
			return;
		}*/

        let addressDto: AddressDto = {
            id: this.addressId,
            name: this.addressName,
            number: this.formData.fields.filter(f => f.name === "number")[0].value,
            street: this.formData.fields.filter(f => f.name === "street")[0].value,
            zipCode: this.formData.fields.filter(f => f.name === "zipCode")[0].value,
            city: this.formData.fields.filter(f => f.name === "city")[0].value,
            adressType: "personal"
        };

        //save a new address
		if (this.isInscription) {
			this.addressService.insertAddress(addressDto, this.currentUser.id).then((data: StatusResponseDto) => {
				if (data.status == "success") {
					//in the case of inscription: we should dismiss the mainAdress modal/page and show the PersonalDetails modal/page
					this.goToPersonalDetailsPage();
				} else {
					//TODO: display a notification of error
					console.log("une erreur est survenue lors de l'enregistrement des donnes");
					console.log(data.error);
				}
			});
		} else {
			//update an existing address
			this.addressService.updateAddress(addressDto).then((data: StatusResponseDto) => {
				if (data.status == "success") {
				//TODO: display a notification of success
				} else {
					//TODO: display a notification of error
					console.log("une erreur est survenue lors de l'enregistrement des donnes");
					console.log(data.error);
				}
			});
		}
	}

	goToPersonalDetailsPage() {
		this.navigationService.dismiss(this.viewCtrl);
		let vojPage: VojPage = {page: PersonalDetailsPage, isWebModal: true, params: {isInscription: this.isInscription}};
		this.navigationService.navigate("main", vojPage);
	}

	getCurrentAddress(): void {
		// Show loading spinner
		let loading = this.loadingCtrl.create({});
		loading.present();

		// Clear input fields
		this.resetForm();

		// Check for geolocation availability
		if (!navigator.geolocation) {
			// Hide loading spinner
			loading.dismiss();
			//TODO: display an alert with an error msg explaining the cause of the geolocation failure
			return;
		}

		// Get current position
		navigator.geolocation.getCurrentPosition((res) => {
			// Current coordinates
			let latitude = res.coords.latitude;
			let longitude = res.coords.longitude;

			//TODO: put this key in environment.ts file. How about accessing it from the server?
			// Google API key
			let key = 'AIzaSyAiXwdHbyWYjC63gTo1X3IUKTKf64oiAgI';

			// Full URL for request
			let url = 'https://maps.googleapis.com/maps/api/geocode/json?latlng=' + latitude + ',' + longitude + '&key=' + key;

			//TODO: Yurii, can you put this peace of code in AddressService?
			// Request for getting address data
			this.http.get(url).toPromise()
				.then(response => {
					let geocodeData = response.json().results;

					let streetName: string, buildNum: string, city: string, postalCode: number;

					// Get current address
					for (let i = 0; i < geocodeData[0].address_components.length; i++) {
						// Get current city
						if (geocodeData[0].address_components[i].types[0] === 'locality' &&
							geocodeData[0].address_components[i].types[1] === 'political') {
							city = geocodeData[0].address_components[i].long_name;
							console.log('City:', city);
						}
						// Get current street
						else if (geocodeData[0].address_components[i].types[0] === 'route') {
							streetName = geocodeData[0].address_components[i].long_name;
							console.log('Street:', streetName);
						}
						// Get build number
						else if (geocodeData[0].address_components[i].types[0] === 'street_number') {
							buildNum = geocodeData[0].address_components[i].long_name;
							console.log('Build Number:', buildNum);
						}
					}

					// Get postal code
					for (let k = 1; k < 3; k++) {
						for (let i = 0; i < geocodeData[k].address_components.length; i++) {
							if (geocodeData[k].address_components[i].types[0] === 'postal_code') {
								postalCode = geocodeData[k].address_components[i].long_name;
								console.log('Postal code:', postalCode);
							}
						}
					}

					// Set input fields
					for (let key in this.formData.fields) {
						// Set city
						if (this.formData.fields[key].name === 'city') {
							this.formData.fields[key].value = city;
						}
						// Set street
						else if (this.formData.fields[key].name === 'street') {
							this.formData.fields[key].value = streetName;
						}
						// Set build number
						else if (this.formData.fields[key].name === 'number') {
							this.formData.fields[key].value = buildNum;
						}
						// Set postal code
						else if (this.formData.fields[key].name === 'zipCode') {
							this.formData.fields[key].value = Number(postalCode);
						}
					}

					// Hide loading spinner
					loading.dismiss();
				})
				.catch(err => {
					//TODO: display an alert with an error msg explaining the cause of the geolocation failure
					// Hide loading spinner
					loading.dismiss();
				});
		}, err => {
			//TODO: display an alert with an error msg explaining the cause of the geolocation failure
			// Hide loading spinner
			loading.dismiss();
		});
	}

	resetForm(){
		for (let key in this.formData.fields) {
			// Clear city
			if (this.formData.fields[key].name === 'city') {
				this.formData.fields[key].value = '';
			}
			// Clear street
			else if (this.formData.fields[key].name === 'street') {
				this.formData.fields[key].value = '';
			}
			// Clear build number
			else if (this.formData.fields[key].name === 'number') {
				this.formData.fields[key].value = '';
			}
			// Clear postal code
			else if (this.formData.fields[key].name === 'zipCode') {
				this.formData.fields[key].value = null;
			}
		}
	}
    initializationMainAddress(roleId) {
		//TODO: Yurii, in case of an error, getAddress function returns data.status with the value "failure", you should deal with this case and display an alert with an error msg
		//In case of success, getAddress function returns AddressDTO object
    	this.addressService.getAddress(roleId, "personal").then((data) => {
			//TODO: Yurii, please don't access the fields with their indexes, because we may change the order of the fields later, and this will create a real problem. You can see in the identity page the method used to access to the form fields
			for (let key in data){
				//TODO: Yurii, you can remove addressName for the moment, we don't use it
                key === 'name' ? this.addressName = data[key] : null;
                key === 'number' ? this.formData.fields[1].value = data[key] : null;
				key === 'street' ? this.formData.fields[2].value = data[key] : null;
                key === 'zipCode' ? this.formData.fields[3].value = data[key] : null;
                key === 'city' ? this.formData.fields[4].value = data[key] : null;
                key === 'id' ? this.addressId = data[key] : null;
			}
		});
	}

	/*ionViewDidLoad() {
        this.fieldNumber = this.formData.fields[1].value;
        this.fieldStreet = this.formData.fields[2].value;
        this.fieldZipCode = this.formData.fields[3].value;
        this.fieldCity = this.formData.fields[4].value;
	}*/
}



