import {Injectable} from '@angular/core';
import {Headers, Http, RequestOptionsArgs} from '@angular/http';
import {Observable} from "rxjs/Rx";
import 'rxjs/add/operator/timeout';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/finally';
import {Loading, LoadingController, Toast, ToastController} from "ionic-angular";

const TIME_OUT_PERIOD = 120000;

/*
  Generated class for the HttpRequestProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class HttpRequestProvider {

    static toastCtrl: ToastController;
    static loading: LoadingController;
    static loaderComponent: Loading;
    static senderClassName: string;
    static isToastEntered: boolean = false;
    static calloutURL: string = "http://localhost:8000/search/";


    constructor(public http: Http, private toastCtrl: ToastController, private loadingCtrl: LoadingController) {
        console.log('Hello HttpRequestProvider Provider');
        HttpRequestProvider.toastCtrl = this.toastCtrl;
        HttpRequestProvider.loading = this.loadingCtrl;
    }

    /**
     * Shows error message
     * @param error
     * @returns {Promise<any>}
     */
    private handleError(error: any): Promise<any> {
        console.error('An error occurred', error); // for demo purposes only
        let vojMessage: string = HttpRequestProvider.getErrMessage(error, HttpRequestProvider.senderClassName);

        HttpRequestProvider.presentErrToast(vojMessage);
        //TODO send email to notify us
        let errorObject: any = {success: false, error: error.message || error};
        return Promise.reject(errorObject);
    }


    /**
     * Send a callOut request via http post method
     * @param payload
     * @param classObject
     * @param silentMode
     * @returns {Observable<T>}
     */
    sendCallOut(payload: any, classObject?: any, silentMode?: boolean) {
        let newTimeOutPeriod: number = 0;
        let silentLadingToast = this.toastCtrl.create();
        //
        if (silentMode !== true) {
            HttpRequestProvider.presentLoading();
            newTimeOutPeriod = TIME_OUT_PERIOD;
        } else {
            //silent mode : waiting for more time..
            newTimeOutPeriod = TIME_OUT_PERIOD * 10;
            silentLadingToast = HttpRequestProvider.presentSilentLoadingToast("En cours de chargement des donnes...", newTimeOutPeriod, 'top');
        }
        // Getting service class name
        HttpRequestProvider.senderClassName = (classObject) ? classObject.constructor.name : "";
        // Sending request
        let options: RequestOptionsArgs = HttpRequestProvider.getHttpJsonHeaders();

        return this.http.post(HttpRequestProvider.calloutURL, JSON.stringify(payload), options)
            .map(res => res.json())
            .timeout(newTimeOutPeriod)
            .catch(this.handleError)
            .finally(() => {
                try {
                    if (HttpRequestProvider.loaderComponent)
                        HttpRequestProvider.loaderComponent.dismiss();
                    if (silentLadingToast)
                        silentLadingToast.onDidDismiss(() => {
                            console.log('Dismissed toast');
                        });
                } catch (error) {
                    console.log(error);
                }

            });
    }

    /**
     * Calls a specific web service given the URL and payload
     * @param payload
     * @param url
     * @param classObject
     * @param silentMode
     * @returns {Observable<T>}
     */
    call(payload: any, url: string, classObject?: any, silentMode?: boolean) {
        let newTimeOutPeriod: number = 0;
        let silentLadingToast = this.toastCtrl.create();
        if (silentMode !== true) {
            HttpRequestProvider.presentLoading();
            newTimeOutPeriod = TIME_OUT_PERIOD;
        } else {
            //silent mode : waiting for more time..
            newTimeOutPeriod = TIME_OUT_PERIOD * 10;
            if (!HttpRequestProvider.isToastEntered) {
                // TEL present new tost if and if only the last toast is dismissed..
                silentLadingToast = HttpRequestProvider.presentSilentLoadingToast("En cours de chargement des donnes...", newTimeOutPeriod, 'top');
                HttpRequestProvider.isToastEntered = true;
            }

        }
        // Getting service class name
        HttpRequestProvider.senderClassName = (classObject) ? classObject.constructor.name : "";
        // Sending request
        let options: RequestOptionsArgs = HttpRequestProvider.getHttpJsonHeaders();

        return this.http.post(url, JSON.stringify(payload), options)
            .map(res => res.json())
            .timeout(newTimeOutPeriod)
            .catch(this.handleError)
            .finally(() => {
                if (HttpRequestProvider.loaderComponent)
                    HttpRequestProvider.loaderComponent.dismiss();
                if (silentLadingToast) {
                    silentLadingToast.dismissAll();
                }
            });
    }

    public static getHttpJsonHeaders(): RequestOptionsArgs {
        let headers = new Headers();
        let options: RequestOptionsArgs = {headers: headers};
        options.headers.append("Content-Type", 'application/json');
        //if (GlobalConfigs.env === 'PROD')
        options.headers.append("Authorization", 'Basic dm9qYWRtaW46NG5ncmFNNDFueXU=');
        return options;
    }

    public static getHttpTextHeaders() {
        let headers = new Headers();
        headers.append("Content-Type", 'text/plain');
        /*if (GlobalConfigs.env === 'PROD')
            headers.append("Authorization", 'Basic aGFkZXM6NWV0Y2Fy');*/
        return headers;
    }

    public static getHttpXmlHeaders() {
        let headers = new Headers();
        headers.append("Content-Type", 'text/xml');
        /*if (GlobalConfigs.env === 'PROD')
            headers.append("Authorization", 'Basic aGFkZXM6NWV0Y2Fy');*/
        return headers;
    }

    /**
     * Show a red toast that contains error message
     * @param message
     * @param duration
     * @param position
     */
    static presentErrToast(message: string, duration?: number, position?: string) {
        if (!duration)
            duration = 5;
        let toast = this.toastCtrl.create({
            message: message,
            position: position,
            dismissOnPageChange: true,
            showCloseButton: true,
            closeButtonText: "Ok",
            duration: duration * 1000
        });
        toast.present();
        // Change toast color to red
        let toastElement = document.getElementsByClassName('toast-wrapper toast-bottom');
        if (toastElement && toastElement.length > 0)
            (toastElement[0] as any).style.background = "#E8384F";
    }

    /**
     * Show a normal toast that contains silent loading message
     * @param message
     * @param duration
     * @param position
     */
    static presentSilentLoadingToast(message: string, duration?: number, position?: "top" | "middle" | "bottom"): Toast {
        let toast = this.toastCtrl.create({
            message: message,
            position: position,
            dismissOnPageChange: false,
            showCloseButton: true,
            closeButtonText: "Ok",
            duration: duration
        });
        toast.present();
        toast.onDidDismiss(() => {
            console.log('Dismissed toast');
            HttpRequestProvider.isToastEntered = false;
        });
        return toast;
    }

    /**
     * Present Loading component while the treatment of request
     */
    static presentLoading() {
        try {
            HttpRequestProvider.loaderComponent = HttpRequestProvider.loading.create({content: "Merci de patienter..."});
            HttpRequestProvider.loaderComponent.present();
        } catch (error) {
            console.log(error);
        }


    }

    /**
     * Get error message from status code
     * @param error
     * @param senderClassName
     * @returns {string}
     */
    static getErrMessage(error: any, senderClassName: string) {
        if (error.message === "timeout") {
            error.status = -100;
        }
        switch (~~(error.status / 100)) {
            case 0 :
                // General error with status 0: http://stackoverflow.com/a/26451773/2936049
                return "0  Problme sur serveur, merci de ressayer ultrieurement. (" + senderClassName + ")";
            case 4 :
                // 4xx: client type error
                return "4  Merci de vrifier votre connexion internet et ressayer ultrieurement. (" + senderClassName + ")";
            case 5 :
                // 5xx: server type error
                return "5  Problme sur serveur, merci de ressayer ultrieurement. (" + senderClassName + ")";
            case -1 :
                // TimeOut error specified by us. See cont variable TIME_OUT_PERIOD
                return "-1  Merci de vrifier votre connexion internet et ressayer ultrieurement. (" + senderClassName + ")";
            default :
                // Other problems...
                return " Merci de vrifier votre connexion internet et ressayer ultrieurement. (" + senderClassName + ")";
        }
    }

}






