import {Storage} from "@ionic/storage";
import {VojCardModel} from "../models/voj-card.model";
import {TextType} from "../models/text-type";
import {VojMapModel} from "../models/voj-map.model";
import {ButtonType} from "../models/button-type.model";
import {SearchDTOFactory} from "./dto-strategy/search/search-dto-factory";
import {SearchDTOStrategy} from "./dto-strategy/search/search-dto-strategy";

export class SearchResultDTO {
    id : number;
    indexation : number;
    name : string;
    restrictedName : string;
    phone : string;
    email : string;
    availability : string;
    job : string;
    lat : string;
    lng : string;
    eventMode : boolean = false;
    eventId : number = 0;
    dataSource : any;
    badges : Array<ButtonType>;
    read : boolean = false;


    constructor(row : any,
                private storage : Storage,
                private factory : SearchDTOFactory){
        let r = row.result;
        this.id = r.id;
        this.indexation = 0;
        this.name = r.name;
        this.name = r.restrictedName;
        this.phone = r.phone;
        this.email = r.email;
        this.availability = "Disponible";
        this.lat = r.latitude;
        this.lng = r.longitude;

        if(row.jobs && row.jobs.length>0) {
            this.job = row.jobs[0].label;
            this.job += row.jobs[0].level==2?'  expert':' dbutant';
        }
        this.dataSource = row;

        this.storage.get('currentUser').then((sdata:any)=>{
            let data = JSON.parse(sdata);
            if (!data || !data.account) {
                this.name = this.dataSource.result.restrictedName;
            } else {
                this.name = this.dataSource.result.name;
            }

        });
    }

    toVojCard() : VojCardModel {
        let strategy : SearchDTOStrategy = this.factory.create();
        let result : VojCardModel = new VojCardModel();
        result.id = this.id;
        result.title = new TextType();
        result.title.label = this.job;
        result.thumbnail = "https://dev.vitonjob.com/resources/employer_avatar_bg.png";
        result.cover = this.dataSource.cover?this.dataSource.cover:"https://dev.vitonjob.com/resources/726.jpg";
        result.subtitles = strategy.generateSubtitles(this.dataSource);
        result.actions = strategy.generateCardButtons();

        result.segments = strategy.generateSegments(this.dataSource, result);

        result.socialButtons = [/*{
            id : 'mail',
            icon:'mail',
            label:'',
            action:()=> {
                console.log('SMS');
            }
        },{
            id : 'call',
            icon:'call',
            label:'',
            action:()=> {
                console.log('PHONE');
            }
        },{
            id : 'fav',
            icon:'star',
            label:'',
            action:()=> {
                console.log('FAV');
            }
        }*/];

        result.address = new VojMapModel();
        result.address.title = this.name;
        result.badges = this.badges;
        if(this.lat)
            result.address.lat = parseFloat(this.lat);
        if(this.lng)
            result.address.lng = parseFloat(this.lng);

        return result;
    }

    toCardDetails() : VojCardModel {
        let strategy : SearchDTOStrategy = this.factory.create();
        let result : VojCardModel = new VojCardModel();
        result.id = this.id;
        result.title = new TextType();
        result.title.label = this.name;
        result.thumbnail = "https://dev.vitonjob.com/resources/employer_avatar_bg.png";
        result.cover = this.dataSource.cover?this.dataSource.cover:"https://dev.vitonjob.com/resources/726.jpg";
        result.subtitles = [];

        for(let i = 0 ; i < this.dataSource.jobs.length ; i++) {
            result.subtitles.push({
                icon:'briefcase',
                label:this.dataSource.jobs[i].label + (this.dataSource.jobs[i].level == 1?" dbutant":" expriment")
            });
        let otherSubs = strategy.generateSubtitles(this.dataSource);
        let rating : TextType = null;

        //  Remove the restricted adress
        if(otherSubs[0].id == 'restricted_adress') {
            rating = otherSubs[1];
            otherSubs.splice(0, 2);
        } else {
            rating = otherSubs[0];
            otherSubs.splice(0, 1);
        }

        result.title.label = result.title.label + ' <span class="small-rating">'+rating.label+'</span>';

        for(let i = 0 ; i < otherSubs.length ; i++) {
            result.subtitles.push(otherSubs[i]);
        }

        if(this.dataSource.address) {
            result.subtitles.push({
               icon:'home',
               label:this.dataSource.address
            });
        }

        }
        for(let i = 0 ; i < this.dataSource.languages.length ; i++) {
            result.subtitles.push({
                icon:'globe',
                label:this.dataSource.languages[i].label + (this.dataSource.languages[i].level == 1?" dbutant":" courant")
            });
        }
        for(let i = 0 ; i < this.dataSource.qualities.length ; i++) {
            result.subtitles.push({
                icon:'flower',
                label:this.dataSource.qualities[i].label
            });
        }


        result.actions = result.actions = this.factory.create().generateDetailedCardButtons();

        result.socialButtons = [{
            id : 'mail',
            icon:'mail',
            label:'',
            action:()=> {
                console.log('SMS');
            }
        },{
            id : 'call',
            icon:'call',
            label:'',
            action:()=> {
                console.log('PHONE');
            }
        },{
            id : 'fav',
            icon:'star',
            label:'',
            action:()=> {
                console.log('FAV');
            }
        }];

        result.address = new VojMapModel();
        result.address.title = this.name;
        result.badges = this.badges;
        if(this.lat)
            result.address.lat = parseFloat(this.lat);
        if(this.lng)
            result.address.lng = parseFloat(this.lng);

        return result;
    }
}


